import { basePath } from '../utils';
import React from 'react';


export default class Kierki extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Kierki',
            desc: 'gra dla 4 [3] osb',
            url: basePath() + '/kierki',
            players: [3, 4],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        <p>
                            Grupa gier, przede wszystkim dla czterech osb, w porwnaniu z wieloma innymi grami karcianymi, niejako "odwrotnych" (wygrywa nie ten z grajcych, ktry zdobywa maksimum punktw lub lew pozytywnych, lecz ten, ktremu uda si do minimum unikn zapisania na swoje konto punktw i lew negatywnych). Gry tej grupy s na obszarze jzyka francuskiego oglnie nazywane grami "misre", na obszarze jzyka angielskiego - grami "nullo".<br />
                            Kierki, uprawiane dzisiaj w rnych formach i wariantach w wielu krajach, zwane take gr w krla lub (z angielskiego) w kinga, wywodz si z dawnej, szesnastowiecznej francuskiej gry "reversis" i angielskiej "reverse", a take z gier hiszpaskich "quinola", "espagnolette" i "cirulo", ktrych warianty znane byy w niewiele lat pniej i uprawiane w Polsce jako "chrapanka" i "drubart".<br />
                            Zapalonym graczem w te gry by Jan III Sobieski.
                        </p>
                        <p>
                            Oprcz podstawowych zasad Kierek, ktre s opisane jako pierwsze, istnieje kilka wariantw tej gry:
                        </p>
                        <ul>
                            <li><a href="#dzokerowe" className="pure-menu-link underline">Kierki dokerowe</a></li>
                            <li><a href="#dominowe" className="pure-menu-link underline">Kierki dominowe</a></li>
                            <li><a href="#licytowane" className="pure-menu-link underline">Kierki licytowane</a></li>
                            <li><a href="#omnibus" className="pure-menu-link underline">Omnibus</a></li>
                            <li><a href="#slobberhannes" className="pure-menu-link underline">Slobberhannes</a></li>
                            <li><a href="#polignac" className="pure-menu-link underline">Polignac</a></li>
                            <li><a href="#czarnamanka" className="pure-menu-link underline">Czarna Maka</a></li>
                            <li><a href="#metamorfoza" className="pure-menu-link underline">Metamorfoza</a></li>
                        </ul>
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 4 [3] osb.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia kart (52 karty). Starszestwo kart od asa do dwjki. Warto punktowa kart rna w rnych rozgrywkach.<br />
                        Nie ma koloru atutowego.<br />
                        Istnieje obowizek dorzucania do koloru, lecz nie ma obowizku przebijania starsz kart w kolorze wyjcia.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Kady z grajcych otrzymuje po 13 kart. Pierwszy wychodzi zawsze gracz po lewej od rozdajcego. Rozgrywane s kolejne lewy. Gracz, ktry wzi lew wychodzi do nastpnej.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pena partia kierek skada si z szeciu kolejnych rozda. W kadym z nich inne karty s minusowe, ujemne. Gracz, ktry w danym rozdaniu wemie jedn lub wicej kart minusowych, zapisuje sobie odpowiedni ilo punktw minusowych. I tak:
                    </p>
                    <table className="pure-table pure-table-horizontal">
                        <tbody>
                            <tr className="pure-table-odd">
                                <td>pierwsze rozdanie</td>
                                <td>bez lew</td>
                                <td>za kad wzit lew 20 punktw</td>
                            </tr>
                            <tr>
                                <td>drugie rozdanie</td>
                                <td>bez kierw</td>
                                <td>za kad wzit kart kierow 20 punktw</td>
                            </tr>
                            <tr className="pure-table-odd">
                                <td>trzecie rozdanie</td>
                                <td>bez dam</td>
                                <td>za kad wzit dam 60 punktw</td>
                            </tr>
                            <tr>
                                <td>czwarte rozdanie</td>
                                <td>bez panw</td>
                                <td>za kadego wzitego krla lub waleta 30 punktw</td>
                            </tr>
                            <tr className="pure-table-odd">
                                <td>pite rozdanie</td>
                                <td>bez krla kier</td>
                                <td>za jego wzicie 150 punktw</td>
                            </tr>
                            <tr>
                                <td>szste rozdanie</td>
                                <td>bez ostatniej lewy</td>
                                <td>za jej wzicie 150 punktw</td>
                            </tr>
                        </tbody>
                    </table>

                    <p>
                        W drugim rozdaniu nie wolno, jeli ma si inne karty na rku, wychodzi w kiery.<br />
                        W pitym rozdaniu nie wolno, gdy ma si inne karty na rku, wychodzi w kiery; posiadacz krla kierowego - zalenie od umowy - nie ma lub ma obowizek zrzuci go przy pierwszej okazji. Szste rozdanie bywa, zalenie od umowy, rozgrywane take w dwu innych wariantach:
                    </p>
                    <ul>
                        <li>bez dwch ostatnich lew; po 75 punktw za kad z nich</li>
                        <li>bez sidmej i ostatniej lewy; te po 75 punktw za kad z nich.</li>
                        <li>bez trzeciej i sidmej; te po 75 punktw za kad z nich.</li>
                        <li>bez trzeciej i smej; te po 75 punktw za kad z nich.</li>
                    </ul>
                    <p>
                        Niekiedy, zalenie od umowy, rozgrywane bywa take sidme rozdanie, zwane gr z "rozbjnikiem", "wariatem" lub "lokomotyw". W tym rozdaniu wszystkie karty minusowe z poszczeglnych rozda s minusowe naraz (w jednej lewie mog znale si punkty minusowe za wzicie lewy, czterech kierw, damy, krla i waleta, krla kier i ostatniej lewy).<br />
                        Nie wolno w tym rozdaniu wychodzi w kiery.
                    </p>
                    <p>
                        Przy grze w kierki w trzy osoby usuwa si z talii dwjk trefl i kady z grajcych otrzymuje po siedemnacie kart. Zasady rozgrywki nie ulegaj zmianie, nieznacznie zmienia si zapis punktowy:
                    </p>
                    <ul>
                        <li>za lew - 15pkt</li>
                        <li>za kiery - 20pkt</li>
                        <li>za dam - 60pkt</li>
                        <li>za panw - 30pkt</li>
                        <li>za krla kierowego - 140pkt</li>
                        <li>za ostatni lew - 140pkt (lub za dwie ostatnie po 70pkt)</li>
                    </ul>
                    <p>
                        Istniej dwa zasadnicze warianty zdobywania punktw plusowych: rozgrywka atutowa oraz tzw. loteryjka (loteryjka jest te samodzieln gr karcian - jej zasady opisane s pod nazw: fantan).
                    </p>
                    <p>
                        Wygrywajcy loteryjk gracz otrzymuje 800 punktw plusowych; gracz, ktry pozbywa si swoich kart z rki jako drugi, otrzymuje 500 punktw plusowych. Mona rozgrywa loteryjk nie raz, lecz czterokrotnie - wwczas w kadej rozgrywce pierwsza nagroda wynosi 200pkt plusowych, druga za 125pkt.<br />
                        Przy grze w trzy osoby i jednorazowym rozgrywaniu loteryjki nagrody wynosz: pierwsza 790pkt, druga 485pkt. Przy grze w trzy osoby i trzykrotnym rozgrywaniu loteryjki nagrody wynosz: pierwsza 250pkt, druga 175pkt w kadej z loteryjek.
                    </p>
                    <p>
                        Drugi zasadniczy wariant zdobywania punktw plusowych to rozgrywka atutowa, rozgrywana czterokrotnie (przy czterech grajcych) lub trzykrotnie (przy trzech).<br />
                        W rozgrywce atutowej po rozdaniu kart gracz na lewo od rozdajcego (rozgrywajcy) pierwszy wychodzi z karty, lecz przed tym deklaruje swj kolor atutowy, ktry bdzie obowizywa w danej rozgrywce.<br />
                        Istnieje obowizek dorzucania do koloru.<br />
                        Nie ma obowizku przebijania atutem w przypadku nieposiadania koloru.<br />
                        Za kad wzit lew ten z graczy, ktry j wzi, otrzymuje 25 punktw plusowych. Po zakoczeniu rozgrywki w nastpnym rozdaniu kolor atutowy wybiera nastpny gracz. Cykl rozgrywek koczy si, gdy kady z graczy wybiera atu i by pierwszym wychodzcym.
                    </p>

                    <h2 className="content-subhead">Wariant rozgrywki atutowej</h2>
                    <p>
                        Kady z grajcych otrzymuje po 12 kart, pozostae cztery le odkryte na stole (przy trzech grajcych kady otrzymuje po 16 kart, pozostae trzy le odkryte na stole). Karty na stole s wasnoci rozgrywajcego; moe on wczy je do swoich kart w rku, kademu z pozostaych graczy oddajc jedn swoj dowolnie wybran kart zakryt i deklarujc kolor atutowy. Gracz rozgrywajcy moe take odstpi karty na stole i prawo nominowania koloru atutowego w danym rozdaniu ktremu z przeciwnikw w zamian za zadeklarowan przez niego ilo punktw plusowych.
                    </p>
                    <p>
                        Deklaracje te odbywaj si w trybie licytacji - kady z graczy, poza rozgrywajcy, ma prawo zgosi t ilo punktw, ktr ofiarowuje za karty na stole i prawo nominowania koloru atutowego. Licytacj wygrywa ten z graczy, ktry zgosi najwysz ilo punktw. Wwczas wcza on karty ze stou do swoich kart w rku, kademu przeciwnikowi oddajc jedn swoj dowolnie wybran kart i deklarujc kolor atutowy.
                    </p>
                    <p>
                        Rozliczenie z rozgrywajcym nastpuje natychmiast po ukoczeniu rozdania.<br />
                        Jeli nominujcy kolor atutowy zdoby ilo lew punktowo wiksz ni ilo punktw, ktre ofiarowa za prawo nominowania koloru atutowego (na przykad karty zostay odstpione za 125pkt, a gracz wygrywajcy licytacj zdoby sze lew, tj. 150pkt), w zapisie otrzymuje odpowiednio zmniejszon ilo punktw i odpowiedni ilo punktw otrzymuje gracz, ktry odda karty ze stou do licytacji (w naszym przykadzie: dla gracza wygrywajcego licytacj - 25pkt; dla oddajcego karty do licytacji - 125pkt za odstpienie kart oraz dodatkowo ta ilo punktw za lewy, ktre sam zdoby w rozgrywce). Jeli ilo punktw za lewy zdobyte nie wystarcza do pokrycia zobowizania, zostaj one odpisane z oglnego zapisu gracza (i dopisane graczowi, ktry odda karty ze stou do licytacji).
                    </p>
                    <p>
                        Mona wyobrazi sobie, i w kolejnych czterech rozgrywkach atutowych licytowanych nominowa kolor bdzie ten sam gracz.<br />
                        Gracz rozgrywajcy ma prawo nie otwiera licytacji. Gdy j jednak otworzy, ma obowizek odstpi karty na stole i prawo nominowania koloru atutowego dajcemu najwicej.
                    </p>
                    <p>
                        Gracz rozgrywajcy moe otworzy licytacj warunkowo, deklarujc minimaln liczb punktw, jak chce otrzyma za odstpienie kart ze stou i nominowanie koloru atutowego (np. 150 punktw). Gdy nie ma chtnych, moe obniy wymagan przez siebie liczb punktw lub samemu nominowa kolor atutowy.
                    </p>
                    <p>
                        Gdy gra si w kierki z "rozbjnikiem", konieczne s dwie tury rozgrywek plusowych. Wwczas tradycyjnie jedn z nich jest loteryjka, drug za rozgrywka atutowa.
                    </p>
                </div>

                <div className="content">
                    <a name="dzokerowe"></a>
                    <h1 className="content-subhead main-header">Kierki dokerowe</h1>
                    <div className="info">Amerykaski wariant gry.</div>
                    <p>
                        Z talii usuwa si dwjk trefl, umieszczajc zamiast niej dokera (przy grze w trzy osoby usuwa si dwjk i trjk trefl).
                    </p>
                    <p>
                        Kartami minusowymi s tylko kiery oraz doker. Za wzicie asa kierowego - 14pkt, krla - 13pkt, damy - 12pkt, waleta - 11pkt, dziesitki - 10pkt i dalej iloci punktw rwne iloci oczek na karcie. Za wzicie dokera - 20 punktw minusowych.
                    </p>
                    <p>
                        Doker jest w rozgrywce kart o specjalnych waciwociach: uwaany jest za kart kierow modsz od waleta, lecz starsz od dziesitki (bije wic kad lew, w ktrej nie ma figury). W lewie niekierowej natomiast doker, niezalenie od koloru wyjcia, jest zawsze kart najstarsz i bije t lew (nie mona wic zrzuci dokera na aden inny kolor prcz kierw).
                    </p>
                    <p>
                        Kade rozdanie jest samodzieln rozgrywk. Pena partia kierek dokerowych obejmuje zazwyczaj tyle rozda, ilu jest graczy.
                    </p>
                </div>

                <div className="content">
                    <a name="dominowe"></a>
                    <h1 className="content-subhead main-header">Kierki dominowe</h1>
                    <div className="info">Amerykaski wariant gry.</div>
                    <p>
                        Kartami minusowymi s tylko kiery (kada karta kierowa - jeden punkt minusowy). Gra toczy si w kolejnych rozdaniach, dopki w zapisie ktry z graczy nie osignie 31 punktw minusowych. Wwczas ca gr wygrywa ten z graczy, ktry ma najmniej punktw minusowych.
                    </p>
                    <p>
                        Kady z grajcych otrzymuje po sze kart, reszta kart pozostaje zakryta w talonie. W toku rozgrywki wolno wychodzi w kiery.
                    </p>
                    <p>
                        Istnieje obowizek dorzucania do koloru, dopki s karty w talonie. Jeli gracz nie moe dorzuci do koloru kolejnej lewy, cignie jedn lub wicej kart z talonu a do chwili, gdy bdzie mg dorzuci do koloru.
                    </p>
                    <p>
                        Gdy wyczerpi si karty w talonie, wolno w przypadku braku koloru wyjcia zrzuci dowoln kart.
                    </p>
                    <p>
                        Jeli w toku rozgrywki ktry z graczy pozbdzie si wszystkich swoich kart z rki, wypada z rozgrywki, ktra toczy si dalej z udziaem pozostaych graczy do chwili, gdy zostan zgrane wszystkie karty lub gdy z kartami w rku pozostanie tylko jeden z graczy (w tym ostatnim przypadku karty pozostae w rku docza on do swoich lew).
                    </p>
                    <p>
                        Jeli w toku rozgrywki ktry z graczy pozbywa si swojej ostatniej karty z rki, biorc na ni lew, gracz ten wypada z dalszej rozgrywki, obowizek za wyjcia do nastpnej lewy przechodzi na gracza nastpnego (od jego lewej strony).
                    </p>
                </div>

                <div className="content">
                    <a name="licytowane"></a>
                    <h1 className="content-subhead main-header">Kierki licytowane</h1>
                    <div className="info">Amerykaski wariant gry.</div>
                    <p>
                        Kady z grajcych otrzymuje po trzynacie (przy trzech grajcych po siedemnacie) kart. Kartami minusowymi s tylko karty jednego, wybranego w licytacji koloru (za jedn tak kart wzit w lewie - jeden punkt minusowy).
                    </p>
                    <p>
                        Po rozdaniu kart gracz od lewej strony rozdajcego rozpoczyna licytacj, pasujc lub deklarujc pewn ilo punktw minusowych, ktr zapisze sobie za prawo nominowania koloru minusowego w rozgrywce. Gracz, ktry w licytacji zadeklaruje najwysz ilo punktw, wygrywa licytacj - zapisuje sobie zadeklarowan ilo punktw minusowych i wyznacza kolor minusowy danej rozgrywki; nie bra kier, nie bra pik, nie bra kar lub nie bra trefli.
                    </p>
                    <p>
                        Gra toczy si w kolejnych rozdaniach, dopki w zapisie ktry z graczy nie osignie 77 punktw minusowych. Wwczas ca gr wygrywa ten z graczy, ktry ma najmniej punktw minusowych.
                    </p>
                </div>

                <div className="content">
                    <a name="omnibus"></a>
                    <h1 className="content-subhead main-header">Omnibus</h1>
                    <div className="info">Amerykaski wariant gry.</div>
                    <p>
                        Kartami minusowymi s wszystkie kiery (za wzicie karty kierowej - jeden punkt minusowy) oraz dama pikowa (za jej wzicie - trzynacie punktw minusowych). Kart plusow jest dziesitka karo - za jej wzicie liczy si graczowi 10 punktw plusowych.
                    </p>
                    <p>
                        W rozgrywce wolno wychodzi zarwno w kiery, jak w piki.
                    </p>
                    <p>
                        Jeli ktry z graczy wemie wszystkie kiery, dam pik i dziesitk karo (wszystkie karty punktowane), otrzymuje 26 punktw plusowych.
                    </p>
                    <p>
                        Przed rozpoczciem rozgrywki kady z graczy wybiera trzy dowolne karty ze swojej rki i zakryte przekazuje je graczowi po lewej stronie, otrzymujc w zamian trzy karty od gracza po prawej stronie.
                    </p>
                    <p>
                        Do pierwszej lewy wychodzi zawsze gracz po lewej stronie rozdajcego.
                    </p>
                    <p>
                        Gra toczy si w kolejnych rozdaniach, dopki w zapisie ktry z graczy nie osignie 100 punktw minusowych. Wwczas ca gr wygrywa ten z graczy, ktry ma najmniej punktw minusowych lub najwicej plusowych.
                    </p>
                    <p>
                        Przy trzech grajcych - kady otrzymuje po 17 kart; usuwa si z talii 2 <span className="club"></span>.
                    </p>
                    <p>
                        Przy czterech grajcych - kady otrzymuje po 13 kart.
                    </p>
                </div>

                <div className="content">
                    <a name="slobberhannes"></a>
                    <h1 className="content-subhead main-header">Slobberhannes</h1>
                    <div className="info">Holenderski wariant gry dla trzech osb.</div>
                    <p>
                        Do gry uywa si talii pikietowej (32 karty).<br />
                        Starszestwo kart od asa do sidemki.<br />
                        Nie ma koloru atutowego.<br />
                        Istnieje obowizek dorzucania do koloru.<br />
                    </p>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie.<br />
                        Wychodzi do pierwszej lewy zawsze gracz po lewej stronie rozdajcego.<br />
                        Kady z grajcych otrzymuje po osiem kart.
                    </p>
                    <p>
                        Za wzicie pierwszej lewy - jeden punkt minusowy.<br />
                        Za wzicie ostatniej lewy - jeden punkt minusowy.<br />
                        Za wzicie damy treflowej - dwa punkty minusowe.
                    </p>
                    <p>
                        W rozgrywce mona wychodzi w trefle.
                    </p>
                    <p>
                        Gra toczy si w kolejnych rozdaniach, dopki w zapisie ktry z graczy nie osignie dziesiciu punktw minusowych. Wwczas ca gr wygrywa ten z graczy, ktry ma najmniej punktw minusowych.
                    </p>
                </div>

                <div className="content">
                    <a name="polignac"></a>
                    <h1 className="content-subhead main-header">Polignac</h1>
                    <div className="info">
                        Francuski wariant gry, pochodzcy z XIX wieku.<br />
                        Nazwa pochodzi od nazwiska ministra w rzdzie Karola X.<br />
                        Dzisiaj wariant ten uprawiany jest gwnie w krajach anglosaskich.
                    </div>
                    <p>
                        Kady z trzech grajcych otrzymuje po siedemnacie kart. Ostatnia karta zostaje odsonita i ley na stole. Wyznacza ona kolor atutowy w danej rozgrywce; sama udziau w rozgrywce nie bierze.
                    </p>
                    <p>
                        Do pierwszej lewy wychodzi zawsze gracz po lewej stronie rozdajcego.<br />
                        Za wzicie kadej lewy gracz otrzymuje jeden punkt plusowy.<br />
                        Wolno wychodzi we wszystkie kolory. W wypadku nieposiadania koloru wyjcia nie ma obowizku przebijania atutem.
                    </p>
                    <p>
                        Za wzicie waleta kierowego - 4 punkty minusowe;<br />
                        za wzicie waleta karowego - 3 punkty minusowe;<br />
                        za wzicie waleta treflowego - 2 punkty minusowe;<br />
                        za wzicie waleta pikowego - 1 punkt minusowy.
                    </p>
                    <p>
                        Gra toczy si do chwili, gdy ktry z graczy osignie w zapisie 20 punktw plusowych, wygrywajc gr.
                    </p>
                </div>

                <div className="content">
                    <a name="czarnamanka"></a>
                    <h1 className="content-subhead main-header">Czarna Maka</h1>
                    <div className="info">Tradycyjny angielski wariant gry.</div>
                    <p>
                        Pena talia kart, z ktrej usunito dwjk trefl - a wic 51 kart.<br />
                        Starszestwo kart od asa do dwjki.<br />
                        Nie ma koloru atutowego.
                    </p>
                    <p>
                        Kady z grajcych otrzymuje po siedemnacie kart. Po uporzdkowaniu kart kady z graczy przygotowuje sobie trzy karty, ktrych chce si pozby i przekazuje je swojemu partnerowi po prawej rce, jednoczenie samemu otrzymujc trzy inne karty od partnera po lewej rce. Po dokonaniu wymiany kady z graczy ma znw 17 kart. Nastpuje pierwsze wyjcie i rozgrywka.
                    </p>
                    <p>
                        Wychodzi zawsze gracz po lewej rce rozdajcego.<br />
                        Obowizuje dorzucanie do koloru.<br />
                        Nie ma obowizku przebijania starsz kart.<br />
                        Wolno wychodzi w kady kolor.<br />
                    </p>
                    <p>
                        Kartami karnymi s:
                    </p>
                    <ul>
                        <li>wszystkie kiery - 1 punkt karny za kady wzity kier; wolno wychodzi w kiery;</li>
                        <li>dama pikowa - 13 punktw karnych (std nazwa gry); wolno wychodzi w piki;</li>
                        <li>krl pikowy - 10 punktw karnych;</li>
                        <li>as pikowy - siedem punktw karnych;</li>
                    </ul>
                    <p>
                        Gra toczy si w szeregu rozda do umwionej z gry iloci punktw karnych (zazwyczaj 100 lub 250, rzadziej 500). Ten z graczy, ktry ma najmniej punktw karnych, w chwili gdy jeden z grajcych osign umwion granic punktw, wygrywa gr.
                    </p>

                    <h2 className="content-subhead">Partia przykadowa</h2>
                    <p>
                        Rozdawa gracz C.<br />
                        Ukad kart u graczy jest nastpujcy:
                    </p>
                    <p>
                        Gracz A<br />
                        W <span className="spade"></span>, 6 <span className="spade"></span>, 3 <span className="spade"></span>;<br />
                        A <span className="heart"></span>, K <span className="heart"></span>, 10 <span className="heart"></span>, 7 <span className="heart"></span>, 5 <span className="heart"></span>;<br />
                        D <span className="diamond"></span>, 9 <span className="diamond"></span>, 8 <span className="diamond"></span>, 2 <span className="diamond"></span>;<br />
                        K <span className="club"></span>, 10 <span className="club"></span>, 6 <span className="club"></span>, 5 <span className="club"></span>, 3 <span className="club"></span>;
                    </p>
                    <p>
                        Gracz B<br />
                        A <span className="spade"></span>, 9 <span className="spade"></span>, 7 <span className="spade"></span>, 5 <span className="spade"></span>, 2 <span className="spade"></span>;<br />
                        W <span className="heart"></span>, 6 <span className="heart"></span>, 3 <span className="heart"></span>, 2 <span className="heart"></span>;<br />
                        A <span className="diamond"></span>, K <span className="diamond"></span>, 7 <span className="diamond"></span>, 4 <span className="diamond"></span>, 3 <span className="diamond"></span>;<br />
                        D <span className="club"></span>, 8 <span className="club"></span>, 4 <span className="club"></span>;
                    </p>
                    <p>
                        Gracz C<br />
                        K <span className="spade"></span>, D <span className="spade"></span>, 10 <span className="spade"></span>, 8 <span className="spade"></span>, 4 <span className="spade"></span>;<br />
                        D <span className="heart"></span>, 9 <span className="heart"></span>, 8 <span className="heart"></span>, 4 <span className="heart"></span>;<br />
                        W <span className="diamond"></span>, 10 <span className="diamond"></span>, 6 <span className="diamond"></span>, 5 <span className="diamond"></span>;<br />
                        A <span className="club"></span>, W <span className="club"></span>, 9 <span className="club"></span>, 7 <span className="club"></span>;
                    </p>
                    <p>
                        Gracz A ma kart raczej dobr, postanawia w wymianie jeszcze j sobie, o ile mona, polepszy, pozbywajc si do gronych wysokich kart kierowych. Przygotowuje wic do wymiany dla gracza C: A <span className="heart"></span>, K <span className="heart"></span>, 10 <span className="heart"></span>.<br />
                        Karty gracza B te nie wygldaj le - A <span className="spade"></span>jest dobrze chroniony wieloma maymi kartami w tym kolorze. Bardziej opacalne wic, ni pozbywanie si A <span className="spade"></span>, jest pozby si cakowicie trefli, stworzy renons w tym kolorze; to okaza si moe bardzo przydatne w rozgrywce. Gracz B szykuje wic do wymiany dla gracza A: D <span className="club"></span>, 8 <span className="club"></span>, 4 <span className="club"></span>.<br />
                        Gracz C ma ,cho o tym oczywicie nie wie, najgorsze perspektywy. W wymianie pozbdzie si dwch karnych kart pikowych, ale co z tego? Po refleksji przygotowuje do wymiany dla gracza B: K <span className="spade"></span>, D <span className="spade"></span>, D <span className="heart"></span>.
                    </p>
                    <p>
                        Nastpuje teraz wymiana kart (jednoczesna, midzy trzema graczami).<br />
                        Ostateczny ukad kart przed rozgrywk jest nastpujcy:
                    </p>
                    <p>
                        Gracz A<br />
                        W <span className="spade"></span>, 6 <span className="spade"></span>, 3 <span className="spade"></span>;<br />
                        7 <span className="heart"></span>, 5 <span className="heart"></span>;<br />
                        D <span className="diamond"></span>, 9 <span className="diamond"></span>, 8 <span className="diamond"></span>, 2 <span className="diamond"></span>;<br />
                        K <span className="club"></span>, D <span className="club"></span>, 10 <span className="club"></span>, 8 <span className="club"></span>, 6 <span className="club"></span>, 5 <span className="club"></span>, 4 <span className="club"></span>, 3 <span className="club"></span>;
                    </p>
                    <p>
                        Gracz B<br />
                        A <span className="spade"></span>, K <span className="spade"></span>, D <span className="spade"></span>, 9 <span className="spade"></span>, 7 <span className="spade"></span>, 5 <span className="spade"></span>, 2 <span className="spade"></span>;<br />
                        D <span className="heart"></span>, W <span className="heart"></span>, 6 <span className="heart"></span>, 3 <span className="heart"></span>, 2 <span className="heart"></span>;<br />
                        A <span className="diamond"></span>, K <span className="diamond"></span>, 7 <span className="diamond"></span>, 4 <span className="diamond"></span>, 3 <span className="diamond"></span>;<br />
                        <span className="club"></span> ---------
                    </p>
                    <p>
                        Gracz C<br />
                        10 <span className="spade"></span>, 8 <span className="spade"></span>, 4 <span className="spade"></span>;<br />
                        A <span className="heart"></span>, K <span className="heart"></span>, 10 <span className="heart"></span>, 9 <span className="heart"></span>, 8 <span className="heart"></span>, 4 <span className="heart"></span>;<br />
                        W <span className="diamond"></span>, 10 <span className="diamond"></span>, 6 <span className="diamond"></span>, 5 <span className="diamond"></span>;<br />
                        A <span className="club"></span>, W <span className="club"></span>, 9 <span className="club"></span>, 7 <span className="club"></span>;
                    </p>
                    <p>
                        Wychodzi A w W <span className="spade"></span>, bo to najbezpieczniej; spadaj 7 <span className="spade"></span> i 10 <span className="spade"></span>. Znw wychodzi A w 6 <span className="spade"></span>, spadaj 5 <span className="spade"></span> i 4 <span className="spade"></span> (ani B, ani C nie chc by wychodzcymi). A po raz trzeci wychodzi w piki - tym razem w 3 <span className="spade"></span>. Spada 2 <span className="spade"></span> i C bierze lew na 8 <span className="spade"></span>. B jest w szczeglnie zej sytuacji, zostay mu same wysokie, wyrobione ju piki.<br />
                        C wychodzi w W <span className="diamond"></span>, spadaj D <span className="diamond"></span> i K <span className="diamond"></span>. B wychodzi w A <span className="diamond"></span>, spadaj 10 <span className="diamond"></span> i 9 <span className="diamond"></span>. B raz jeszcze wychodzi w karo, pozbywajc si niebezpiecznego dla niego koloru - na jego 7 <span className="diamond"></span> spada 6 <span className="diamond"></span> i A bierze lew na 8 <span className="diamond"></span>. Sytuacja A jest te niezbyt dobra, ma za duo wysokich kart treflowych. A wychodzi (uwaga! bardzo sprytnie) w 5 <span className="heart"></span>. B bije D <span className="heart"></span>, C kadzie 10 <span className="heart"></span> - B ma wic ju za t lew trzy punkty karne. B wychodzi oczywicie w 4 <span className="diamond"></span>, C bije 5 <span className="diamond"></span>, A kadzie 2 <span className="diamond"></span>. C prbuje si ratowa wychodzc w 4 <span className="heart"></span>, A bije 7 <span className="heart"></span>, B kadzie 6 <span className="heart"></span> - i trzy punkty karne dla A. Teraz ujawnia si celowo uprzedniego wyjcia A w kiery - spokojnie wychodzi on w 3 <span className="club"></span> i jest ju zupenie bezpieczny, do koca rozgrywki nie wemie ani jednej lewy. Na 3 <span className="club"></span> B zrzuca W <span className="heart"></span> i take jest ju cakiem bezpieczny; nieszczsny C musi przebi 3 <span className="club"></span> i jakkolwiek by gra, wemie wszystkie pozostae lewy w rozgrywce, w nich siedem pozostaych kierw i wszystkie karne, wysoko punktowane karty pikowe - razem trzydzieci jeden punktw kanych.
                    </p>
                    <p>
                        Rozgrywka wydaje si prosta. Sprawdmy jednak, co by si dziao, gdyby w sidmej lewie A nie wyszed sprytnie w 5 <span className="heart"></span>, lecz inaczej, na przykad od razu w 3 <span className="club"></span>.<br />
                        Wwczas C bierze lew na A <span className="club"></span>, B za dorzuca D <span className="heart"></span>. C wychodzi w 5 <span className="diamond"></span>, na ktr spadaj 4 <span className="diamond"></span> i 2 <span className="diamond"></span>. C wychodzi wic w W <span className="club"></span>, spada 10 <span className="club"></span> i W <span className="heart"></span> od B. Teraz dalej w trefle: C wychodzi w 9 <span className="club"></span>, spada 8 <span className="club"></span> i D <span className="spade"></span> (trzynacie punktw kanych, klska!) od B. C wychodzi w 8 <span className="heart"></span>, spadaj na ni 7 <span className="heart"></span> i 3 <span className="heart"></span>, wreszcie C wychodzi w 4 <span className="heart"></span> i A bierze t lew (musi!) na 5 <span className="heart"></span>, od B za spada 2 <span className="heart"></span>. Teraz ostatnie cztery lewy bierze A na swoje ju wyrobione trefle.
                    </p>
                    <p>
                        W tym wariancie B koczy rozgrywk bez punktw karnych, C ma ich dziewitnacie, A za dwadziecia cztery punkty karne. Jak wida zupenie inny wynik.
                    </p>

                    <h2 className="content-subhead">Warianty Czarnej Maki</h2>
                    <p>
                        Mona w ni gra te w 4, 5 lub 6 graczy.
                    </p>
                    <p>
                        Przy czterech graczach gra si pen tali, kady za otrzymuje po 13 kart i gra mona albo kady na wasny rachunek, albo systemem bridowym - dwu przeciwko dwom.
                    </p>
                    <p>
                        Przy piciu graczach usuwa si dwie dwjki - treflow i karow.<br />
                        Kady z graczy otrzymuje po 10 kart.
                    </p>
                    <p>
                        Przy szeciu graczach usuwa si wszystkie dwjki i kady z graczy otrzymuje po 8 kart.
                    </p>
                    <p>
                        We wszystkich wariantach gry zawsze wymienia si trzy karty, co zwiksza w grze, gdy jest wicej graczy, element szczcia i przypadku.
                    </p>
                </div>

                <div className="content">
                    <a name="metamorfoza"></a>
                    <h1 className="content-subhead main-header">Metamorfoza</h1>
                    <div className="info">
                        Wariant gry opracowany w 1962r. przez Roberta Abbotta z Nowego Jorku.
                    </div>
                    <p>
                        Do gry uywa si penej talii kart (52 karty) oraz dla kadego z graczy dodatkowo as, dwjka, trjka i czwrka z innej talii. Te osiem (lub - przy trzech grajcych - dwanacie) kart peni specjaln rol, nie bierze udziau w grze.
                    </p>
                    <p>
                        Pena partia metamorfozy skada si z czterech kolejnych rozgrywek o rnych zasadach rozgrywania i o rnych zasadach zapisu. I tak:
                    </p>
                    <ul>
                        <li>pierwsza rozgrywka: bez atu; za kad wzit kart treflow - 4pkt minusowe;</li>
                        <li>druga rozgrywka: atu karo; za kad wzit kart kierow - 3pkt minusowe;</li>
                        <li>trzecia rozgrywka: bez atu; za kad wzit kart - 1pkt minusowy (a wic za lew 2 lub 3 punkty minusowe, zalenie od liczby grajcych);</li>
                        <li>czwarta rozgrywka: bez atu; za kad wzit kart pikow - 4pkt plusowe.</li>
                    </ul>
                    <p>
                        Kady z grajcych otrzymuje po 12 kart; reszta pozostaje zakryta w talonie.
                    </p>
                    <p>
                        Po pierwszym rozdaniu kady z graczy decyduje, w ktrym z kolejnych rozda partii jego zdaniem wygrywa. Po podjciu decyzji kadzie przed sob jedn z czterech kart (asa, dwjk, trjk lub czwrk) zakryt dla zaznaczenia, i deklaruje wygranie przez siebie pierwszej, drugiej, trzeciej lub czwartej rozgrywki.
                    </p>
                    <p>
                        Gdy gracze podjli decyzj, odsania si ich karty wskanikowe. Pozostaj one odsonite do koca gry.
                    </p>
                    <p>
                        Gracz, ktry rzeczywicie wygrywa rozgrywk zadeklarowan wstpnie (osiga w niej najwiksz ilo punktw plusowych lub najmniejsz ilo punktw minusowych), otrzymuje w postaci premii dodatkowo 10 punktw plusowych. Jeli w zadeklarowanej rozgrywce gracz zremisuje lub j przegra, nie pociga to za sob adnych skutkw.
                    </p>
                    <p>
                        W pierwszej rozgrywce wychodzi do pierwszej lewy gracz po lewej stronie rozdajcego.
                    </p>
                    <p>
                        Po zakoczeniu rozgrywki karty nie s tasowane, lecz kady z graczy uywa kart zawartych w lewach, ktre wzi, do gry w drugiej rozgrywce.
                    </p>
                    <p>
                        Jeli po zakoczeniu pierwszej rozgrywki, a przed rozpoczciem drugiej ktry z graczy ma w lewach (w rku do drugiej rozgrywki) mniej ni dwanacie kart, brakujc mu ilo kart uzupenia z zakrytego talonu.<br />
                        Jeli gracz ma wicej ni dwanacie kart, nadwyk z rzuca wedug wasnego wyboru.
                    </p>
                    <p>
                        W drugiej rozgrywce do pierwszej lewy wychodzi gracz, ktry wychodzi w pierwszej rozgrywce.
                    </p>
                    <p>
                        I tym razem po zakoczeniu rozgrywki karty nie s tasowane, lecz kady z graczy uywa kart zawartych w lewach, ktre wzi, do gry w nastpnej rozgrywce. Tym razem karty uzupenia si (i zrzuca) inaczej: gracz, ktry ma kart za mao, cignie brakujc mu ilo kart z rki gracza po lewej stronie; w ten sposb wszyscy gracze uzupeniaj ilo posiadanych w rku kart do dwunastu.
                    </p>
                    <p>
                        W trzeciej rozgrywce do pierwszej lewy wychodzi gracz po lewej stronie tego, ktry zaczyna rozgrywk uprzednio.
                    </p>
                    <p>
                        I tym razem po zakoczeniu rozgrywki karty nie s tasowane, lecz jak poprzednio kady z graczy uywa kart zawartych w lewach, ktre wzi, do czwartej, ostatniej rozgrywki.
                    </p>
                    <p>
                        Braki i nadwyki kart w rku usuwa si tym razem podobnie jak po pierwszej rozgrywce (cignc brakujc ilo kart z talonu lub zrzucajc nadwyk zakryt na st). W czwartej rozgrywce do pierwszej lewy wychodzi ten z graczy, ktry wychodzi w rozgrywce trzeciej.
                    </p>
                    <p>
                        Po zakoczeniu rundy rozgrywek sumuje si uzyskane wyniki (punkty plusowe i minusowe kadego z graczy).
                    </p>
                    <p>
                        Nastpnie do kolejnej rundy rozdaje karty gracz, ktry by pierwszym wychodzcym w pierwszej rozgrywce poprzedniej rundy.
                    </p>
                    <p>
                        Pena tura gry skada si z czterech rund (szesnastu rozgrywek) przy dwch grajcych lub z trzech rund (dwunastu rozgrywek) przy trzech grajcych.
                    </p>
                    <p>
                        Ca tur wygrywa ten z graczy, ktry zdoby najwiksz ilo punktw plusowych lub (jeli wszyscy gracze maj punkty minusowe) najmniejsz ilo punktw minusowych.
                    </p>
                </div>
            </div>
        );
    }
}



