import { basePath } from '../utils';
import React from 'react';


export default class Huragan extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Huragan',
            desc: 'gra dla 2 [4] osb',
            url: basePath() + '/huragan',
            players: [2, 4],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Wspczesna gra karciana, wywodzca si z dawnej gry rosyjskiej. Gra jest szczeglnie popularna na obszarze jzyka angielskiego i nazywana tam "hurricane" lub "tablanette".
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla dwch osb.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia kart, z ktrej usunito semki, dziewitki, dziesitki (a wic 40 kart).<br />
                        Warto punktowa kart od asa do sidemki -wedug iloci oczek, walet 8, dama - 9, krl - 10pkt.<br />
                        Kolor uprzywilejowany (lecz nie atutowy): kiery.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Gracze rozdaj karty w kolejnych rozgrywkach na zmian. Kady z grajcych otrzymuje po 6 kart. cztery karty odkryte wykada si na st, reszta kart pozostaje zakryta w talonie.
                    </p>
                    <p>
                        Gdy karty z rki zostan zgrane, obydwaj gracze otrzymuj po sze kart z talonu po raz drugi, a po ich zgraniu po raz trzeci.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Gr rozpoczyna przeciwnik rozdajcego.<br />
                        Moe on kart z rki wygra (zdoby) jedn lub wicej kart ze stou. Jedn kart ze stou zdobywa si, wykadajc z rki kart tej samej wysokoci. Wicej ni jedn kart ze stou zdobywa si, wykadajc z rki kart o wysokoci sumy oczek kart zdobywanych (wykadajc krla - 10pkt - zdobywamy pitk, trjk i dwjk - take 10pkt).
                    </p>
                    <p>
                        Jeli na stole ley jaka karta o stosunkowo wysokiej wartoci, nie wolno wikszej iloci innych kart ze stou o niszej wartoci zdobywa drug kart o tej samej wartoci.
                    </p>
                    <p>
                        Jeli w swojej kolejce gracz nie moe lub nie chce zdoby ani jednej karty ze stou, jedn ze swoich kart z rki ma obowizek wyoy na st - musi to jednak by taka karta, za pomoc ktrej nie mona w danym momencie gry zdoby adnej karty ze stou.
                    </p>
                    <p>
                        Gracz, ktry swoj kart z rki zdobywa wszystkie karty lece na stole, wykona tzw. huragan. Za huragan otrzymuje si jeden punkt w pniejszym obliczeniu - i kart na st wykada przeciwnik tego, ktry wykona huragan.
                    </p>
                    <p>
                        Kto wykada z rki ostatni kart w grze, zdobywa ni wszystkie karty lece na stole, lecz bez premiowego punktu za huragan.
                    </p>
                    <p>
                        Po zakoczeniu rozgrywki oblicza si punkty za zdobyte karty w nastpujcy sposb:
                    </p>
                    <ul>
                        <li>za wikszo zdobytych kart - 1pkt;</li>
                        <li>za wikszo zdobytych kart kierowych - 1pkt;</li>
                        <li>za zdobyt sidemk kierow - 1pkt;</li>
                        <li>za najwysz tzw. premier - 1pkt;</li>
                    </ul>
                    <p>
                        Premia jest to zestaw trzech lub czterech kart tej samej wysokoci, znajdujcych si w kartach zdobytych. Wysoko premiery oblicza si mnoc ilo kart w zestawie (trzy lub cztery) przez specjaln warto premierow kadej karty. S to wartoci nastpujce:<br />
                        sidemka - 21, szstka - 18, as - 16, pitka - 15, czwrka - 14, trjka - 13, dwjka - 12, krl - 10, dama - 9, walet - 8 punktw.<br />
                        Trzy pitki maj wic warto premierow 45 i s starsze od wartoci premierowych czterech dam (36); jeden punkt zapisuje sobie w tym przypadku zdobywca trzech pitek.
                    </p>
                    <p>
                        Gra toczy si w kolejnych rozdaniach do chwili, gdy jeden z graczy osignie w zapisie umwion z gry liczb punktw (zazwyczaj 16).
                    </p>

                    <h2 className="content-subhead">Huragan dla czterech osb</h2>
                    <p>
                        Dwie osoby siedzce na przeciw siebie graj przeciwko pozostaym dwom. Kady z grajcych otrzymuje po dziewi kart, pozostae cztery karty wykada si odkryte na stole.
                    </p>
                    <p>
                        Wszystkie inne zasady rozgrywki i zapisu - bez zmian.
                    </p>
                </div>
            </div>
        );
    }
}



