import { basePath } from '../utils';
import React from 'react';


export default class Machana extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Machana',
            desc: 'gra dla 2 - 4 graczy',
            url: basePath() + '/machana',
            players: [2, 3, 4],
            cards: 'Maa talia kart + Joker (25 karty)'
        }
    }
    
    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info"> Gra polska, stworzona okoo 1948 roku. Znana jest rwnie pod nazw 1224</div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 2 - 4 osb.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Maa talia kart + Joker (25 karty).<br />
                        Starszestwo kart: bez znaczenia.<br />
                        Warto punktowa: 9 - 9, 10 - 10, Walet - 20, Dama - 30, Krl - 40, As - 50, Joker - 66, Walet Karo - 22, Dama Trefl - 33, Krl Pik - 44, As Kier - 55.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pierwszy rozdajcy wybierany jest losowo, pniej rozdaje gracz siedzcy na lewo od pierwszego rozdajcego.<br />
                        Midzy graczy rozdaje si po 3 karty, Reszta pozostaje w zakrytym talonie.<br />
                        Przy maej iloci graczy, tj. tylko 2, dopuszcza si rozdawanie po 4 karty.
                    </p>
                    <p>
                        Gr rozpoczyna gracz siedzcy po lewej stronie rozdajacego. Gracz zdobywa punkty tylko z kart, ktre znajduj si w jego puli. Do puli mona wkada tylko dziewitki i dziesitki.
                    </p>
                    <p>
                        Mona to jednak zrobi tylko wtedy, gdy gracz posiada minimum jedn inn kart, czyli np. Waleta, Dam, Krla, Asa.<br />
                        Kadej z tych kart przyporzdkowana jest liczba, tj.: As - 1, Walet - 2, Dama - 3, Krl - 4.
                    </p>
                    <p>
                        Wtedy gracz wyrzuca na rodek jedn z tych kart (jeli nie posiada adnej z tych kart lub adnej dziewitki czy dziesitki - dobiera jedn kart z nie rozdanych i kolej przechodzi na nastpnego gracza).<br />
                        Po zrzuceniu na rodek np. Krla, gracz moe dooy do swojej puli (pooy przy sobie) dowoln liczb dziewitek i dziesitek, lecz maksymalnie tyle kart ile wynosi liczba przypisana do wyrzucanej na rodek karty, czyli w tym przypadku 4.<br />
                        Gracz dobiera z nierozdanych tyle kart ile nie woy do swojej puli.
                    </p>
                    <p>
                        Np.: Gracz wyrzuca na rodek Krla, do puli wkada 1 kart, wic dobiera 3.<br />
                        Suma kart dobranych i woonych do puli musi si rwna wartoci wyrzucanej na rodek karty.
                    </p>
                    <p>
                        Jeli gracz posiada Jokera moe do swojej puli zgarn karty wyrzucone na rodek.<br />
                        By to zrobi gracz musi wyoy na rodek Jokera i razem z nim zabiera do swojej puli wszystkie wyrzucone dotychczas karty, lecz w tym ruchu gracz nie dobiera adnych kart, nie moe take woy do puli adnej posiadanej dziewitki lub dziesitki.
                    </p>
                    <p>
                        Gracz moe wyoy Jokera tylko wtedy, gdy w swojej puli ma ju przynajmniej jedn kart.<br />
                        Jeli w momencie koca rozdania, ktry z graczy posiada w swoich kartach Jokera, to w gracz uzyskuje minus 100 punktw, a karty z jego puli si nie licz.
                    </p>

                    <h2 className="content-subhead">Zakoczenie</h2>
                    <p>Koniec rozdania nastpuje gdy ktry z graczy pozbdzie si swoich kart lub jeli skocz si karty nie rozdane.<br />
                        W pierwszym i drugim wypadku punkty liczone s tylko z kart woonych do puli, lecz w pierwszym przypadku 50 punktw dodatkowo otrzymuje gracz, ktry pozby si kart.
                    </p>
                    <p>
                        Zwycia gracz, ktry 4 razy posiada w swojej puli Jokera i jako pierwszy uzyska 1224 punkty.</p>
                </div>
            </div>
        );
    }
}



