import { basePath } from '../utils';
import React from 'react';


export default class TrzyTrzynascie extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Trzy Trzynacie',
            name2: '3-13',
            desc: 'gra dla 2 lub wicej osb',
            url: basePath() + '/trzytrzynascie',
            players: [2, 3, 4],
            cards: 'Standardowa talia kart (52 karty)'
        }
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">Jeden z wielu wariantw remika.</div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla dwch lub wicej osb</p>

                    <h2 className="content-subhead"></h2>
                    <p></p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>Dla dwch graczy jedna talia (52 karty), dwie talie dla trzech lub czterech graczy.</p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>Gra ma 11 rund. Pierwszy rozdajcy jest wybierany losowo. Kady nastpny rozdajcy jest z lewej strony poprzedniego. W pierwszej rundzie kademu z graczy rozdawane s trzy karty, w rundzie drugiej kademu s rozdawane cztery karty i z kad nastpn rund rozdawane s o jedn kart wicej, tak e w ostatniej (jedenastej) rundzie kady z graczy otrzymuje 13 kart. Reszt kart odkadamy figurami na d na stosie (zakryty stos). Grn kart z tego stosu odkrywamy i kadziemy obok. Jest to karta rozpoczynajca gr i tworzca drugi stos (odkryty stos).</p>

                    <h2 className="content-subhead">Cel gry</h2>
                    <p>
                        Celem gry jest utworzenie ze wszystkich kart na rce kompletw . S dwa typy kompletw:
                    </p>
                    <ol>
                        <li>
                            Grupa trzech lub wicej kart tych samych figur, np. 5 <span className="diamond"></span> - 5 <span className="spade"></span> - 5 <span className="club"></span></li>
                        <li>Sekwencja trzech lub wicej kart tego samego koloru, np. 4 <span className="heart"></span> - 5 <span className="heart"></span> - 6 <span className="heart"></span></li>
                    </ol>
                    <p>
                        Komplet moe zawiera wicej ni trzy karty - dla przykadu cztery sidemki lub 8-9-10-J-Q jednego koloru, ale nie mona uywa tych samych kart dla wicej ni jednego kompletu.
                    </p>

                    <h2 className="content-subhead"></h2>
                    <p></p>

                    <h2 className="content-subhead">"Dzikie karty"</h2>
                    <p>
                        W kadej rundzie jest "dzika karta". Jest to karta o wartoci rwnej liczbie rozdanych kart. "Dzika karta" moe by uywana w miejsce innej karty przy tworzeniu grupy lub sekwencji. Moesz take tworzy komplet skadajcy si tylko z "dzikich kart". Poniej znajduje si lista "dzikich kart" kadej rundy:
                    </p>

                    <table className="pure-table pure-table-horizontal">
                        <tbody>
                            <tr>
                                <td>Runda 1:</td>
                                <td>Trjki</td>
                            </tr>
                            <tr>
                                <td>Runda 2:</td>
                                <td>Czwrki</td>
                            </tr>
                            <tr>
                                <td>Runda 3:</td>
                                <td>Pitki</td>
                            </tr>
                            <tr>
                                <td>Runda 4:</td>
                                <td>Szstki</td>
                            </tr>
                            <tr>
                                <td>Runda 5:</td>
                                <td>Sidemki</td>
                            </tr>
                            <tr>
                                <td>Runda 6:</td>
                                <td>semki</td>
                            </tr>
                            <tr>
                                <td>Runda 7:</td>
                                <td>Dziewitki</td>
                            </tr>
                            <tr>
                                <td>Runda 8:</td>
                                <td>Dziesitki</td>
                            </tr>
                            <tr>
                                <td>Runda 9:</td>
                                <td>Walety</td>
                            </tr>
                            <tr>
                                <td>Runda 10:</td>
                                <td>Damy</td>
                            </tr>
                            <tr>
                                <td>Runda 11:</td>
                                <td>Krle</td>
                            </tr>
                        </tbody>
                    </table>

                    <h2 className="content-subhead">Gra</h2>
                    <p>Rozpoczyna gracz na lewo od rozdajcego, a kolejka idzie zgodnie z ruchem wskazwek zegara. Kolejka skada si z wycignicia jednej karty - albo pierwszej z gry ze stosu zakrytego, albo ze stosu odkrytego ostatnio wyrzuconej karty przez gracza poprzedniego - i wyrzucenia jednej karty na stos odkryty. Zauwa, e tylko ostatnio wyrzucona karta moe by brana z tego stosu, tzn. jeli jaki gracz wyrzuci kart, a nastpny wemie kart ze stosu zakrytego, karta ta ju nie moe by przez nikogo zabrana.</p>

                    <h2 className="content-subhead">Sytuacja kocowa</h2>
                    <p>eby zakoczy musisz uoy wszystkie karty z rki do oddzielnych kompletw. W tym przypadku ogo zakoczenie, lecz reszcie graczy wolno jeszcze bdzie wykona ostatni kolejk. Kiedy kolejka wrci do ciebie runda jest zakoczona i mona podliczy punkty.</p>

                    <h2 className="content-subhead">Punktacja</h2>
                    <p>Na koniec rundy reszta graczy dokada wszystkie moliwe karty do kompletw. Karty, ktre zostan danemu graczowi na rce s liczone jako punkty karne wg poniszej zasady:</p>
                    <table className="pure-table pure-table-horizontal">
                        <tbody>
                            <tr>
                                <td>As</td>
                                <td>1pkt</td>
                            </tr>
                            <tr>
                                <td>Dwjka</td>
                                <td>2pkt</td>
                            </tr>
                            <tr>
                                <td>Trjka</td>
                                <td>3pkt</td>
                            </tr>
                            <tr>
                                <td>Czwrka</td>
                                <td>4pkt</td>
                            </tr>
                            <tr>
                                <td>Pitka</td>
                                <td>5pkt</td>
                            </tr>
                            <tr>
                                <td>Szstka</td>
                                <td>6pkt</td>
                            </tr>
                            <tr>
                                <td>Sidemka</td>
                                <td>7pkt</td>
                            </tr>
                            <tr>
                                <td>semka</td>
                                <td>8pkt</td>
                            </tr>
                            <tr>
                                <td>Dziewitka</td>
                                <td>9pkt</td>
                            </tr>
                            <tr>
                                <td>Dziesitka</td>
                                <td>10pkt</td>
                            </tr>
                            <tr>
                                <td>Walet</td>
                                <td>10pkt</td>
                            </tr>
                            <tr>
                                <td>Dama</td>
                                <td>10pkt</td>
                            </tr>
                            <tr>
                                <td>Krl</td>
                                <td>10pkt</td>
                            </tr>
                        </tbody>
                    </table>
                    <p>Punkty s sumowane ze wszystkich rund. Wygrywa ten, kto w ostatniej rundzie bdzie mia najmniejsz liczb punktw.</p>
                </div>
            </div >
        );
    }
}



