import { basePath } from '../utils';
import React from 'react';


export default class Bezik extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Bezik',
            desc: 'gra dla 2 osb',
            url: basePath() + '/bezik',
            players: [2],
            cards: 'Dwie lub wicej talii kart'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Gra wywodzca si z Francji XVII wieku, znana w wielu wariantach w wielu krajach - na obszarze jzyka niemieckiego jako "binokel" (w Polsce na lsku jako "pinu"); na obszarze jzyka angielskiego jako "pinochle" (obydwie te nazwy wywodz si z woskiego "bini oculi" - "dwoje oczu"); uywanie dwu lub wicej identycznych talii kart jest cech charakterystyczn gry.<br />
                        Wariant gry szecioma taliami dla dwch osb, ktry opisany jest poniej, uwaany jest przez wiele autorytetw w dziedzinie gir za najciekawszy wariant bezika i jednoczenie za jedn z najlepszych gier karcianych dla dwch osb.
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 2 osb.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>Do gry uywa si dwie lub wicej talii kart.<br />
                        Poniej opisany jest wariant z szecioma taliami (6 x 32 karty od asw do sidemek = 192 karty).<br />
                        Starszestwo kart: as, dziesitka, krl, dama, walet, dziewitka, semka, sidemka.<br />
                        Istnieje zmienny kolor atutowy.</p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Po przetasowaniu wszystkich szeciu poczonych talii kart kady z graczy otrzymuje po dwanacie kart. Reszta kart pozostaje w zakrytym talonie.
                    </p>
                    <p>
                        Jeli ktry z graczy nie ma po rozdaniu kart ani jednej figury (krla, damy lub waleta) w rku, deklaruje to i zapisuje sobie od razu punkty premiowe za tak zwan "carte blanche". Gdy po zgraniu jednej karty dany gracz wci nie ma w rku ani jednej figury, zapisuje sobie po raz wtry premi za "carte blanche" i tak dalej a do chwili, gdy z talonu uzyska figur.<br />
                        Za dwanacie kart w rku bez figury w dalszym toku rozgrywki nie zapisuje si ju premii za "carte blanche".
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <div className="info">
                        Cay rachunek punktowy przy grze w bezika powinien by prowadzony na bieco i dostpny dla obydwu graczy.
                    </div>
                    <p>
                        Po rozdaniu kart i ewentualnym zapisaniu punktw za "carte blanche" rozpoczyna si rozgrywka, polegajca na zgrywaniu kolejno kart (z rki lub - z zadeklarowanych i wyoonych ukadw - ze stou).
                    </p>
                    <p>
                        Pierwszy wychodzi przeciwnik rozdajcego.<br />
                        Nie ma obowizku ani dorzucania do koloru, ani przebijania starsz kart.
                    </p>
                    <p>
                        Z dwu kart w tym samym kolorze wygrywa lew starsza karta. Jeli karty s w rnych kolorach, lew wygrywa wychodzcy. Jeli jedna z kart jest atutem, ona wygrywa lew. Jeli obydwie karty s identyczne, wygrywa lew wychodzcy.
                    </p>
                    <p>
                        Po zdobyciu lewy przed wyjciem do nastpnej gracze dobieraj po jednej karcie ze stosu zakrytego do rki (by znowu mie w rku 12 kart). Pierwszy dobiera kart ten z graczy, ktry wzi lew, i on wychodzi do nastpnej. Karty tworzce lewy pozostawia si na stole w stosie, ktrego w trakcie gry nie wolno adnemu z graczy przeglda.
                    </p>
                    <p>
                        Przed uzupenieniem rki i wyjciem gracz, ktry wygra lew, ma prawo zadeklarowa posiadany przez siebie ukad premiowany (jeli ma ich w rku wicej ni jeden, wybiera ktry) i zapisuje sobie za niego premi punktow, wykadajc karty w zadeklarowanym ukadzie przed sob na stole.
                    </p>
                    <p>
                        Karty wyoone na stole s wasnoci gracza (czci rki), ktry je wyoy, i mog by wedug jego uznania zgrywane lub zagrywane do kolejnych lew.
                    </p>
                    <p>
                        W pierwszym wyjciu rozgrywka jest bezatutowa. Kolor atutowy zostaje wyznaczony przez pierwszy zadeklarowany i wyoony maria (za ktry premi zapisuje si, oczywicie, jak za maria atutowy).
                    </p>
                    <p>
                        Gracz nie ma obowizku deklarowania posiadanych w rku ukadw kart ani nawet (co jest uprawnieniem teoretycznym) nie ma obowizku zapisywania sobie punktw premiowych za zadeklarowane i wyoone ukady.
                    </p>
                    <p>
                        Karty raz wyoone w ukadzie mog zosta uyte do kolejnego innego ukadu (np. po wyoeniu sekwensu gracz wykada jeszcze jednego krla koloru sekwensu, deklarujc drugi sekwens i zdobywajc za niego punkty). Wolno w ten sposb "rozamywa" i uzyskiwa na nowo ukady rwnowane lub cenniejsze, nie wolno uzyskiwa i zapisywa sobie punktw za ukady sabsze (gracz, ktry zadeklarowa i wyoy sekwens, nie ma ju prawa do deklaracji i zapisania sobie punktw za maria zawarty w tym sekwensie).
                    </p>
                    <p>
                        Dodatkowe ograniczenia zapisu punktowego:
                    </p>
                    <ul>
                        <li>
                            nie wolno wykada jednoczenie na st dwu sekwensw w tym samym kolorze;
                        </li>
                        <li>
                            nie wolno zapisywa sobie punktw za kwartet atutowy lub zwyky, dopki jaka karta w wysokoci tego kwartetu w innym ukadzie znajduje si wyoona na stole; np. gracz ma wyoone trzy walety kierowe (atutowe) i wykada podwjny bezik - nie moe zapisa sobie punktw za kwartet waletowy, dopki nie zgra do jakiej lewy jednego z waletw ze stou, ani te nie moe dooy waleta kier (by zapisa sobie 400pkt za waletowy kwartet atutowy) , dopki nie zgra obydwu waletw karowych ze stou.
                        </li>
                    </ul>

                    <h2 className="content-subhead">Premie punktowe</h2>
                    <table className="pure-table pure-table-horizontal">
                        <tbody>
                            <tr>
                                <td>za maria krlewski (K i D atutow)</td>
                                <td>- 40</td>
                            </tr>
                            <tr>
                                <td>za maria zwyky (K i D nieatutow)</td>
                                <td>- 20</td>
                            </tr>
                            <tr>
                                <td>za sekwens atutowy (A,10,K,D,W atutowe)</td>
                                <td>- 250</td>
                            </tr>
                            <tr>
                                <td>za sekwens zwyky (A,10,K,D,W nie atutowe)</td>
                                <td>- 150</td>
                            </tr>
                            <tr>
                                <td>za kwartet asowy (cztery A)</td>
                                <td>- 100</td>
                            </tr>
                            <tr>
                                <td>za kwartet krlewski (cztery K)</td>
                                <td>- 80</td>
                            </tr>
                            <tr>
                                <td>za kwartet damowy (cztery D)</td>
                                <td>- 60</td>
                            </tr>
                            <tr>
                                <td>za kwartet waletowy (cztery W)</td>
                                <td>- 40</td>
                            </tr>
                            <tr>
                                <td>za kwartet asowy atutowy (cztery A atutowe)</td>
                                <td>- 1000</td>
                            </tr>
                            <tr>
                                <td>za kwartet dziesitkowy atutowy (cztery 10 atutowe)</td>
                                <td>- 900</td>
                            </tr>
                            <tr>
                                <td>za kwartet krlewski atutowy (cztery K atutowe)</td>
                                <td>- 800</td>
                            </tr>
                            <tr>
                                <td>za kwartet damowy atutowy (cztery D atutowe)</td>
                                <td>- 600</td>
                            </tr>
                            <tr>
                                <td>za kwartet waletowy atutowy (cztery W atutowe)</td>
                                <td>- 400</td>
                            </tr>
                            <tr>
                                <td>za bezik (D <span className="spade"></span> i W <span className="diamond"></span> )</td>
                                <td>- 40</td>
                            </tr>
                            <tr>
                                <td>za bezik podwjny (dwie D <span className="spade"></span> i dwa W <span className="diamond"></span> )</td>
                                <td>- 500</td>
                            </tr>
                            <tr>
                                <td>za bezik potrjny (trzy D <span className="spade"></span> i trzy W <span className="diamond"></span> )</td>
                                <td>- 1500</td>
                            </tr>
                            <tr>
                                <td>za bezik poczwrny (cztery D <span className="spade"></span> i cztery W <span className="diamond"></span> )</td>
                                <td>- 4500</td>
                            </tr>
                            <tr>
                                <td>za ostatni lew w rozgrywce</td>
                                <td>- 250</td>
                            </tr>
                            <tr>
                                <td>za carte blanche</td><td>- 250</td>
                            </tr>
                            <tr>
                                <td>za wygran gr</td><td>- 1000</td>
                            </tr>
                            <tr>
                                <td>rubikon wynosi</td><td>- 3000</td>
                            </tr>
                        </tbody>
                    </table>
                    <p>
                        Gdy gracze wyczerpali stos (uzupenili kary w rku ostatnimi dwiema kartami ze stosu), zmieniaj si zasady zgaszania ukadw i rozgrywki w nastpujcy sposb:
                    </p>
                    <ul>
                        <li>nie wolno ju zgasza ani zapisywa sobie adnych premiowanych ukadw;</li>
                        <li>naley dorzuca do koloru wyjcia;</li>
                        <li>naley przebija starsz kart w kolorze;</li>
                        <li>naley, w przypadku nieposiadania koloru, przebija atutem.</li>
                    </ul>
                    <p>
                        Ten z graczy, ktry wygrywa ostatni lew, zdobywa 250 punktw premiowych (nie musi ich sobie zapisywa, jeli tego nie chce).
                    </p>
                    <p>
                        Po zakoczeniu rozgrywki nastpuje ostateczne rozliczenie wedug nastpujcych zasad:
                    </p>
                    <ul>
                        <li>
                            jeli gracz zdoby ponad 3000pkt w rozgrywce, przekroczy tzw. "rubikon" i jego punkty zostaj odjte od iloci punktw zdobytych przez gracza wygrywajcego (np. jeden z graczy zdoby w rozgrywce 7960pkt, drugi - 3780pkt; wygrana wynosi 4180pkt + 1000pkt za wygran, a wic 5180pkt; w skrcie i uproszczeniu liczy si w setkach - 52 due punkty dla gracza pierwszego);
                        </li>
                        <li>
                            jeli gracz nie uzyska 3000pkt w rozgrywce (jest przed rubikonem, zosta "zrubikowany"), punkty uzyskane przez niego zostaj dodane do punktw uzyskanych przez wygrywajcego przeciwnika; np. gracz pierwszy uzyska w rozgrywce 7960pkt, gracz drugi - 2780pkt - wwczas gracz pierwszy wygrywa 7960 + 2780 + 1000 (za wygran, razem 11740pkt, czyli 117 duych punktw.
                        </li>
                    </ul>
                    <p>
                        Jeli obaj gracze nie uzyskali 3000pkt w rozgrywce (nie przekroczyli rubikonu), wynik oblicza si tak, jak gdyby obydwaj przekroczyli t umown granic (odejmuje si uzyskane w rozgrywce iloci punktw).
                    </p>
                    <p>
                        Jeli rnica punktw midzy graczami wynosi mniej ni 100pkt, zawsze liczy si 1 duy punkt (a wic minimalna wygrana gra to 11 duych punktw).
                    </p>
                </div>
            </div>
        );
    }
}



