import { basePath } from '../utils';
import React from 'react';


export default class BlackJack extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Black Jack',
            name2: '21',
            url: basePath() + '/blackjack',
            players: [2, 3, 4, 5, 6, 7, 8],
            cards: 'Standardowa talia kart (52 karty)'
        }
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">Gra hazardowa.</div>

                    <h2 className="content-subhead">O grze</h2>
                    <p>W kasynach do tej gry uywa si specjalnych stow pokrytych zielonym ptnem, na ktrym zaznaczone s okna do stawiania zakadw. Poniej przedstawione s zasady gry lekko zmodyfikowane do uytku domowego :)</p>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>W grze uczestniczy krupier i maksymalnie siedmiu (?) graczy. <br />
                        Krupier jest pracownikiem kasyna gry. To on kieruje gr przy stole i odpowiada za porzdek gry, przyjmujc stawki i wypacajc wygrane.<br />
                        W domowej wersji black jacka rol krupiera moe peni jeden z graczy (np. gospodarz domu :).</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>Do gry uywa si standardowej talii kart (52 karty). Z reguy krupier operuje szecioma taliami jednoczenie.<br />
                        Nie wystpuje starszestwo kolorw.<br />
                        Wartoci punktowe figur: krl, dama, walet i dziesitka maj po dziesi punktw; as ma 1 punkt lub 11 w zalenoci od sytuacji; reszta kart zgodnie z nominaln wartoci na karcie.<br />
                        As ma domylnie 11 punktw, lecz w sytuacji kiedy wszystkie karty z asem przekraczaj liczb 21 as przyjmuje warto 1 (na korzy gracza; np. A, 7 i 10 dawayby sum 28, ale e liczba ta jest wiksza od 21 as przyjmuje warto 1 i suma tych trzech kart daje 18).</p>

                    <h2 className="content-subhead">Cel gry</h2>
                    <p>Celem gry jest uzyskanie w kartach 21 punktw. Najpewniejsz wygran jest osignicie black jacka.<br />
                        Black jack jest najwyszym ukadem kart w tej grze. Tworz go dwie karty: as i jedna z kart, ktra ma dziesi punktw (krl, dama, walet lub dziesitka). Musz to by dwie pierwsze kart, ktre s rozdawane na pocztku gry, czyli karty "z rki".<br />
                        Osignicie ukadu black jack po rozdzieleniu kart nie jest black jackiem. Liczy si tylko jako 21 punktw.<br />
                        W sytuacji, gdy zarwno krupier jak i gracz maj black jacka, wystpuje remis i stawka pozostaje do dyspozycji gracza do nastpnego rozdania.
                    </p>
                    <p>
                        Poniewa trafienie 21 oczek nie zdarza si za czsto, realizowany jest w grze cel zastpczy, czyli maksymalne zblienie si do tej liczby. Wygrywa ten, kto ma sum punktw blisz 21. Kady, kto przekroczy 21 oczek, przegrywa; jest to tzw. fura (bust).</p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>Po przetasowaniu kart przez krupiera i przeoeniu ich przez jednego z graczy odkadane s na stole. Od tego momentu mona rozpocz stawianie zakadw.<br />
                        Obowizuj minimalne i maksymalne stawki.<br />
                        Gracze stawiaj zakady na swoich pulach, do momentu a krupier signie po kart. Od tej chwili nie wolno ju stawia adnych zakadw. Krupier rozdaje karty, kadc po jednej przed kadym graczem oraz przed sob. Rozpoczyna od gracza znajdujcego si po lewej jego stronie.<br />
                        Karty s odkryte, tak e kady moe zobaczy od pocztku co otrzyma, a take co otrzyma krupier i pozostali gracze. Jest to o tyle wane, e kady moe oszacowa szans w pojedynku z krupierem. <br />
                        Nastpnie krupier rozdaje kademu z graczy po jeszcze jednej odkrytej karcie i sobie rwnie jedn, lecz zakryt.<br />
                        Krupier pyta czy chcemy cign nastpn kart. Gracz moe da dowolnej liczby kart, niezalenie od tego ile punktw osign. Krupier natomiast musi cign nastpn kart, jeeli suma dotychczasowa jest mniejsza lub rwna 16; jeeli suma kart przekroczy lub bdzie rwna 17, krupier zatrzymuje si. Wygrywa ten, kto ma sum blisz lub rwn 21. W przypadku remisu pula pozostaje w puli zakadu do dyspozycji gracza.
                    </p>
                    <p>
                        W przypadku uzyskania przez gracza black jacka, krupier paci 3:2. Oznacza to, e jeeli postawilimy 4 etony, to w przypadku black jacka otrzymamy 6, a jeeli postawilimy 9, otrzymamy 13,5.
                    </p>
                    <p>
                        Jedyn przewag krupiera w stosunku do gracza jest kolejno cignicia kart. Krupier cignie karty ostatni, ma moliwo obejrzenia, co maj inni gracze, a poza tym wszystkie wczeniejsze fury u graczy przegrywaj.</p>

                    <h2 className="content-subhead">Ubezpieczenie stawki</h2>
                    <p>Gracz moe dokona ubezpieczenia swojej stawki w przypadku, gdy pierwsza karty krupiera jest asem. Ubezpieczenie (insurance) polega na postawieniu odrbnego zakadu, ktrego warto wynosi dokadnie poow stawki, ktr gracz pierwotnie postawi.<br />
                        Ubezpieczamy si na wypadek zdobycia przez krupiera black jacka i tylko wtedy ubezpieczenie wygrywa.<br />
                        W sytuacji, gdy pierwsz kart krupiera jest as, istnieje due prawdopodobiestwo, e otrzyma on kart o nominale 10-ciu punktw. Wtedy ubezpieczenie wygrywa 2:1. Jeeli jednak krupier nie otrzyma black jacka, wwczas ubezpieczenie przegrywa. Pozostaje jeszcze zakad, ktry moe wygra.</p>
                    <div className="example">
                        <div className="title">Przykad ubezpieczenia stawki:</div>
                        <div className="content">
                            Postawilimy zakad 5 etonw i otrzymalimy kart 9. Krupier wylosowa asa. Przewidujemy, e krupier moe otrzyma black jacka i zawieramy ubezpieczenie. Dodatkow stawk, rwn poowie zakadu (2,5 etonu) umieszczamy na osobnej puli. Cigniemy nastpne karty. Powiedzmy, e s to 4 i 5, czna suma wynosi zatem 18.<br />
                            Krupier cignie kart i otrzymuje waleta (walet ma 10pkt). Tak wic krupier otrzyma black jacka i tym samym nasz gwny zakad przegra (tracimy 5 etonw). Poniewa jednak ubezpieczylimy nasz zakad, nasze ubezpieczenie wygrywa 2:1. Otrzymujemy wic z powrotem 5 etonw. W efekcie tej kombinacji pozostajemy z t liczb etonw, ile wynosi nasz pierwotny zakad. Jeeli krupier nie otrzymaby w tym przykadzie black jacka, lecz liczb poniej 18, wwczas przegralibymy ubezpieczenie, ale wygralibymy stawk gwn. Efekt - wygrywamy poow stawki. Jeeli krupier otrzymaby sum kart wiksz ni 18, przegralibymy wszystko. Jeeli suma wyniosa by dokadnie 18, przegralibymy tylko ubezpieczenie, stawka za pozostaaby nasza.
                            </div>
                    </div>

                    <h2 className="content-subhead">Podwojenie stawki</h2>
                    <p>Gracz moe podwoi swoj pierwotn stawk w przypadku, gdy suma w pierwszych jego dwch kartach rwna jest 9, 10 lub 11.<br />
                        Przykadowo, otrzymalimy ukad dwch kart 4 i 5, co daje w sumie 9 oczek. Stawka na naszej puli wynosi 5 etonw. Moemy j w tej sytuacji podwoi do 10 etonw.
                    </p>
                    <p>
                        Najwaniejsze przy podwajaniu stawki jest to, e moemy cign dodatkowo tylko jedn kart i musimy si zatrzyma. W kadym przypadku, gdy krupier widzi ukad upowaniajcy do podwajania stawki, pyta gracza, jaka jest jego decyzja.</p>

                    <h2 className="content-subhead">Rozdzielenie kart</h2>
                    <p>Jeeli dwie pierwsze karty, ktre otrzyma gracz, maj tak sam liczb punktw, np. 8 i 8, lub walet i krl, wwczas gracz moe rozdzieli karty na dwa odrbne zakady (lecz nie ma takiego obowizku). Oczywicie stawka zakadu znajdujca si na puli zostaje w tym momencie podwojona przez gracza, ktry od tej pory gra z krupierem dwoma zakadami. Do kadej z rozdzielonych kart cigniemy dodatkowe karty, aby osign sum najbardziej zblion do 21.
                    </p>
                    <p>
                        Osignicie w przypadku rozdzielenia dwch asw ukadu: as i ktra z kart o wartoci 10 punktw (krl, dama, walet, dziesitka), nie jest black jackiem, lecz liczy si jako 21pkt.<br />
                        Jest regu, e przy rozdzielaniu dwch asw gracz moe cign tylko jedn kart do kadego z nich.
                    </p>
                    <p>
                        Zdarza si, e po rozdzieleniu pierwszych dwch kart otrzymamy znowu par o jednakowej liczbie punktw. W takiej sytuacji moemy ponownie dokona rozdzielenia kart i zwikszy stawk. Ponowne pojawienie si dwch jednakowych kart pozwala na jeszcze jedno rozdzielenie kart - ostatnie z moliwych.</p>
                    <p>
                        Dodatkowo przy rozdzielaniu kart moemy otrzyma sum dwch kart rwn 9, 10 lub 11. Kadorazowo wtedy moemy tak stawk podwoi. Naley pamita, e przy podojeniu stawki moemy cign tylko jedn kart.</p>
                </div>
            </div>
        );
    }
}



