import { basePath } from '../utils';
import React from 'react';


export default class Ogorek extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Ogrek',
            desc: 'gra dla 2 - 7 osb',
            url: basePath() + '/ogorek',
            players: [2, 3, 4, 5, 6, 7],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 2 - 7 osb.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Talia 52 kart.<br />
                        Kolor nie ma znaczenia.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Rozdajcego wybiera si przez losowanie, ktry po przetasowaniu kart rozdaje kademu z graczy po 7 kart na raz, rozpoczynajc od gracza po jego lewej stronie. Karty nie rozdane s odkadane na zakrytym stosie.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        W rozdaniu granych jest 7 lew. Karty zrzucane na st nie s zbierane ani odwracane po kadej lewie. Kady z graczy ma prawo sprawdzi karty wczeniej wyrzucone.<br />
                        Rozpoczyna gracz po lewej stronie rozdajcego. Gracz, ktry wygra kolejk, wychodzi do nastpnej lewy. Pierwsza wyrzucona karta w lewie jest prowadzca, reszta graczy rzuca za ni do lewy zgodnie z ruchem wskazwek zegara. Prowadzcy wybiera dowoln kart, reszta graczy natomiast ma do wyboru w kolejce:
                    </p>
                    <ul>
                        <li>zagra kart, ktrej warto jest rwna lub wysza od prowadzcej;</li>
                        <li>zagra swoj najnisz kart.</li>
                    </ul>

                    <p>
                        Najwysza karta bierze lew. Jeli w lewie jest kilka takich samych, bierze j ostatnio pooona.<br />
                        Jeli kolejny gracz, ktry powinien rozdawa zostanie wyeliminowany z gry, rozdajcym jest nastpny w kolejnoci.
                    </p>

                    <h2 className="content-subhead">Punktacja</h2>
                    <p>
                        Gracz, ktry wzi ostatni lew traci rozdanie i jest karany. Punktacja graczy jest sumowana z kar jakie zgromadzili. Przegrany w rozdaniu gracz otrzymuje punkty karne w zalenoci od wartoci karty zrzuconej w ostatniej lewie (A=14, K=13, D=12, W=11, reszta kart zgodnie z ich wartoci). Kiedy punktacja osignie lub przekroczy 21 punktw, dany gracz przegrywa. Moe on powrci do gry, lecz na jego konto zapisuje si najwysz liczb punktw zdobyt przez ktrego z reszty graczy. Jeli osignie on 21 punktw po raz drugi, wypada z gry na stae.<br />
                        Ewentualnie, przy grze w dwie osoby, gra toczy si do momentu a ktry z graczy zdobdzie lub przekroczy 21 punktw.
                    </p>
                    <p>
                        Jeli w ostatniej lewie ktry z graczy rzuci kart tej samej wysokoci co gracz, ktry j wemie (przegrywajcy dan lew), dostaje on premi o takiej samej wysokoci co warto tej karty (jednoczenie takiej samej co kara dla gracza, ktry t lew wzi). Premia ta jest odejmowana od punktacji danego gracza.
                    </p>
                    <p>
                        Np.: w ostatniej lewie brao udzia szeciu graczy, a karty przez nich zagrane to kolejno: 5, 9, 3, 9, 9 i 7. Pity gracz bierze lew ostatni rzucon dziewitk i otrzymuje 9 punktw karnych; drugi i czwarty gracz dostaj premi 9pkt, ktr odejmuje si od zgromadzonych do tej pory przez nich punktw.
                    </p>
                    <p>
                        Odejmujc premi nie obnia si oglnej punktacji poniej zera.<br />
                        Dla graczy, ktrzy weszli po raz drugi do gry, premia nie jest obniana poniej wartoci z jak ponownie weszli do gry.<br />
                        I tak, przy grze w dwie osoby, gdy obaj maj po 18 pkt i obaj w ostatniej lewie zrzucaj dziesitki, gracz ktry zrzuci j drugi otrzymuje 10pkt karnych (przekracza warto 21pkt) i przystpuje ponownie do gry z 18 punktami (ilo punktw zdobytych przez przeciwnika), przeciwnik natomiast otrzymuje 10pkt premii (za zrzucon kart o tej samej wysokoci co zabierajcy lew) zmniejszajc swj wynik do 8 punktw.
                    </p>
                    <p>
                        Zwycizca zostaje pierwszym rozdajcym w nastpnej grze.
                    </p>
                </div>
            </div>
        );
    }
}



