import { basePath } from '../utils';
import React from 'react';


export default class Poker extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Poker',
            desc: 'gra dla 5 (od 2 do 8) osb ',
            url: basePath() + '/poker',
            players: [2, 3, 4, 5, 6, 7, 8],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">Gra hazardowa.</div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>
                        Chocia nie ma okrelonego limitu liczby uczestnikw, w pokerze powinno bra udzia od 2 do 8 graczy, a optymalna ich liczba to 5.
                    </p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia kart (52 karty). Jeeli graczy jest mniej ni piciu zaleca si stosowanie talii o mniejszej iloci kart (od 48 do 24 ze skokiem co 4 karty tzn. odejmujc wszystkie 4 karty o danej wartoci).<br />
                        Starszestwo kart od asa do dwjki (w niektrych odmianach przyjmuje si, e as jest kart najmodsz o wartoci 1).<br />
                        Nie ma starszestwa kolorw.
                    </p>

                    <h2 className="content-subhead">Cel gry</h2>
                    <p>
                        Celem gry jest uzyskanie jak najwyszej kombinacji piciu kart, ktre mona kombinowa wedug: kart tej samej wartoci, tego samego koloru lub wedug sekwensu (kolejno wzrastajcych wartoci kart).
                    </p>

                    <h2 className="content-subhead">Znaczenia ukadw</h2>
                    <p>
                        Poniej podane jest 9 moliwych ukadw, od najmniej do najbardziej wartociowego.<br />
                        Hierarchia ta wynika ze wzgldnej czstoci wystpowania poszczeglnych ukadw.
                    </p>
                    <a name="starszenstwo"></a>
                    <table className="pure-table pure-table-horizontal">
                        <tbody>
                            <tr className="pure-table-odd">
                                <td>Lp.</td>
                                <td>ang</td>
                                <td>pl</td>
                                <td>opis</td>
                            </tr>
                            <tr>
                                <td>1</td>
                                <td>No pair</td>
                                <td>bez pary</td>
                                <td>Najmniej ceniony ukad kart. O wartoci w stosunku do innych ukadw przesdza warto najstarszej posiadanej karty. Jeeli wszyscy gracze maj "bez pary" o wygranej decyduje najstarsza karta; jeeli jest kilku graczy, ktrzy maj takie same najwysze karty, to decyduje o wygranej midzy nimi karta druga lub nawet trzecia; jeeli wszystkie pi kart jest jednakowych, nastpuje podzia puli</td>
                            </tr>
                            <tr>
                                <td>2</td>
                                <td>One pair</td>
                                <td>para</td>
                                <td>sytuacja, gdy pord piciu kart znajduj si dwie karty o tej samej wartoci, np.: Walet, dwa Krle, sidemka i czwrka; jeeli dwch graczy ma par, to zwycia posiadacz pary skadajcej si z kart o wyszej wartoci; jeeli pary s takie same decyduje najstarsza z pozostaych kart</td>
                            </tr>
                            <tr>
                                <td>3</td>
                                <td>Two pair</td>
                                <td>dwie pary</td>
                                <td>jeeli dwch graczy posiada dwie pary, to o zwycistwie decyduje warto najstarszej pary; jeeli ich warto jest taka sama , o zwycistwie decyduje warto drugiej pary lub ewentualnie pita karta w przypadku takich samych obu par</td>
                            </tr>
                            <tr>
                                <td>4</td>
                                <td>Three of a kind</td>
                                <td>trjka</td>
                                <td>trzy karty tej samej wartoci; w przypadku, gdy wicej graczy ma trjki, o wygranej decyduje warto kart skadajcych si na t trjk (nie ma moliwoci wystpienia dwch trjek tej samej wartoci)</td>
                            </tr>
                            <tr>
                                <td>5</td>
                                <td>Straight</td>
                                <td>strit/sekwens</td>
                                <td>sytuacja piciu kolejno nastpujcych po sobie kart, niezalenie od ich koloru. Najwyej cenionym stritem jest tzw. strit asowy, zawierajcy: asa, krla, dam waleta i dziesitk; strit o najmniejszej wartoci to strit pitkowy: 5, 4, 3, 2 i as (liczony jako 1). W przypadku dwch stritw, o zwycistwie decyduje najstarsza karta</td>
                            </tr>
                            <tr>
                                <td>6</td>
                                <td>Flush</td>
                                <td>kolor</td>
                                <td>pi kart nalecych do tego samego koloru, np. piciu trefli; nie odgrywa roli kolejno kart; w przypadku dwch posiadanych kolorw wygrywa ten, ktry ma w swoim kolorze najstarsz kart, ewentualnie o zwycistwie decyduje karta druga, trzecia, czwarta, lub pita</td>
                            </tr>
                            <tr>
                                <td>7</td>
                                <td>Full haus</td>
                                <td>ful</td>
                                <td>jednoczesna kombinacja trjki i pary; o zwycistwie midzy fulami decyduj wartoci trjek wchodzcych w ich skad</td>
                            </tr>
                            <tr>
                                <td>8</td>
                                <td>Four of a kind</td>
                                <td>kareta</td>
                                <td>cztery karty tej samej wartoci; najstarsza kareta to cztery asy</td>
                            </tr>
                            <tr>
                                <td>9</td>
                                <td>Straight flush</td>
                                <td>poker</td>
                                <td>jednoczesna kombinacja strita i koloru, czyli pi kolejnych kart tego samego koloru; najsabszy poker to: 5, 4, 3, 2 i As w tym samym kolorze; najstarszy poker to: As, Krl, Dama, Walet i Dziesitka w tym samym kolorze, tzw. krlewski poker (royal flush); przy kilku pokerach wygrywa ten z najwysz kart</td>
                            </tr>
                        </tbody>
                    </table>

                    <p>
                        Powysza hierarchia dotyczy pokera rozgrywanego pen tali 52 kart. Przy grze skrcon tali kart hierarchia ta ulega zmianie zgodnie z ponisz tabel:
                    </p>

                    <table className="pure-table">
                        <tbody>
                            <tr className="pure-table-odd">
                                <td>Liczba kart</td>
                                <td>52</td>
                                <td>48</td>
                                <td>44</td>
                                <td>40</td>
                                <td>36</td>
                                <td>32</td>
                            </tr>
                            <tr>
                                <td>Hierarchia</td>
                                <td>poker<br />kareta<br />ful<br />kolor<br />strit</td>
                                <td>poker<br />kareta<br />kolor<br />ful<br />strit</td>
                                <td>poker<br />kareta<br />kolor<br />ful<br />strit</td>
                                <td>poker<br />kareta<br />kolor<br />ful<br />strit</td>
                                <td>poker<br />kareta<br />kolor<br />ful<br />strit</td>
                                <td>poker<br />kolor<br />kareta<br />ful<br />strit</td>
                            </tr>
                        </tbody>
                    </table>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Pierwszego rozdajcego wybiera si losowo. Tasuje on karty i daje do przeoenia. Przed rozdaniem kart rozdajcy daje haso do uiszczenia podstawowej stawki (rwnej stawce minimalnej uzgodnionej przed rozpoczciem gry). Nastpnie kademu z uczestnikw rozdaje pojedyncze karty zgodnie z ruchem wskazwek zegara, do momentu a kady z nich otrzyma po pi kart. Reszt kart odkada zakryte na stole.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Gracze po otrzymaniu kart decyduj si na dalsz gr lub pasuj.<br />
                        Gr otwiera gracz siedzcy na lewo od rozdajcego. Moe on pasowa (tzn. wycofa si z gry) lub otworzy gr.<br />
                        Pasujcy traci stawk wejciow, a kolejka przechodzi na nastpnego gracza zgodnie z ruchem wskazwek zegara.<br />
                        Otwierajc gr, gracz deklaruje swoje wejcie do puli stawk wiksz od minimalnej i nie wiksz od maksymalnej (rwnie wczeniej uzgodnionej).
                    </p>
                    <p>
                        Po otwarciu puli kady z graczy ma trzy moliwoci: pasowa, utrzyma stawk poprzednika lub przebi stawk.<br />
                        Pasowanie oznacza dla gracza koniec gry i utrat stawki wejciowej.<br />
                        Utrzymanie stawki poprzednika oznacza wpat do puli w wysokoci stawki poprzednika i prawo do dalszej gry i licytacji.<br />
                        Przebicie stawki oznacza wpat wyszej stawki. Podwyszona stawka jest obowizujca dla nastpnych graczy chccych bra udzia w dalszej grze. Kto nie chce lub nie moe si podporzdkowa, musi pasowa, tracc dotychczasowy wkad.
                    </p>
                    <p>
                        Kolejk zakadw moe zakoczy tylko pasowanie lub utrzymanie stawki przez wszystkich graczy, lub te sytuacja, gdy po podwyszeniu stawki wszyscy gracze pasuj lub tylko utrzymuj zwikszon stawk.
                    </p>
                    <p>
                        Po pierwszej licytacji, wszyscy gracze aktywni (ci, ktrzy nie pasowali) maj prawo do wymiany czci lub wszystkich swoich kart. Pierwszy wymienia najbliszy aktywny gracz po lewej stronie rozdajcego. Po oddaniu okrelonej liczby kart, rozdajcy musi mu da tak sam liczb kart z talonu zakrytego.<br />
                        Nie ma obowizku wymiany kart.
                    </p>
                    <p>
                        Moe zaistnie sytuacja, e zabraknie kart na wymian, wtedy wykorzystujemy, po przetasowaniu, karty odrzucone przez innych graczy.
                    </p>
                    <p>
                        Po wymianie kart nastpuje druga kolejka zakadw. Prawo do otwarcia ma znowu najbliszy gracz aktywny siedzcy po lewej stronie rozdajcego.<br />
                        Przebieg drugiej licytacji jest identyczny jak pierwszej, z tym e udzia w niej bior tylko ci gracze, ktrzy wczeniej nie spasowali.
                    </p>
                    <p>
                        Po drugiej licytacji naley ustali zwycizc rozgrywki.<br />
                        Sprawdzenie moe nastpi z wyoeniem kart lub bez.<br />
                        Jako pierwszy wykada karty ostatni podwyszajcy stawk lub otwierajcy drug licytacj. Kolejni gracze wykadaj swoje karty, jeeli uwaaj, e maj wysze lub jeli byo tak uzgodnione.
                    </p>
                    <p>
                        Kady z graczy moe objani, co ma lub moe tego nie robi.<br />
                        Posiadacz najwyszej kombinacji bierze ca pul. Jeli jest dwch takich graczy, to dziel oni pul midzy sob.<br />
                        W przypadku, gdy pozostanie niepodzielna reszta, przechodzi ona do nastpnej puli.
                    </p>
                    <p>
                        Jeeli w czasie drugiej licytacji wszyscy gracze z wyjtkiem jednego pasuj, bierze on ca pul i nie ma obowizku ujawni swoich kart.
                    </p>
                    <p>
                        Po okreleniu zwycizcy kolejny gracz siedzcy po lewej stronie rozdajcego jest nowym rozdajcym i gra rozpoczyna si od nowa.
                    </p>
                </div>

                <div className="content">
                    <a name="rownolegly"></a>
                    <h1 className="content-subhead main-header">Poker rwnolegy</h1>
                    <div className="info">
                        Gra opracowana przez angielskiego autora i znawc w dziedzinie gier, Huberta Phillipsa, w 1953 roku. Gra jest znana na obszarze jzyka angielskiego jako "quintet", na obszarze jzyka francuskiego za jako "pochette".
                    </div>

                    <h2 className="content-subhead">Gracze i karty</h2>
                    <p>
                        Gra dla dwch osb.<br />
                        Dwie preferansowe talie kart - po 32 karty.<br />
                        Starszestwo kart: od Asa do Sidemki.<br />
                        Starszestwo kolorw nie istnieje.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Kady z grajcych otrzymuje pen tali kart. Rozdajcego wyznacza si przez losowanie. Rozdajcy tasuje swoj tali i wykada z niej pierwsze siedem kart odkryte na stole. Karty te nie wezm udziau w grze w danym cyklu rozgrywek - przeciwnik rozdajcego take usuwa ze swojej talii takie same karty.<br />
                        W ten sposb kady z graczy dysponuje do gry 25 takimi samymi co kontrpartner kartami.
                    </p>
                    <p>
                        Gracze, kady dla siebie, dziel swoje 25 kart na pi pakietw po pi kart w kadym, w sposb dowolny, ukadajc kolejne pakiety zakryte przed sob.
                    </p>
                    <p>
                        W rozgrywce nastpuje kolejno porwnywanie wartoci pakietw kart, zestawionych przez obydwu graczy, jako tzw. "rk pokerowych", tj. wedug <a className="pure-menu-link underline" href="#starszenstwo">starszestwa ukadw kart</a> przyjtego w tradycyjnej grze w pokera
                    </p>
                    <p>
                        Po zestawieniu przez graczy piciu pakietw (figur pokerowych) nastpuje rozgrywka - ich porwnanie.<br />
                        Pakiety naley porwnywa kolejno: ustalonej przez graczy przed rozpoczciem pokazw-rozlicze kolejnoci nie wolno zmienia.
                    </p>
                    <p>
                        Za wygran w pierwszym pokazie - 3pkt, w drugim - 4pkt i dalej po jednym punkcie wicej, a do wygranej za pity pokaz - 7pkt.
                    </p>
                    <p>
                        Ca gr wygrywa ten z graczy, ktry w piciu pokazach zdoby wicej punktw.
                    </p>

                    <div className="example">
                        <div className="title">
                            Przykadowa partia pokera rwnolegego:
                        </div>
                        <div className="content">
                            <p>
                                Rozdajcy gracz A wyoy na st odkryte karty: 8 <span className="heart"></span>, 7 <span className="heart"></span>, 9 <span className="diamond"></span>, 10 <span className="spade"></span>, 7 <span className="spade"></span>, D <span className="club"></span> i W <span className="club"></span>.<br />
                                Te karty odpadaj wic z gry, zostaj usunite z talii obydwu graczy. Z pozostaych kart gracze ustalaj zestawy piciu pokazw i porwnuj je midzy sob.
                            </p>
                            <p>
                                W pierwszym pokazie gracz A demonstruje 9 <span className="spade"></span>, 9 <span className="club"></span>, 8 <span className="spade"></span>, 8 <span className="club"></span> i 7 <span className="club"></span> (dwie pary dziewitkowe), gracz B za demonstruje A <span className="spade"></span>, A <span className="heart"></span>, A <span className="club"></span>, 8 <span className="diamond"></span> i 8 <span className="club"></span> (fula asowego).<br />
                                Gracz B wygra 3 punkty.
                            </p>
                            <p>
                                W drugim pokazie gracz A demonstruje K <span className="spade"></span>, K <span className="diamond"></span>, K <span className="club"></span>, W <span className="spade"></span> i 10 <span className="club"></span> (trjk krli), gracz B za demonstruje K <span className="spade"></span>, D <span className="spade"></span>, W <span className="spade"></span>, 9 <span className="spade"></span> i 8 <span className="spade"></span> (kolor pikowy).<br />
                                Znw wygra gracz B, zdobywajc 4 punkty - ma cznie 7pkt.
                            </p>
                            <p>
                                W trzecim pokazie gracz A demonstruje D <span className="diamond"></span>, W <span className="diamond"></span>, 10 <span className="diamond"></span>, 8 <span className="diamond"></span> i 7 <span className="diamond"></span> (kolor karowy), gracz B za demonstruje K <span className="heart"></span>, D <span className="heart"></span>, W <span className="heart"></span>, 10 <span className="heart"></span> i 9 <span className="heart"></span> (poker od krla).<br />
                                Znw wygra gracz B, zdobywajc 5 punkty - ma ju cznie 12pkt.
                            </p>
                            <p>
                                W czwartym pokazie gracz A ma A <span className="spade"></span>, A <span className="heart"></span>, A <span className="diamond"></span>, A <span className="club"></span> i D <span className="spade"></span> (kareta Asw), gracz B za ma A <span className="diamond"></span>, K <span className="diamond"></span>, D <span className="diamond"></span>, W <span className="diamond"></span> i 10 <span className="diamond"></span> (poker od Asa).<br />
                                I tym razem wygra B, zdobywajc 6 punkty - ma ich ju 18pkt.
                            </p>
                            <p>
                                W pitym pokazie gracz A demonstruje K <span className="heart"></span>, D <span className="heart"></span>, W <span className="heart"></span>, 10 <span className="heart"></span> i 9 <span className="heart"></span> (poker od krla), gracz B za ma 7 <span className="diamond"></span>, 7 <span className="club"></span>, K <span className="club"></span>, 10 <span className="club"></span> i 9 <span className="club"></span> (para sidemek).<br />
                                Ten pokaz wygra A, zdobywajc 7 punktw.
                            </p>
                            <p>
                                cznie gracz B zdoby 18 punktw, gracz A - 7 punktw, a wic ca gr wygra gracz B.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}



