import { basePath } from '../utils';
import React from 'react';


export default class MauMau extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Mau-mau',
            name2: 'Pony',
            url: basePath() + '/maumau',
            players: [],
            cards: 'Pikietowa talia kart (32 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Gra wspczesna, zwana take jako "pony".
                    </div>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pikietowa talia kart (32 karty).<br />
                        Nie ma koloru atutowego.<br />
                        Starszestwo kart nie ma w grze znaczenia.<br />
                        Warto punktowa kart: as - 11, krl - 4, dama - 3, walet - 2, pozostae karty wedug iloci oczek.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie. Do nastpnych rozda gracze rozdaj karty kolejno. Gr rozpoczyna zawsze gracz na lewo od rozdajcego.<br />
                        Kady z grajcych otrzymuje po pi kart. Nastpn kart kadzie si odsonit na stole ("wywica si") - jest to pierwsza karta odkrytego stosu. Reszta kart pozostaje w zakrytym stosie.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Gracze kolejno, w kierunku ruchu wskazwek zegara, wykadaj po jednej karcie z rki na st, na odkryty stos.<br />
                        Wolno w swojej kolejce gry dooy kart tego samego koloru co wierzchnia karta stosu lub tej samej wysokoci.
                    </p>
                    <p>
                        Celem gry jest pozbycie si wszystkich kart z rki dla uzyskania w zapisie w kolejnych rozgrywkach jak najmniejszej iloci punktw minusowych.
                    </p>
                    <p>
                        Wszystkie walety peni rol specjaln: kadego waleta mona zawsze wyoy na st, zapowiadajc jednoczenie pocztek nowego koloru, ktry obowizuje nastpnego gracza (nie musi to by kolor wykadanego waleta; moe by dowolnie wybrany przez gracza, ktry waleta wykada).
                    </p>
                    <p>
                        Gdy gracz w swojej kolejce gry nie moe wyoy adnej karty z rki, pobiera jedn kart ze stosu zakrytego. Gdy nadal nie moe wyoy adnej karty, kolej gry przechodzi na nastpnego gracza.
                    </p>
                    <p>
                        Rozgrywka koczy si z chwil, gdy ktry z graczy pozbywa si ostatniej karty z rki. Wygrywajcy nie otrzymuje adnych punktw - pozostali gracze otrzymuj ilo punktw minusowych rwn iloci oczek wszystkich kart pozostaych w rku.
                    </p>
                    <p>
                        Gra toczy si w kolejnych rozdaniach do chwili, gdy jeden z graczy w zapisie przekroczy umwion z gry ilo punktw (zazwyczaj 100pkt).
                    </p>

                    <h2 className="content-subhead">Warianty</h2>
                    <p>
                        W mau-mau, szczeglnie przy wikszej iloci uczestnikw gry, mona gra pen tali (52 karty).<br />
                        Mona umwi si, e obowizuje jedna lub kilka spord nastpujcych, dodatkowych regu rozgrywki i zapisu:
                    </p>
                    <ol>
                        <li>
                            jeli karta koczca gr (ostatnia z rki) jest waletem, zapis punktowy wszystkich graczy przegrywajcych rozgrywk podwaja si;
                        </li>
                        <li>
                            po zrzuceniu sidemki do stosu nastpny gracz ma obowizek pobra jedn kart ze stosu zakrytego i nie zrzuca adnej karty;
                        </li>
                        <li>
                            po zrzuceniu semki do stosu odkrytego nastpny gracz traci kolejk (zostaje ominity) w grze;
                        </li>
                        <li>
                            na dam wolno pooy tylko krla tego samego koloru lub dowolnego waleta;
                        </li>
                        <li>
                            kady z grajcych otrzymuje nie po 5, lecz po 7 kart;
                        </li>
                    </ol>
                    <p>
                        Pozostae reguy gry i zapisu - bez zmian.
                    </p>
                </div>
            </div>
        );
    }
}



