import { basePath } from '../utils';
import React from 'react';


export default class Swinie extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'winie',
            desc: 'gra dla 2 lub wicej osb',
            url: basePath() + '/swinie',
            players: [2, 3, 4, 5, 6, 7, 8],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 2 lub wicej osb.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia kart (52 karty).<br />
                        Przy wikszej liczbie graczy mona uy kilku talii.<br />
                        Starszestwo kart nie ma znaczenia.
                    </p>

                    <h2 className="content-subhead">Zasady gry</h2>
                    <p>
                        Pierwszego tasujcego karty wybiera si przez losowanie.<br />
                        Po przetasowaniu ca tali kart rozrzuca si na stole w nieadzie (takie stado wi :), nie odkrywajc ich. Gracz, ktry tasowa karty, wykada jedn z nich odkryt pord "wi". Rozpoczyna ona odkryty stos. Kolejka zaczyna si od gracza po lewej stronie tego, ktry tasowa, i posuwa si zgodnie z ruchem wskazwek zegara.
                    </p>
                    <p>
                        Gracz, na ktrego przysza kolej, cignie dowoln ze "wi" i jeli kolor lub warto tej karty jest zgodna z wierzchni kart stosu, moe j na ten stos pooy i przekaza tym samym kolejk do nastpnego gracza. Jeli gracz nie moe lub nie chce tej karty pooy na stos, cignie nastpn ze "wi", i tak dalej, a do momentu kiedy pooy kart na stosie. Wtedy dopiero kolejka przechodzi na nastpnego gracza.
                    </p>
                    <p>
                        Podczas gry poszczeglni gracze mog gromadzi karty na rku i kiedy nadejdzie ich kolejka, nie musz cign "wini", lecz mog wyrzuci kart z rki i przekaza kolejk do nastpnego gracza.
                    </p>
                    <p>
                        Jeli gracz ma na rku kilka kart tej samej wartoci, moe je wyrzuci jednoczenie pod warunkiem, e warto ich zgadza si z wartoci karty lecej na wierzchu stosu.
                    </p>
                    <p>
                        W momencie, gdy skocz si karty porozrzucane na stole ("winie"), kady z graczy operuje tylko kartami na rku. W takim wypadku, jeli gracz nie moe zrzuci adnej karty, traci jedn kolejk, ktra przechodzi do nastpnego gracza.
                    </p>
                    <p>
                        Gra toczy si do momentu, a z kartami na rku zostanie jeden gracz. Jeli zdarzy si sytuacja, e kilku graczy zostanie z kartami na rku i aden z nich nie bdzie mg adnej z nich zrzuci na stos, gra rwnie si koczy.
                    </p>

                    <h2 className="content-subhead">Punktacja</h2>
                    <p>
                        Kady z graczy otrzymuje tyle punktw, ile zostao mu, po zakoczeniu gry, kart na rku. Jeli gracz pozby si wszystkich kart - otrzymuje 0 punktw.<br />
                        Punkty z kolejnych gier sumuj si.
                    </p>
                    <p>
                        Gra toczy si do momentu, a jeden z graczy zdobdzie lub przekroczy wczeniej umwion liczb punktw, lub toczy si okrelon wczeniej ilo razy (np. tyle razy, ilu jest graczy).
                    </p>
                    <p>
                        Gr wygrywa ten z graczy, ktry w kocowym rozliczeniu zdoby najmniej punktw.
                    </p>
                </div>
            </div>
        );
    }
}



