import { basePath } from '../utils';
import React from 'react';


export default class Ecarte extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Ecarte',
            desc: 'gra dla 2 osb',
            url: basePath() + '/ecarte',
            players: [2],
            cards: 'Pikietowa talia kart (32 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Gra wywodzca si z siedemnastowiecznej Francji, znana pod rnymi nazwami lub w nieco innych formach w wielu krajach Europy jako Napoleon, mistigri, pamfilius, euchre, lanterlu i inne.<br />
                        W Polsce rne formy tej gry znane byy ju w XVIII wieku i pniej pod nazwami panfil, pantofel, baamut, picy kot i inne.
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 2 osb.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pikietowa talia kart (32 karty).<br />
                        Starszestwo kart jest specyficzne: krl, dama, walet, as, dziesitka, dziewitka, semka, sidemka.<br />
                        Istnieje zmienny kolor atutowy.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pierwszego rozdajcego wybiera si przez losowanie. Rozdajcy daje przeciwnikowi i sobie po pi kart (tradycyjnie najpierw trzy, potem dwie), jedenast kart wywica, umieszczajc j odkryt pod zakrytym stosem (reszt kart). Karta wywicona wyznacza kolor atutowy w danym rozdaniu. Jeli jest to krl, rozdajcy zdobywa punkt.
                    </p>
                    <p>
                        Celem rozgrywki jest wzicie co najmniej trzech spord piciu lew. Wychodzi pierwszy, do pierwszej lewy, zawsze przeciwnik rozdajcego; do dalszych lew wychodzi ten z graczy, ktry wzi lew poprzedni.
                    </p>
                    <p>
                        Istnieje obowizek dorzucania do koloru i przebijania starsz kart w kolorze. W wypadku nieposiadania koloru wyjcia istnieje obowizek przebijania atutem.
                    </p>
                    <p>
                        Jeli ktry z graczy posiada krla atutowego, moe przed rozpoczciem rozgrywki ujawni ten fakt, zdobywa wwczas punkt. Nie ma obowizku zgaszania krla, lecz za krla nie zgoszonego przed rozpoczciem rozgrywki nie zdobywa si potem punktu.
                    </p>
                    <p>
                        Jeli przeciwnik rozdajcego wygra trzy lub cztery lewy, zdobywa punkt. Jeli ich nie wygra (trzy lub wicej lew zdoby rozdajcy), rozdajcy zdobywa dwa punkty.<br />
                        Jeli rozdajcy zadysponowa gr bez wymiany kart i zdoby co najmniej trzy lewy, wygrywa punkt. Jeli ich nie zdoby - przeciwnik rozdajcego wygrywa dwa punkty.<br />
                        Jeli choby raz wymieniono karty, ten z graczy, ktry wygrywa trzy lub cztery lewy, zdobywa punkt.<br />
                        Jeli ktrykolwiek z graczy zdobdzie wszystkie pi lew, zdobywa w ten sposb zawsze dwa punkty.
                    </p>
                    <p>
                        W jednym rozdaniu mona maksymalnie zdoby trzy punkty (dwa za gr i jeden za krla atutowego).
                    </p>
                    <p>
                        Przed rozpoczciem rozgrywki przeciwnik rozdajcego ma obowizek zoy deklaracj: "gram" lub "proponuj".
                    </p>
                    <p>
                        Jeli zoy deklaracj "gram", wychodzi do pierwszej lewy i toczy si rozgrywka. Jeli przeciwnik rozdajcego zoy deklaracj "proponuj", oznacza to propozycj wymiany kart. Na t propozycj rozdajcy moe zgodzi si (odpowiadajc: "prosz") lub nie zgodzi si (odpowiadajc: "prosz gra").
                    </p>
                    <p>
                        Jeli rozdajcy nie wyraa zgody na proponowan wymian kart, przeciwnik rozdajcego wychodzi do pierwszej lewy i toczy si rozgrywka.<br />
                        Jeli rozdajcy wyraa zgod na proponowan wymian kart, jego przeciwnik zrzuca zakryte z rki na st karty, ktre pragnie wymieni (wolno wymieni wszystkie karty), dobierajc zakryte karty ze stosu; nastpnie co najmniej jedn kart ma obowizek wymieni rwnie rozdajcy.
                    </p>
                    <p>
                        Po dokonaniu wymiany kart przeciwnik rozdajcego znw skada deklaracj "gram" lub "proponuj". W wypadku zgody rozdajcego wymiana moe nastpi po raz drugi, trzeci i dalsze. Wymiana kart toczy si a do chwili, gdy przeciwnik rozdajcego zadeklaruje "gram" lub te gdy rozdajcy zadeklaruje "prosz gra". Jeli w toku kolejnych wymian zabrako kart na stosie, karty zrzucone uprzednio tasuje si i tworzy z nich nowy stos.
                    </p>
                    <p>
                        Gra toczy si w kolejnych rozdaniach do chwili, gdy jeden z graczy zdobdzie pi punktw. Oznacza to wygran pen parti.
                    </p>
                </div>
            </div>
        );
    }
}



