import { basePath } from '../utils';
import React from 'react';
import garibaldkaImg from '../images/garibaldka.png';


export default class Garibaldka extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Garibaldka',
            desc: 'gra dla 2 osb',
            url: basePath() + '/garibaldka',
            players: [2],
            cards: 'Dwie standardowe talie kart (2 x 52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Tradycyjna gra karciana powszechnie znana i uprawiana w caej Europie. Na obszarze jzyka angielskiego nazywana jest "russian bank" ("bank rosyjski"), na obszarze jzyka francuskiego "crapette", na obszarze jzyka niemieckiego "Schikanieren" ("szykanowanie").
                    </div>

                    <h2 className="content-subhead">Gracze i karty</h2>
                    <p>
                        Gra dla dwch osb. Dwie pene talie kart (104 karty).<br />
                        Starszestwo kart od krla do asa.<br />
                        Nie ma koloru atutowego.<br />
                        Kady z grajcych gra wasn tali kart.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Kady z grajcych wykada przed sob ze swojej talii zakryty stos trzynastu kart, nazywany w toku dalszej gry magazynem. Wierzchni kart magazynu odsania si. Ten z grajcych, na ktrego magazynie ley starsza karta, rozpoczyna gr.
                    </p>
                    <p>
                        Przed rozpoczciem gry kady z grajcych wykada jeszcze ze swego talonu cztery karty, odkryte, pionowo w kolumnie. S to kolumny kart pomocniczych. Le one tak, i midzy nimi pozostaje wolne miejsce na zjawiajce si w toku gry karty bazowe - wszystkie asy.
                    </p>
                    <p>
                        Rozpoczynajcy gr odsania pierwsz kart swego zakrytego talonu i teraz:
                    </p>
                    <ul>
                        <li>
                            ma prawo do rodka ukadu pooy wszystkie asy spord pomocniczych kart i na nie ewentualnie pasujce dalsze karty w tym samym kolorze o oczko wysze (na asach stopniowo gromadz si karty rosnco do krli);
                        </li>
                        <li>
                            ma prawo na kad kart pomocnicz ka inn kart pomocnicz, lecz tylko o jedno oczko nisz na wysz w kolorach alternatywnych (czarna na czerwon i odwrotnie); w ten sposb na kartach pomocniczych powstaj odkryte stosy pomocnicze (wszystkie karty s widoczne), ktrych wierzchnie, ostatnie karty s wolne do manipulacji;
                        </li>
                        <li>
                            wolna do manipulacji jest odkryta karta wasnego magazynu - mona j pooy na kart bazow lub na ktry ze stosw pomocniczych; gdy zgra si kart z magazynu, odsania si z niego nastpn kart, ktra z kolei staje si wolna do manipulacji; gdy wyczerpi si karty magazynu, wolnego miejsca nie zapenia si niczym;
                        </li>
                        <li>
                            wolna do manipulacji jest pierwsza, odsonita karta wasnego talonu (wolno j podejrze) - i t mona pooy na kart bazow lub na ktry ze stosw pomocniczych; po zgraniu karty z zamknitego talonu gracz ma prawo podejrze i zgrywa z niego nastpn kart;
                        </li>
                        <li>
                            wszystkie karty wolne do manipulacji (ze stosw pomocniczych, wasnego magazynu i talonu) mona ka na wierzchni kart magazynu przeciwnika (utrudniajc mu w ten sposb gr), lecz tylko kart o jedno oczko wysz lub o jedno oczko nisz i w tym samym kolorze (na asa wolno ka tylko dwjk, nie krla);
                        </li>
                        <li>
                            gdy wyczerpie si ktry ze stosw pomocniczych, na miejsce wolne mona pooy dowoln spord kart wolnych do manipulacji i na niej nabudowa nowy stos pomocniczy;
                        </li>
                        <li>
                            karty z jednego stosu pomocniczego na drugi wolno przenosi tylko po jednej.
                        </li>
                    </ul>

                    <p>
                        Gracz, ktry rozpocz gr, gra tak dugo, dopki ma do wykonania jakiekolwiek kombinacje w ukadzie i dopki zgrywa kolejne karty swego zamknitego talonu. Gdy wyczerpa wszystkie kombinacje i odkrytej wierzchniej karty swego zamknitego talonu nigdzie zgra nie moe, kadzie j odkryt przed sob i koczy si jego tura gry. Odkryta karta talonu stanowi pocztek (doln kart) odkrytego talonu gracza - na niej bd w toku gry gromadziy si kolejne dalsze karty odkryte, ktrych gracz nie bdzie mg wczy do ukadu.
                    </p>
                    <p>
                        Do swojej tury gry przystpuje teraz drugi gracz, odkrywajc wierzchni kart swego zakrytego talonu i wykonujc wszystkie moliwe i korzystne dla niego przestawienia i kombinacje. aden z graczy w adnym momencie gry nie ma obowizku wykonywa adnych przestawie i kombinacji, jeli sobie tego nie yczy.
                    </p>
                    <p>
                        Kart raz zgranych na karty bazowe (do rodka ukadu) nie wolno wycofywa do gry.
                    </p>
                    <p>
                        Gracz grajcy jako drugi ma jedn moliwo wicej, ktrej nie mia rozpoczynajcy gr: moe kad kart spord wolnych do manipulacji dooy przeciwnikowi na wierzchni kart jego stosu otwartego, lecz tylko o jedno oczko nisz lub wysz w tym samym kolorze. Gdy z kolei gracz drugi nie moe ju kontynuowa kombinacji ani zgra wierzchniej karty swego talonu, kadzie j przed sob na stos otwarty i kolej gry przechodzi znw na gracza pierwszego.
                    </p>
                    <p>
                        Gra toczy si do chwili, gdy jeden z graczy pozbdzie si wszystkich kart wasnego magazynu i talonu zarwno odkrytego, jak i zamknitego. Oznacza to wygranie gry.
                    </p>
                    <p>
                        Jeli w toku gry wyczerpi si ktremu z graczy karty zamknitego talonu, odwraca karty talonu otwartego i nowo powstay talon zamknity zgrywa od pocztku.
                    </p>
                    <div className="example">
                        <div className="example content">
                            <p>
                                W sytuacji przedstawionej na poniszym rysunku jestemy na pocztku gry. Ujawniy si dotychczas tylko dwa asy bazowe, na jednym z nich ley ju jedna karta (dwjka). Z omiu stosw pomocniczych a sze jest jeszcze jednokartowych, tylko w dwch stosach le po dwie karty. U gracza dolnego wierzchni, odkryt kart magazynu jest D <span className="diamond"></span>, wierzchni kart odkrytego talonu 5 <span className="club"></span>, ktrej nie mona ju nigdzie zgra do ukadu.
                            </p>

                            <div className="center-outer-container">
                                <div className="center-inner-container">
                                    <img className="pure-img" src={garibaldkaImg}></img>
                                </div>
                            </div>

                            <p>
                                U gracza grnego wierzchni, odkryt kart magazynu jest 2 <span className="spade"></span>; odkrytego talonu jeszcze nie ma, gracz nie rozpocz gry.
                            </p>
                            <p>
                                Gracz dolny mgby wprawdzie 3 <span className="spade"></span>z ukadu umieci na 2 <span className="spade"></span>(magazynowej) u przeciwnika, lecz nic to nie daje, gdy gracz grny natychmiast przeprowadzi t manipulacj z powrotem. Nastpi wic koniec tury gracza dolnego, do gry przystpuje teraz gracz grny.
                            </p>
                            <p>
                                Jeli wierzchni kart jego talonu okae si A <span className="spade"></span> wejdzie on do rodka jako bazowy i na niego 2 <span className="spade"></span> z magazynu, odsaniajc jego kolejn kart. Jednoczenie gracz bdzie mg zgrywa nastpn kart talonu; by moe oka si jakie nowe moliwoci.
                            </p>
                            <p>
                                Jeli wierzchni kart talonu bdzie na przykd dowolna czerwona sidemka, pjdzie na 8 <span className="club"></span> na stos pomocniczy i na ni 6 <span className="spade"></span> z drugiego stosu pomocniczego. Zwolni si miejsce pomocnicze, powstanie moliwo rozadowania jeszcze jednej karty z magazynu (co jest najpilniejszym zadaniem w grze).
                            </p>
                        </div>
                    </div>

                    <h2 className="content-subhead">Wariant garibaldki</h2>
                    <p>
                        Mona umwi si, i istnieje dla kadego z graczy obowizek natychmiastowego dokadania na karty bazowe tych wszystkich kart z ukadu, ktre mona dooy.
                    </p>
                    <p>
                        Jeli gracz nie zauway takiej moliwoci i przeprowadza jak inn kombinacj przeoe, jego przeciwnik stuka w st - odbiera mu kolejk gry, sam dokada kart do bazowych i przystpuje do swojej tury gry.
                    </p>

                </div>
            </div >
        );
    }
}



