import { basePath } from '../utils';
import React from 'react';
import delfyImg from '../images/delfy.png';


export default class Eleusis extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Eleusis',
            desc: 'gra dla 2 lub wicej osb',
            url: basePath() + '/eleusis',
            players: [2, 3, 4, 5, 6, 7, 8],
            cards: 'Standardowa talia kart (52 karty) lub dwie talie'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info"> Gra logiczna opracowana w 1959r. przez Roberta Abbotta z Nowego Jorku.</div>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Ilo kart zalena od liczby uczestnikw:
                    </p>
                    <ul>
                        <li>dla 4 uczestnikw pena talia, 52 karty; z nich jedna dla rozdajcego i po 17 dla kadego z trzech grajcych</li>
                        <li>dla 5 uczestnikw 51 kart (z usuniciem 2 trefl); z nich jedna dla rozdajcego i po 10 dla kadego z czterech grajcych</li>
                        <li>dla 6 uczestnikw 49 kart (z usuniciem dwjek treflowej, karowej i kierowej); z nich jedna dla rozdajcego i po 8 dla kadego z piciu grajcych</li>
                        <li>dla 7 uczestnikw 99 kart (dwie talie, z usuniciem dwjek treflowych, karowych i jednej dwjki kierowej);</li>
                        <li>dla 8 uczestnikw 97 kart (dwie talie, z usuniciem wszystkich dwjek oprcz jednej dwjki pikowej);</li>
                    </ul>

                    <h2 className="content-subhead">Cel gry</h2>
                    <p>
                        Cel gry jest inny dla rozdajcego i inny dla reszty graczy:<br />
                        dla uczestnikw - pozby si wszystkich kart z rki i ze stou;<br />
                        dla rozdajcego - opracowa tak regu gry, ktra pozwoli jednemu z graczy wygra ze znaczn przewag nad innymi.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Po przetasowaniu rozdajcy rozdaje karty wszystkim uczestnikom gry, sobie zatrzymujc tylko jedn kart, tzw. kart startow. Nastpnie rozdajcy notuje na papierze, dla pniejszej weryfikacji, nie znan grajcym regu gry, czyli regu zrzucania kart w danym rozdaniu, i wykada swoj kart startow.
                    </p>

                    <h2 className="content-subhead">Regua zrzucania kart</h2>
                    <p>
                        Regua zrzucania kart moe by przez rozdajcego ustalona dowolnie, z pewnymi, nieznacznymi ograniczeniami. Oto przykadowe reguy zrzucania kart, od najatwiejszej do najtrudniejszych:
                    </p>
                    <ul>
                        <li>kad na kart czerwon kart czarn i odwrotnie;</li>
                        <li>kad na trefla zawsze karo, na karo - kiera, na kiera - pika, na pika - trefla;</li>
                        <li>kad na kart parzyst nieparzyst i odwrotnie;</li>
                        <li>na karty od asa do sidemki kad karty od semki do krla i odwrotnie;</li>
                        <li>na kart parzyst kad kart czerwon, na kart nieparzyst - czarn;</li>
                        <li>kad kart o jedn, dwa lub trzy miejsca starsz, zakadajc zamknity krg kart, a wic starszestwo W, D, K, A, 2, 3, 4;</li>
                        <li>jeli poprzedzajcy ci gracz zagra dobrze - kad kart czerwon, jeli zagra le - kad kart czarn;</li>
                        <li>podziel warto ostatniej karty przez cztery; jeli nie ma reszty - kad kart treflow; jeli reszta wynosi jeden - kad kart karow; jeli reszta wynosi dwa - kierow; jeli reszta wynosi trzy - pikow;</li>
                    </ul>

                    <p>
                        Regua zrzucania kart - co jest jedynym jej ograniczeniem - nie moe by zbyt wska, musi dawa grajcym co najmniej dziesi rnych moliwoci prawidowych zrzutek z penej talii.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Po wyoeniu na st karty startowej pierwszy z grajcych po lewej rce rozdajcego zrzuca dowolnie wybran ze swoich kart na kart startow. Rozdajcy wwczas ogasza, czy jest to zrzutka dobra, czy za, a wic zgodna czy nie z tajn, nie znan graczom regu gry w danym rozdaniu. Jeli zrzutka bya dobra, pozostaje na stosie na stole (stos jest otwarty, wszystkie w nim kolejne karty s widoczne dla grajcych); jeli jest za, zrzucajcy kart wycofuje j i otwart kadzie przed sob. Jest to tzw. "karta omykowa". Nastpnie zrzuca swoj kart drugi gracz i kolejno zrzucaj dalsi.
                    </p>
                    <p>
                        Dopki gracz ma karty w rku, nie wolno mu zrzuca kart omykowych. Naley pozbywa si ich po pozbyciu si wszystkich kart z rki.
                    </p>
                    <p>
                        Gdy wszyscy gracze pozbyli si kart z rki (cho wielu lub kady z nich ma przed sob jeszcze wasne karty omykowe), zapisuje si punkty uzyskane w danym rozdaniu przez rozdajcego wedug nastpujcych zasad:
                    </p>
                    <div className="example">
                        <div className="content example">
                            najmniejsz ilo kart omykowych przed jednym z graczy mnoy si przez liczb graczy mniejsz o jeden od iloci wszystkich uczestnikw gry (bez rozdajcego) i otrzyman liczb odejmuje si od sumy wszystkich kart omykowych na stole, bez kart tego z graczy, ktry ma kart omykowych najmniej.
                        </div>
                    </div>
                    <p>
                        Np. gra siedmiu uczestnikw; w momencie, gdy wszyscy pozbyli si kart z rki, maj przed sob nastpujce iloci kart omykowych: gracz A - 7, gracz B - 4, C - 3, D - 8, E - 2, F - 10. Gracz G (sidmy) by rozdajcym. Najrzadziej myli si gracz E; liczymy 2 (ilo jego kart omykowych) razy pi (ilo graczy bez rozdajcego minus jeden) rwna si 10. Suma iloci pozostaych kart omykowych: 7+4+3+8+10=32. Po odjciu rozdajcy otrzymuje wic 22 punkty.
                    </p>
                    <p>
                        Gra toczy si dalej - teraz gracze prbuj pozby si kart omykowych, zrzucajc kolejno po jednej wybranej z nich. Gra dobiega koca, gdy jeden z graczy pozbdzie si wszystkich swoich kart lub gdy rozdajcy stwierdzi, i zgodnie z regu nie mona ju zrzuci adnej z kart pozostaych na stole.
                    </p>
                    <p>
                        Jeli ktry z graczy pozbdzie si wszystkich kart, otrzymuje 6 punktw premii za skoczenie gry plus tyle punktw, ile wynosi suma kart omykowych, pozostaych jeszcze do zrzucenia reszcie graczy. Jeli gra skoczy si, gdy nie mona pozby si kart zgodnie z regu, punkty otrzymuje ten z graczy, ktremu pozostao najmniej kart omykowych (ilo punktw rwna si sumie kart przeciwnikw minus ilo kart wasnych), lecz bez szeciu punktw premii.
                    </p>
                    <p>
                        Pena tura Eleusis koczy si, gdy wszyscy uczestnicy gry byli raz rozdajcymi.<br />
                        Wygrywa ten, kto zdoby najwicej punktw.
                    </p>
                </div>

                <div>
                    <div className="content">
                        <a name="delfy"></a>
                        <h1 className="content-subhead main-header">Delfy</h1>
                        <div className="info">
                            Wariant eleusis opracowany w 1965 roku przez Martina D. Kruskala z Nowego Jorku.
                        </div>

                        <p>
                            Pena talia (52 karty), niezalenie od liczby graczy.
                        </p>
                        <p>
                            Pierwszego rozdajcego, zwanego "wyroczni", ustala si przez losowanie.
                        </p>
                        <p>
                            Rozdajcy ustala i notuje tajn, nie znan pozostaym graczom, regu zrzucania kart; nastpnie tasuje tali kart i odsania pierwsz kart, startow.
                        </p>
                        <p>
                            Kady z graczy dysponuje specjalnym etonem (moe to by dwustronnie zapisana kartka papieru) z napisami: "Prawda", "Nieprawda" lub "Gwna linia", "Boczna linia".
                        </p>
                        <p>
                            Rozdajcy odsania kolejn, drug kart z talii. Po jej odsoniciu kady z grajcych ustawia swj eton w sposb zgodny z wasnym przekonaniem co do odsonitej karty ("Prawda", "Nieprawda" lub "Gwna linia", "Boczna linia").
                        </p>
                        <p>
                            Rozdajcy ukada kart na stole - jeli jest ona zgodna z regu zrzucania kart w danej rozgrywce, ukada j w gwnej linii, w rzdzie poziomym; jeli nie jest zgodna z regu gry, w bocznej linii, pionowo.<br />
                            Np. na poniszym rysunku karta startowa S oraz karty 3, 4, 6, 7, 8, 12, 14, 15, 16, 18, 19 i 20 zostay wyoone zgodnie z regu; karty 1, 2, 5, 9, 10, 11, 13, 17 i 20 niezgodnie z regu gry.
                        </p>
                        <div className="center-outer-container">
                            <div className="center-inner-container">
                                <img className="pure-img" src={delfyImg}></img>
                            </div>
                        </div>
                        <p className="center">
                            Jeli gracz przed wyoeniem danej karty odgad jej walor (suszno lub niesuszno z regu gry), otrzymuje od rozdajcego jeden eton; jeli gracz pomyli si, nie odgad jakoci karty, traci wszystkie etony jakie posiada.
                        </p>
                        <p>
                            Gra toczy si do chwili wyoenia ostatniej karty. W kocowym obliczeniu poszczeglni gracze wygrywaj lub przegrywaj do siebie ilo punktw zgodn z iloci posiadanych etonw. Np. w momencie zakoczenia gry gracz A posiada 18 etonw, gracz B - 15, gracz C - 2 etony, gracz D by rozdajcym (wyroczni).<br />
                            Gracz A wygrywa od gracza B 3 punkty oraz od gracza C 16 punktw. Gracz B wygrywa od gracza C 13 punktw. Gracz D (wyrocznia) zapisuje sobie na plus wszystkie punkty wygrane, tj. wygrywa cznie 3 + 16 + 13, a wic 32 punkty. W kocowych obliczeniach gracz A ma na koncie 19pkt, gracz B - 7pkt, gracz C - 58pkt ujemnych, za gracz D - 32 punkty.
                        </p>
                        <p>
                            Pena tura w delfy dobiega koca, gdy kady z graczy kolejno by rozdajcym (wyroczni).
                        </p>
                    </div>
                </div>
            </div>
        );
    }
}



