import { basePath } from '../utils';
import React from 'react';


export default class Pan extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Pan',
            desc: 'gra dla 2 - 4 osb',
            url: basePath() + '/pan',
            players: [2, 3, 4],
            cards: 'Maa talia kart (24 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>
                        Gra dla 2 - 4 osb.
                    </p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Do gry uywa si 24 kart (od asa do 9).<br />
                        Kolory nie maj znaczenia.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pierwszy rozdajcy jest wybierany losowo, pniej s kolejni, zgodnie z ruchem wskazwek zegara.
                    </p>
                    <p>
                        Wszystkie karty zostaj rozdane graczom. Pierwszy wychodzi gracz posiadajcy na rku 9 <span className="heart"></span> i otwiera w ten sposb stos. Kolejni gracze mog wyrzuci kart rwn lub wysz od ostatnio rzuconej (jeli gracz pierwszy rzuci 9 <span className="heart"></span> , nastpny inn 9, a trzeci gracz 10, to gracz kolejny moe wyrzuci 10 lub kart wysz, lecz nie 9). Jeli gracz, ktrego jest kolej nie moe (lub nie chce) wyrzuci adnej karty pobiera trzy karty ze stosu. Jeli na stosie znajduje si mniej ni trzy karty do pobrania (nie liczc 9 <span className="heart"></span> ) pobiera je wszystkie, pozostawiajc tylko 9 <span className="heart"></span> .
                    </p>
                    <p>
                        Jeli gracz rozpoczynajcy posiada wszystkie cztery 9 moe je od razu zrzuci (jeli jakiej mu brakuje moe zrzuci tylko 9 <span className="heart"></span> ).
                    </p>
                    <p>
                        Jeli ktry z graczy ma wszystkie cztery karty danej wartoci, moe je wszystkie zrzuci od razu, gdy przyjdzie jego kolej (oczywicie, pod warunkiem, e zrzucane karty s wyszej wartoci od karty ostatnio pooonej na stos).
                    </p>
                    <p>
                        Celem gry jest pozbycie si wszystkich kart z rki. Gracz, ktry jako jedyny pozostanie z kartami na rku przegrywa rozdanie i "otrzymuje" literk P. Jeli przegra ktre z nastpnych rozda - otrzymuje literk A, a potem N. Gra toczy si do momentu, a ktry z graczy przegra 3 rozdania, otrzymujc przydomek PAN (lub inny trzyliterowy, najczciej obraliwy).
                    </p>
                    <p>
                        Jeli przedostatni gracz zrzuci swoj ostatni kart (lub karty) i gracz ostatni w jednym pozbywa si wszystkich swoich kart - gra jest nierozstrzygnita.
                    </p>
                </div>
            </div>
        );
    }
}



