import { basePath } from '../utils';
import React from 'react';


export default class IdzNaRyby extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Id na ryby!',
            desc: 'gra dla 3 - 6 [2] osb',
            url: basePath() + '/idznaryby',
            players: [2, 3, 4, 5, 6],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <p>
                        Celem gry jest zbieranie kompletw skadajcych si z czterech kart o tych samych figurach.
                    </p>
                    <p>
                        Najlepiej gra w 3 - 6 osb, ale jest moliwo gry we dwjk.<br />
                        Uywana jest talia 52 kart.<br />
                        Rozdajcy rozdaje po 5 kart graczom (7 przy grze we 2 osoby). Reszt kart odwracamy figurami na d i odkadamy na stosie. Gracz na lewo od rozdajcego jest rozgrywajcym pierwsz kolejk.
                    </p>
                    <p>
                        Kolejka rozpoczyna si pytaniem ktrego z graczy o dan figur. Np. jeli jest to moja kolejka mog powiedzie: "Stefan, daj mi twojego Waleta". Pytajcy musi posiada co najmniej jedn kart z figur o ktr pyta. Pytany gracz musi odda wszystkie karty jakie posiada z dan figur (tzn., e Stefan musiaby odda wszystkie Walety jakie miaby na rku) graczowi, ktry o ni pyta. Rozgrywajcy bierze wwczas karty i w nastpnej kolejce nadal jest rozgrywajcym, pytajc ktrego z graczy o figur.
                    </p>
                    <p>
                        Jeeli osoba pytana nie ma danej figury, to mwi: "Id na ryby!". Rozgrywajcy cignie wtedy kart ("owi ryb") z gry stosu i jeli wycignita karta jest figur o ktra pyta pokazuje j wszystkim i w dalszym cigu zostaje rozgrywajcym. Jeli wycignita karta nie jest figur o ktr pyta, zatrzymuje j a kolejka przechodzi na gracza, ktry powiedzia: "Id na ryby!".
                    </p>
                    <p>
                        Gdy gracz uzbiera komplet czterech figur, powinien je pokaza innym i odoy zakryte na bok.
                    </p>
                    <p>
                        Gra jest kontynuowana do momentu, a ktry z graczy pozostanie bez kart na rku lub skoczy si stos.<br />
                        Zwycizc jest gracz, ktry uzbiera najwicej kompletw z czterech figur.
                    </p>

                    <h2 className="content-subhead">Odmiany Id na ryby</h2>
                    <p>
                        Mona gra tak, i zamiast pytania o figur zadaje si pytania o konkretn kart. Pytajcy musi ju posiada co najmniej jedn kart z figur o ktr pyta. Pozostae zasady s identyczne.
                    </p>
                    <p>
                        Jeli grasz w t odmian, musisz uzgodni z pozostaymi graczami, czy dopuszczalne jest pytanie o kart, ktr si ju ma na rku. Oczywicie "masz ryb" i twoja kolejka pewnie si koczy, ale robisz to, eby zmyli innych graczy aby myleli, e nie masz tej karty.
                    </p>
                    <p>
                        Mona gra tak, e gdy stos si skoczy, trzyma si gr a wszystkie karty, ktre maj zostan uoone w komplet. Oczywici, gdy stos si skoczy nie ma ju "Id na ryby!". Gdy osoba pytana nie ma danej karty, kolejka si koczy od razu.
                    </p>
                    <p>
                        S rwnie odmiany punktacji. Np. moesz zagra seri z rki, zdobywajc jeden punkt za kady komplet. Gra jest kontynuowana do momentu a ko zwyciy przez osignicie uzgodnionej liczby punktw, np. 10pkt.
                    </p>
                </div>
            </div>
        );
    }
}



