import { basePath } from '../utils';
import React from 'react';


export default class Tysiac extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Tysic',
            desc: 'gra dla 2 [3 i 4] osb',
            url: basePath() + '/tysiac',
            players: [2, 3, 4],
            cards: 'Maa talia kart (24 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">Jeden z licznych wariantw francuskiego mariasza, szczeglnie rozpowszechniony i popularny w Polsce.</div>
                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 2 [3 i 4] osb.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Maa talia kart (24 karty). <br />
                        Starszestwo kart: as, dziesitka, krl, dama, walet, dziewitka. <br />
                        Warto punktowa: as - 11, dziesitka - 10, krl - 4, dama - 3, walet - 2, dziewitka - 0. <br />
                        Istnieje zmienny kolor atutowy.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie. Gracze w kolejnych rozgrywkach rozdaj karty na zmian.
                        Kady z graczy otrzymuje po sze kart, reszta kart pozostaje w zakrytym stosie.
                    </p>
                    <p>
                        Gr rozpoczyna przeciwnik rozdajcego, wychodzc kart z rki. W rozgrywce istnieje obowizek dorzucania do koloru i przebijania atutem w przypadku nieposiadania koloru wyjcia. Nie ma obowizku przebijania starz kart.
                    </p>
                    <p>
                        Po wziciu lewy pierwszy uzupenia karty w rku znowu do szeciu kart biorc kart ze stosu zakrytego ten z graczy, ktry wzi lew. On take wychodzi do nastpnej lewy.
                    </p>
                    <p>
                        W momencie wyjcia mona zgasza posiadanie meldunku (pary krla i damy w tym samym kolorze).
                        Meldunek premiowany jest w sposb zaleny od jego koloru: <br />
                        meldunek kierowy 100pkt <br />
                        meldunek karowy 80pkt <br />
                        meldunek treflowy 60pkt <br />
                        meldunek pikowy 40pkt <br />
                    </p>
                    <p>
                        Po zgoszeniu meldunku zgaszajcy go ma obowizek wyj jedn z kart meldunku (w krla lub w dam).<br />
                        W momencie wyjcia kolor meldunku staje si kolorem atutowym a do koca rozgrywki lub do chwili zgoszenia nastpnego meldunku, ktry z kolei staje si kolorem atutowym.
                    </p>

                    <h2 className="content-subhead">Zakoczenie</h2>
                    <p>
                        Rozgrywk prowadzi si do zgrania wszystkich kart ze stosu zakrytego i z rki. Po ukoczeniu rozgrywki podlicza si punkty kadego gracza, zdobyte w danej rozgrywce. Na te punkty skadaj si premie za zgoszone meldunki oraz punkty za wszystkie karty zdobyte przez danego gracza w jego lewach, obliczone wg wartoci punktowej kart.
                    </p>
                    <p>
                        Gracz, ktry pierwszy osignie lub przekroczy w zapisie w kolejnej rozgrywce 1000pkt, wygrywa gr.
                    </p>
                </div>
                <div className="content">
                    <a name="licytowany"></a>
                    <h1 className="content-subhead main-header">Tysic licytowany</h1>
                    <p>
                        Wariant gry.<br />
                        Kady z graczy otrzymuje po dziesi kart, pozostae cztery karty le na stole w dwu zakrytych talonach po dwie karty w kadym z nich.
                    </p>
                    <p>
                        Pierwszym etapem rozdania jest licytacja. W licytacji, ktr rozpoczyna przeciwnik rozdajcego, gracze kolejno deklaruj ilo punktw, ktre zobowizuj si zdoby w rozgrywce. Przeciwnik rozdajcego pierwszy ma obowizek zgosi w licytacji 100 punktw, choby nie mia szans ich zdobycia w rozgrywce (jest "na musiku" - musi licytowa).
                    </p>
                    <p>
                        Wygrywa licytacj ten z graczy, ktry zgosi najwysz ilo punktw. Wygrywajcy licytacj podejmuje ze stou jeden wybrany przez siebie talon, okazuje karty w nim przeciwnikowi, wcza karty z talonu do kart na rku i nastpnie dwie dowolnie wybrane karty z rki umieszcza znowu na stole w zakrytym talonie.<br />
                        Wygrywajcy licytacj moe, jeli chce, po podjciu talonu, podwyszy swoj ostatni zapowied licytacyjn, zgaszajc do rozgrywki jeszcze wiksz ilo punktw.
                    </p>
                    <p>
                        Jeli rozgrywajcy zdoby zadeklarowan lub wiksz ilo punktw, to t zadeklarowan liczb mu si zapisuje. Jeli ich nie zdoby odpisuje mu si zadeklarowan liczb punktw jako minusowe.<br />
                        Przeciwnikowi rozgrywajcego zapisuje si ilo punktw faktycznie przez niego zdobyt w lewach i zgoszonych meldunkach.
                    </p>
                    <p>
                        W rozgrywce i w obliczeniach bierze udzia 20 kart obydwu rk, bez czterech kart z obydwu talonw, z jednym wyjtkiem: jeli wygrywajcy licytacj zadeklarowa 120 punktw, w trakcie rozgrywki nie zgosi za adnego meldunku, lecz wzi wszystkie lewy, wwczas i warto obydwu talonw dolicza si do jego zdobyczy (suma punktw wszystkich kart w talii jest rwna 120)
                    </p>
                </div>
                <div className="content">
                    <h1 className="content-subhead main-header">Tysic licytowany dla trzech osb</h1>
                    <p>
                        Wariant gry.<br />
                        Kady z trzech graczy otrzymuje po siedem kart, trzy karty pozostaj na stole w zakrytym talonie.
                    </p>
                    <p>
                        Zasady licytacji bez zmian.
                    </p>
                    <p>
                        Wygrywajcy licytacj podejmuje karty z talonu, pokazuje przeciwnikom i umieszcza je na rku. Nastpnie dwie dowolne karty z rki przekazuje - po jednej, zakryte - kademu z przeciwnikw. <br />
                        W rozgrywce bior udzia wszystkie karty.
                    </p>
                    <p>
                        Zasady rozgrywki i zapisu bez zmian.
                    </p>
                </div>
                <div className="content">
                    <h1 className="content-subhead main-header">Tysic licytowany dla czterech osb</h1>
                    <p>
                        Zasady identyczne jak w grze dla trzech osb z tym, e trzy karty ktre byy w talonie zakrytym otrzymuje pierwszy gracz na lewo od rozdajcego. Jeli w tych trzech kartach znajduje si meldunek otrzymuje on premi rwn wartoci tego meldunku, oraz za kadego asa po 50 punktw. <br />
                        Osob "na musiku" jest drugi gracz na lewo od rozdajcego. <br />
                        Zasady rozgrywki i zapisu bez zmian.
                    </p>
                    <h2 className="content-subhead">Wariant tysica licytowanego</h2>
                    <ul>
                        <li>
                            Mona wprowadzi wariant licytacji "w ciemno". W tym wariancie gracz moe, jeli chce, bra udzia w licytacji nie ogldajc wasnych kart. Gracz ma prawo obejrze wasne karty w kadym momencie licytacji - wwczas licytacja przestaje by licytacj "w ciemno". <br />
                            Jeli gracz wygra licytacj w ciemno, nie okazuje kart talonu wspgrajcym, zapis za za rozgrywk, zarwno przy wygranej, jak i przegranej zostaje podwojony dla wszystkich graczy.
                        </li>
                        <li>
                            Po przekroczeniu przez ktrego z graczy 800 punktw nastpne punkty dodaje mu si tylko w przypadku wygrania przez niego licytacji i ugrania zadeklarowanej liczby punktw.
                        </li>
                        <li>
                            Gracz, ktry wygra licytacj i uwaa, i nie uda mu si ugra zadeklarowanej liczby punktw, moe przed rozpoczciem rozgrywki przerwa gr (dajc tzw. "bomb"). Moe w cigu gry zrobi tak dowoln liczb razy, lecz tylko za pierwszym razem nie odpisuje mu si zadeklarowanej liczby punktw. Reszta graczy w takim przypadku liczb wszystkich punktw w kartach (tzn. 120pkt) dziel midzy siebie (3 graczy - po 60pkt kady; 4 graczy - po 40pkt kady); jedynie przy dwch graczach ten drugi otrzymuje 60pkt. Mona oczywicie zapisywa po 60pkt bez wzgldu na liczb graczy.
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}



