import { basePath } from '../utils';
import React from 'react';


export default class Hasenpfeffer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Hasenpfeffer',
            desc: 'gra dla 4 osb',
            url: basePath() + '/hasenpfeffer',
            players: [4],
            cards: 'Maa talia kart (24 karty) + doker'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Gra pochodzenia niemieckiego, rozpowszechniona w caej Europie.
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>
                        Gra dla 4 osb. Dwaj gracze graj przeciwko dwom. Pary tworzone s z graczy siedzcych na przeciw siebie.
                    </p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Maa talia kart (24 karty) oraz doker - razem 25 kart.<br />
                        Starszestwo kart od asa do dziewitki - we wszystkich kolorach nieatutowych<br />
                        W zmiennym kolorze atutowym starszestwo jest inne: doker, walet, as, krl, dama, dziesitka, dziewitka.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie.<br />
                        Kady z graczy otrzymuje po sze kart, dwudziesta pita karta pozostaje zakryta na stole.
                    </p>

                    <h2 className="content-subhead">Licytacja</h2>
                    <p>
                        Pierwszym etapem gry jest licytacja, ktr rozpoczyna pierwszy gracz siedzcy na lewo od rozdajcego. Podczas licytacji deklaruje si ilo lew, jak dany gracz zobowizuje si wzi razem ze swoim partnerem w rozgrywce. Licytacj mona rozpocz ju od zadeklarowania jednej lewy. Gracze licytuj kolejno, dopki trzech z nich nie spasuje, czwarty za - tym samym - nie wygra licytacji najwysz zapowiedzi.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Po zakoczeniu licytacji gracz, ktry j wygra, ujawnia wybrany przez siebie kolor atutowy w danej rozgrywce. On take wychodzi do pierwszej lewy.<br />
                        Po pierwszym wyjciu odsania si do wiadomoci wszystkich graczy dwudziest pit kart, dotd zakryt, ktra nie bierze udziau w rozgrywce.
                    </p>
                    <p>
                        Obowizuje dorzucanie do koloru. Nie ma obowizku przebijania przy braku kart w kolorze wyjcia.<br />
                        Nie ma obowizku bicia starsz kart.
                    </p>
                    <p>
                        Gra toczy si do chwili, gdy jedna ze stron osignie w zapisie umwion z gry ilo punktw plusowych (zazwyczaj 10 lub 20).
                    </p>

                    <h2 className="content-subhead">Punktacja</h2>
                    <p>
                        Za wzicie w rozgrywce zadeklarowanej lub wikszej iloci lew partnerzy otrzymuj tyle punktw, ile lew wzili.<br />
                        Za wzicie mniejszej iloci lew od zadeklarowanej partnerzy otrzymuj tyle punktw minusowych, ile lew zabrako im do wypenienia kontraktu.
                    </p>
                </div>
            </div>
        );
    }
}




