import { basePath } from '../utils';
import React from 'react';


export default class Wyscig extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Wycig',
            url: basePath() + '/wyscig',
            players: [2, 3, 4, 5, 6, 7, 8],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia kart (52 karty). Przy wikszej liczbie graczy mona uy dwch talii.<br />
                        Starszestwo kart od Asa do Krla.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pierwszy rozdajcy wybierany jest losowo. Po przetasowaniu rozdaje wszystkie karty midzy graczy (moe si zdarzy, e niektrzy gracze bd mieli o jedn kart wicej).
                    </p>
                    <p>
                        Jako pierwszy wychodzi gracz, ktry ma A <span className="spade"></span>, wykadajc go na st. As ten jest pierwsz kart bazow, na ktr wykadane bd kolejne karty w tym samym kolorze wedug ich starszestwa.<br />
                        Reszta kart bazowych bdzie wyoona w trakcie gry.<br />
                        Po wyoeniu A <span className="spade"></span> kolejka przechodzi na nastpnych graczy wedug ruchu wskazwek zegara. Gracze maj teraz do wyboru:
                    </p>
                    <ul>
                        <li>
                            wyoy kolejn kart bazow (nastpnego Asa) jeli tak posiadaj;
                        </li>
                        <li>
                            dooy na jedn z wierzchnich kart kart tego samego koloru o jedno oczko wysz;
                        </li>
                        <li>
                            powiedzie PASS i przekaza tym samym kolejk na nastpnego gracza.
                        </li>
                    </ul>

                    <p>
                        Karty ukadane s na sobie w ten sposb, aby te na spodzie byy lekko odkryte.
                    </p>

                    <h2 className="content-subhead">Zakoczenie</h2>
                    <p>
                        Gr mona zakoczy na dwa warianty (ustalajc wczeniej jeden z nich):
                    </p>
                    <ul>
                        <li>
                            Wygrywa ten z graczy, ktry pierwszy pozbdzie si kart;
                        </li>
                        <li>
                            Przegrywa ten z graczy, ktry pozostanie choby z jedn kart (nawet jeli moe j gdzie jeszcze dooy), gdy wszyscy inni gracze si ich pozbd.
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}



