import { basePath } from '../utils';
import React from 'react';


export default class Fantan extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Fantan',
            name2: 'Loteryjka',
            desc: 'gra dla 4 [3] osb',
            url: basePath() + '/fantan',
            players: [3, 4],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Tradycyjna gra, nazywana take dominem karcianym, loteryjk i gr w parlament; znana i uprawiana w caej Europie.<br />
                        Fantan by czci obowizujcej rozgrywki przy grze w <a className="pure-menu-link underline" href="/kierki">kierki</a>
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 4 [3] osb.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia kart (52 karty).<br />
                        Starszestwo od asa do dwjki.<br />
                        Nie ma koloru atutowego.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie. W kolejnych rozgrywkach gracze rozdaj karty kolejno. Gr rozpoczyna zawsze gracz na lewo od rozdajcego.<br />
                        Kady z graczy otrzymuje po 13 kart.
                    </p>
                    <p>
                        Rozpoczynajcy gr ma obowizek wyoy na st ktr z posiadanych semek. Jeli nie ma semki, obowizek rozpoczcia gry przechodzi na nastpnego gracza. Po wyoeniu pierwszej semki gracze kolejno, w kierunku ruchu wskazwek zegara, wykadaj po jednej karcie z rki na st. Wolno do lecej na stole semki dooy sidemk lub dziewitk w tym samym kolorze, a do nich z kolei dziesitk lub szstk, rozbudowujc w ten sposb kolor w obydwie strony.<br />
                        Wolno w kadej chwili, w momencie swojej kolejki gry, wyoy posiadan na rku inn semk i w dalszej grze dokada karty do tej semki.
                    </p>
                    <p>
                        Gracz, na ktrego przychodzi kolej gry, sam decyduje, ktre z istniejcych dla niego moliwoci wyoenia kolejnej karty starszej lub modszej o jedno oczko od karty lecej na stole wykorzysta. Nie wolno tylko nie dooy karty do ukadu na stole, jeli dajc si dooy kart ma si na rku i przychodzi kolejka gry.
                    </p>
                    <p>
                        Jeli gracz nie moe dooy adnej z kart na st, traci kolejk, ktra przechodzi na nastpnego gracza.
                    </p>
                    <p>
                        Wygrywa ten z graczy, ktry pierwszy pozbdzie si wszystkich kart z rki.
                    </p>

                    <h2 className="content-subhead">Warianty</h2>
                    <p>
                        Mona gra w fantana umawiajc si uprzednio, i gry nie rozpoczyna si od semki, lecz od karty dowolnej - takiej, jak wyoy rozpoczynajcy gr. Wwczas jednak wszystkie kolory naley rozpoczyna od wyoenia karty tej samej wysokoci.
                    </p>
                    <p>
                        Mona gra w fantana umawiajc si, i gracz, ktry w swojej kolejce gry wykada na st asa, ma prawo natychmiast (w tej samej kolejce gry) wyoy jeszcze jedn kart (chocia nie ma obowizku).
                    </p>
                    <p>
                        Mona gra tak, i przywilej dooenia natychmiast jeszcze jednej karty do ukadu przysuguje nie tylko graczowi, ktry wyoy asa, lecz take temu, ktry wyoy dwjk (a wic premiowane jest wyoenie najstarszej i najmodszej karty w kadym z kolorw).
                    </p>
                    <p>
                        Fantan bywa rwnie czci obowizujcej rozgrywki przy grze w <a className="pure-menu-link underline" href="/kierki">kierki</a>. Wwczas tradycyjnie gra si nie do pierwszego wygrywajcego, lecz do chwili, gdy pozbd si kart dwaj gracze (pierwszy z nich otrzymuje tzw. pierwsz nagrod, drugi - drug).
                    </p>
                </div>
            </div>
        );
    }
}



