import { basePath } from '../utils';
import React from 'react';


export default class Skat extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Skat',
            url: basePath() + '/skat',
            players: [3],
            cards: 'Skatowa talia kart (32 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Gra szczeglnie popularna w Niemczech, ale znana take na caym wiecie; u nas grywa si gwnie na lsku.<br />
                        Skat powsta w ierwszych latach dziewitnastego wieku z udanego poczenia regu kilku innych, starszych gier (taroka, trappoli, lombra, kopy, solo) oraz nowych zasad. Pierwsze zasady gry ustali notariusz i adwokat Fryderyk Ferdynand Hempel z Altenburga. Carl Neefe w roku 1817 wprowadzi typowy dla skata i unikalny w grach karcianych rachunek matadorw (szczytw). Jednolite reguy gry ustalone zostay na pierwszym Kongresie Skatowym w sierpniu 1886r. w Altenburgu, nazywanym odtd "miastem skata".<br />
                        Przepisy gry zamieszczone poniej, oparte s na "Kodeksie skatowym" uchwalonym i uzupenianym na kolejnych kongresach skatowych w Altenburgu w latach 1927, 1932, 1937 oraz na orzeczeniach tak zwanego "Sdu Skatowego", ktry rozstrzyga sprawy sporne midzy graczami, nadsyane z caego wiata, i dziaa od listopada 1963 roku, oczywicie w Altenburgu."
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>
                        Gra dla 3 osb. W grze moe bra udzia czwarta osoba, lecz wtedy w kolejnych rozdaniach rozdajcy pauzuje (nie bierze udziau w grze).
                    </p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Talia kart tzw. skatowa (32 kary, od asa do sidemki).<br />
                        Starszestwo kart: as, dziesitka, krl, dama, dziewitka, semka, sidemka.<br />
                        Walet peni rol specyficzn, s zawsze (poza rozgrywkami zerowymi) atutami.<br />
                        Starszestwo kolorw: trefle, piki, kiery, karo.<br />
                        Warto obliczeniowa kolorw: trefle - 12pkt, piki - 11pkt, kiery - 10pkt, karo - 9pkt.
                    </p>
                    <p>
                        Przy grze w skata poszczeglne karty i kolory kart nosz rne nazwy, tradycyjnie zwizane z t gr. I tak:<br />
                        trefle to inaczej od, krzye, krajce;<br />
                        piki to inaczej wino, ziele, licie, grin;<br />
                        kiery to inaczej czerwie, serce, herc;<br />
                        karo to inaczej dzwonki, szele.<br />
                        As to inaczej tuz, dama - wynik lub ober. Wreszcie walet to inaczej ninik, unter, chopiec, bubek lub dupek.
                    </p>
                    <p>
                        Warto punktowa kart: as - 11, dziesitka - 10, krl - 4, dama - 3, walet - 2.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Kade rozdanie skada si z dwch etapw: licytacji (rajcowania, zapowiedzi) i rozgrywki.
                    </p>
                    <p>
                        Celem gry jest osignicie w kolejnych wygranych rozdaniach maksymalnej iloci punktw obliczeniowych.
                    </p>
                    <p>
                        Rozdanie jest wygrane przez gracza, jeli w jego lewach (wzitkach) znajduje si co najmniej 61 punktw w poszczeglnych kartach cznie (wyjtkiem s rozgrywki zerowe).
                    </p>
                    <p>
                        Kady z grajcych otrzymuje po 10 kart; na stole pozostaj zakryte dwie karty (zwane talonem, tajlongiem, renonsem lub skatem). Karty rozdaje si w nastpujcy sposb: kademu graczowi po 3, 2 do talonu, kademu graczowi po 4 i kademu graczowi po 3.
                    </p>
                    <p>
                        Gracz po lewej rce rozdajcego jest graczem na przodku (przodkiem), drugi - graczem na rodku (rodkiem), sam rozdajcy - graczem na zadku (zadkiem).
                    </p>
                    <p>
                        Licytacj rozpoczyna zawsze rodek, licytujc si z przodkiem; gdy jeden z tych graczy spasuje, do licytacji z pozostaym wcza si zadek. Oczywicie powinien zalicytowa wicej ni pooprzednik lub spasowa. Gdy przodek i rodek od razu spasuj, zadek moe zapowiedzie gr na najniszym poziomie. Gdy wszyscy trzej gracze spasuj, tasuje si karty i rozdaje powtrnie (rozdaje kolejny gracz do rozdania).
                    </p>
                    <p>
                        Wygrywa licytacj (zostaje solist, rozgrywa sam przeciwko dwu pozostaym) ten z graczy, ktry zapowie najwysz ilo punktw obliczeniowych.
                    </p>
                    <p>
                        Najnisza moliwa do uzyskania ilo punktw obliczeniowych w jednym rozdaniu to 18, najwysza - 360.
                    </p>
                    <p>
                        W skacie istniej dwie klasy i trzy rodzaje rozgrywek.
                    </p>
                    <p>
                        Pierwsza klasa to rozgrywki "na widzianego" (z podjciem talonu). Gracz, ktry decyduje si na t rozgrywk, ma prawo podj talon, dwie karty w talonie doczy do swoich dziesiciu kart w rku i nastpnie dowolnie wybrane dwie karty z rki odoy znw zakryte do talonu. Karty te nie bior udziau w rozgrywce, lecz zawarte w nich punkty licz si dla solisty.
                    </p>
                    <p>
                        Druga klasa to rozgrywki z rki ("na niewidzianego", bez podjcia talonu). Gracz, ktry decyduje si na t rozgrywk, nie podejmuje ani te nie oglda talonu. Karty w talonie nie bior udziau w rozgrywce, lecz zawarte w nim punkty licz si take dla solisty. Przy grze z rki rozgrywajcy uzyskuje jeden dodatkowy wspczynnik do obliczenia wartoci gry.
                    </p>
                    <p>
                        Niezalenie od tego, kto wygra licytacj i jak zapowiedzia gr, pierwszym wychodzcym w rozgrywce jest zawsze przodek.<br />
                        Obowizuje dorzucanie do koloru, gdy koloru brak na rku, nie ma obowizku przebijania, mona zrzuci dowoln kart.
                    </p>
                    <p>
                        W kadej klasie rozgrywek skatowych istniej trzy rodzaje rozgrywek: kolorowe, zerwki (null) i waletwki (grand).
                    </p>
                    <p>
                        W rozgrywkach kolorowych istnieje jedenacie kart atutowych (cztery walety oraz siedem kart koloru). W waletwkach istniej tylko cztery atuty (walety), wszystkie pozostae karty s rwnowane. W zerwkach nie ma w ogle koloru ani kart atutowych. Zmienia si take starszestwo kart: as, krl, dama, walet, dziesitka, dziewitka, semka, sidemka.
                    </p>
                    <p>
                        Warto gry kolorowej oblicza si, uwzgldniajc zasadnicz warto koloru atutowego oraz ilo posiadanych lub nieposiadanych waletw i kart koloru atutowego w nieprzerwanym szeregu hierarchicznym (tzw. szczytw lub matadorw).
                    </p>
                    <p>
                        Jeli gracz ma na rku waleta treflowego, gra zawsze "z" (jednym szczytem lub z wiksz ich iloci). Jeli gracz nie ma na rku waleta treflowego, gra zawsze "bez" (jednego szczytu lub bez wikszej ich iloci).
                    </p>
                    <p>
                        Np. gracz ma na rku waleta treflowego, waleta kierowego, waleta karowego oraz asa, dziesitk, krla i semk kier - gra kiery "z jednym" (szereg atutw jest przerwany brakiem waleta pik; gracz ma w rku tylko jedn kart szczytow).
                    </p>
                    <p>
                        Gdy gracz ma na rku wszystkie cztery walety, asa kier, krla kier i dam kier - gra kiery "z picioma" (ma pi kart szczytowych; szereg atutw przerwany jest dopiero brakiem dziesitki kier).
                    </p>
                    <p>
                        Gdy gracz ma na rku waleta pik, waleta kier, waleta karo, asa kier, dziesitk kier i krla kier - gra kiery "bez jednego" (brak pierwszej kart szczytowej, najwyszej karty atutowej, waleta trefl, czyli dupka odnego, zwanego "starym").
                    </p>
                    <p>
                        Gdy gracz ma na rku dziesitk kier, krla kier, dam kier i semk kier - gra kiery "bez piciu" (brak piciu kart szczytowych: czterech waletw i asa kier).
                    </p>
                    <p>
                        Oczywicie identycznie jest we wszystkich innych kolorach atutowych; walety s przecie zawsze atutami.
                    </p>
                    <p>
                        Za wygran gr kolorow "na widzianego" (uzyskanie w rozgrywce powyej 61 punktw w zdobytych lewach) gracz otrzymuje t ilo punktw obliczeniowych, ktra powstaje z pomnoenia sumy wspczynnikw za gr i posiadane lub nie posiadane szczyty przez warto obliczeniow koloru rozgrywki. Za wygran gr gracz otrzymuje jeden wspczynnik.
                    </p>
                    <p>
                        Na przykad:<br />
                        kiery bez dwu = 2 (bez dwu) + 1 (za wygran) x 10 (warto kierw) = 3x10 = 30 punktw obliczeniowych;<br />
                        kiery z dwoma = 2 (z dwoma) + 1 (za wygran) x 10 (warto kierw) = 3x10 = 30 punktw obliczeniowych;<br />
                        piki z picioma = (5+1)x11 = 66;<br />
                        trefle bez trzech = (3+1)x12 = 48;<br />
                        trefle z omioma = (8+1)x12 = 108.
                    </p>
                    <p>
                        Istniej dodatkowe dwa stopnie wygranej. Gdy rozgrywajcy zdobdzie w rozgrywce (w lewach) ponad 89 punktw (przeciwnicy solisty nie maj w swoich lewach cznie 31 punktw), jest to wygrana z tzw. "krawcem" (przeciwnicy solisty s krawcami lub "niedoywionymi"). Za wygran z krawcem dolicza si wygrywajcemu jeden wspczynnik wicej za gr.
                    </p>
                    <p>
                        Na przykad:<br />
                        kiery bez dwu wygrane z krawcem = 2 (bez dwu) + 1 (za wygran) + 1 (za krawca) x 10 (warto kierw) = 4x10 = 40;<br />
                        piki z picioma i krawcem = (5+1+1)x11 = 72;<br />
                        trefle z omioma i krawcem = (8+1+1)x12 = 120.
                    </p>
                    <p>
                        Gdy rozgrywajcy zdobdzie wszystkie lewy, przeciwnicy za adnej (wyszli "na czarno"), jest to wygrana "z kominiarzem". Za wygran z kominiarzem dolicza si rozgrywajcemu rwnie jeden wspczynnik wicej za gr.
                    </p>
                    <p>
                        Na przykad:<br />
                        piki z picioma wygrane z krawcem i na czarno = (5+1+1+1)x11 = 88.
                    </p>
                    <p>
                        Przy grach kolorowych z rki za wygran gr dolicza si wygrywajcemu jeszcze jeden wspczynnik.
                    </p>
                    <p>
                        Zasadnicza warto obrachunkowa dla waletwek (gier grand) wynosi 24 punkty obliczeniowe. Poniewa w tym rodzaju rozgrywek istniej tylko cztery karty atutowe, gra moe by przeprowadzona z jednym, dwoma, trzema lub czterema (a take bez nich).
                    </p>
                    <p>
                        Na przykad:<br />
                        waletwka z dwoma = 2 (z dwoma) + 1 (za wygran) x 24 = 72;<br />
                        waletwka z trzema i z krawcem = (3+1+1)x24 = 120;<br />
                        waletwka z czterema, z krawcem i z kominiarzem = (4+1+1+1)x24 = 168.
                    </p>
                    <p>
                        Szczeglnym rodzajem rozgrywki, najwyszym w skacie jest waletwka z rki z wyoeniem (otwarta). Przez jej podjcie gracz zobowizuje si wzi wszystkie lewy niezalenie od rodzaju rozgrywki i przed pierwszym wyjciem wykada karty na st.
                    </p>
                    <p>
                        Zasadnicza warto obrachunkowa dla waletwek otwartych z rki (grand ouvert) wynosi 36 punktw obliczeniowych. Najwysz moliw ilo punktw w skacie uzyskuje gracz, ktry wygrywa waletwk otwart z rki z czterema, z krawcem i z kominiarzem. Jest to nastpujca ilo punktw:<br />
                        4 (z czterema) + 1 (za gr) + 1 (za gr z rki) + 1 (za krawca) + 1 (za krawca zapowiedzianego) + 1 (za kominiarza) + 1 (za kominiarza zapowiedzianego) x 36 (za waletwk otwart) = 10x36 = 360 punktw obliczeniowych.
                    </p>
                    <p>
                        Zerwki (gry zerowe, null) oblicza si wg staej wartoci punktowej wszystkich moliwych rozgrywek tego rodzaju. I tak:<br />
                        za zerwk na widzianego (z podjciem talonu) rozgrywajcy, ktry nie wzi ani jednej lewy, otrzymuje 23 punkty obliczeniowe;<br />
                        za zerwk z rki - 35pkt; za zerwk na widzianego otwart (z podjciem talonu i wyoeniem kart za st przed pierwszym wyjciem) - 46pkt;<br />
                        za zerwk z roki otwart - 59pkt.
                    </p>
                    <p>
                        Gr z krawcem i kominiarzem oblicza si przy wszystkich rodzajach rodzajach rozgrywek oprcz zerwek, lecz zapowiedzie je mona tylko przy grach z rki.
                    </p>
                    <p>
                        W skaci istniej wic nastpujce stopnie wygranej (przynoszce dodatkowe wspczynniki przy obliczaniu wartoci gry):<br />
                        za gr - 1;<br />
                        za krawca - 1;<br />
                        za krawca zapowiedzianego - 1 (tylko z rki);<br />
                        za kominiarza - 1;<br />
                        za kominiarza zapowiedzianego - 1 (tylko z rki);<br />
                        za gr z rki - 1;<br />
                        za gr z rki otwart - 1.
                    </p>
                    <p>
                        Gracz, ktry wygrywa licytacj, zapowiadajc zdobycie okrelonej liczby punktw obliczeniowych, ma obowizek rozgrywa tak gr, ktrej warto jest rwna lub wiksza od zapowiedzianej iloci punktw obliczeniowych.
                    </p>
                    <p>
                        Gracz, ktry przegrywa rozgrywk (nie osiga 61 punktw w zebranych lewach), otrzymuje punkty minusowe w iloci wartoci zapowiedzianej gry, jeli gra z rki, oraz iloci podwojonej, jeli gra na widzianego.
                    </p>
                    <p>
                        Gracz, ktry przegrywa rozgrywk z krawcem lub z kominiarzem (nie osiga nawet 31 punktw lub nie bierze ani jednej lewy), otrzymuje punkty minusowe obliczone z odpowiednio zwikszonymi wspczynnikami wartoci gry.
                    </p>
                    <p>
                        Przy zerwkach gracz rozgrywajcy przegrywa, jeli przeciwnicy wezm choby jedn lew, nawet jeli w tej lewie nie ma adnych punktw (skada si ona tylko z sidemek, semek lub dziewitek).
                    </p>
                    <p>
                        Przy grach kolorowych i waletwkach, jeli przeciwnicy rozgrywajcego wezm choby jedn lew, nawet jeli w tej lewie nie ma adnych punktw, nie przegrywaj z kominiarzem, lecz tylko z krawcem.
                    </p>
                </div>
            </div>
        );
    }
}



