import { basePath } from '../utils';
import React from 'react';


export default class Oszust extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Oszust',
            desc: 'gra dla 2 - 10 osb',
            url: basePath() + '/oszust',
            players: [2, 3, 4, 5, 6, 7, 8, 9, 10],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Gracze i karty</h2>
                    <p>
                        Gra dla 2 do 10 graczy.<br />
                        Talia 52 kart. Przy wikszej iloci graczy mona uy wicej talii.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Graczom rozdawane s wszystkie karty. W zalenoci od liczby graczy, niektrzy mog mie ich wicej, a niektrzy mniej.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Celem gry jest pozbycie si wszystkich kart.<br />
                        Rozdajcy wybierany jest losowo.
                    </p>
                    <p>
                        Kolejka rozpoczyna si od wyrzucenia jednej karty zakrytej i wypowiedzenia jej wartoci. Mona rwnie wyrzuci kilka kart tej samej wartoci. Pierwszy gracz musi wyrzuci asa, drugi dwjk, trzeci trjk itd. Gdy kolej przyjdzie na wyrzucenie krla, to po nim znw przychodzi kolej na asa, itd.
                    </p>
                    <p>
                        Nie trzeba jednak wyrzuca kart o wysokoci jak si zadeklarowao. Np. jest to twoja kolejka do wyrzucenia sidemek, moesz jednak wyrzuci dowoln kart lub kilka zmieszanych, szczeglnie jeli nie masz adnej sidemki jeste zmuszony do wyrzucenia innej karty.
                    </p>
                    <p>
                        Kady gracz, ktry podejrzewa wyrzucon kart o niezgodno z wymwion wartoci, moe wyzwa gracza wypowiadajc: "Oszust!". Wyzywajcy odsania wtedy wyrzucone karty i nastpuje jedna z dwch moliwoci:
                    </p>
                    <ol>
                        <li>
                            jeli karty s poprawne, a wyzwanie jest faszywe - wyzywajcy bierze cay stos;
                        </li>
                        <li>
                            jeli karty s faszywe, a wyzwanie jest prawdziwe - osoba, ktra je wyrzucia bierze cay stos;
                        </li>
                    </ol>

                    <p>
                        Gra jest kontynuowana od pierwszej osoby na lewo, ktra wzia stos i to ona mwi nastpn warto.
                    </p>
                    <p>
                        Pierwszy gracz, ktry pozbdzie si pierwszy swoich kart i zwyciy ostatnie wyzwanie wygrywa gr. Jeli gracz wyrzuci swoje ostatnie karty i kto go wyzwie, a karty nie bd zgodne z wartoci przez niego wypowiedzian, zabiera stos i gra jest kontynuowana.
                    </p>

                    <h2 className="content-subhead">Warianty</h2>
                    <p>
                        Mona gra z inn kolejnoci kart: nie od asa do krla (A, 2, 3, 4...) lecz od asa do dwjki (A, K, D, W...).
                    </p>
                    <p>
                        Mona gra tak, e po zadeklarowaniu wartoci karty przez jednego z graczy, nastpny moe zadeklarowa kart o rang wysz lub nisz. I tak, gdy ktry z graczy rzuci np. dwie dziesitki i nikt go nie wyzwa, nastpny moe rzuci walety lub dziewitki.
                    </p>
                </div>
            </div>
        );
    }
}



