import { basePath } from '../utils';
import React from 'react';
import preferansImg from '../images/preferans.gif';


export default class Preferans extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Preferans',
            desc: 'gra dla 3 [4] osb',
            url: basePath() + '/preferans',
            players: [3, 4],
            cards: 'Preferansowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Dawny francuski wariant <a href="/wist" className="pure-menu-link underline">wista</a>, szczeglnie popularny w Polsce w XIX i pierwszych latach XX wieku.
                    </div>

                    <h2 className="content-subhead">Gracze i karty</h2>
                    <p>
                        Gra dla 3 [4] osb.<br />
                        Talia tzw. preferansowa (32 karty).<br />
                        Starszestwo kart od asa do sidemki.<br />
                        Starszestwo kolorw: bez atu, kiery, kara, trefle, piki.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie. W kolejnych rozdaniach gracze rozdaj karty kolejno.
                    </p>
                    <p>
                        Kady z trzech graczy otrzymuje po 10 kart, dwie karty pozostaj na stole w zakrytym talonie.
                    </p>
                    <p>
                        Pierwszym etapem kadego rozdania jest licytacja, ktr rozpoczyna gracz po lewej stronie rozdajcego, deklarujc ilo lew, ktr zobowizuje si wzi w rozgrywce, oraz kolor atutowy rozgrywki. Wysza zapowied licytacyjna moe by wysza w kolorze lub w iloci zadeklarowanych lew. Najmniejsza moliwa zapowied w licytacji to zobowizanie wzicia szeciu lew z kolorem pikowym jako atutowym; najwysza zapowied to zobowizanie wzicia dziesiciu lew (tzw. totus) w rozgrywce bezatutowej.
                    </p>
                    <p>
                        Wygrywajcy licytacj (ten z graczy, ktry zgosi najwysz zapowied) podejmuje talon ze stou, okazuje karty w talonie wspgrajcym i wcza je do swojej rki. Nastpnie dwie dowolnie wybrane karty z rki odkada znw na st w zakrytym talonie i skada ostateczn deklaracj rozgrywki. Moe one by w innym kolorze lub obejmowa inn ilo lew ni deklaracja wygrywajca licytacj, musi jednak by od niej wysza w kolorze lub w iloci lew. Gracz ma prawo w rozgrywce pozosta przy swojej ostatecznej zapowiedzi licytacyjnej.
                    </p>
                    <p>
                        Po zoeniu ostatecznej deklaracji przez rozgrywajcego, wspgrajcy deklaruj swj udzia w rozgrywce. Moe on polega na wistowaniu lub na pasowaniu.
                    </p>
                    <p>
                        Zapowied "wistuj" oznacza zobowizanie si przez przeciwnika rozgrywajcego do wzicia w rozgrywce dwu lew (jeli rozgrywajcy zobowiza si wzi sze lew) lub jednej lewy (jeli rozgrywajcy zobowiza si wzi siedem, osiem lub dziewi lew). Jeli rozgrywajcy zobowiza si wzi totusa, rozgrywka zawsze odbywa si w penym skadzie graczy - obydwaj kontrpartnerzy maj obowizek wistowa, lecz aden z nich nie ma obowizku wzicia ani jednej lewy.
                    </p>
                    <p>
                        Przeciwnik rozgrywajcego moe spasowa, zgaszajc tym samym, e nie chce bra udziau w rozgrywce.
                    </p>
                    <p>
                        Wwczas drugi przeciwnik rozgrywajcego ma dwie moliwoci: rozgrywa sam (wwczas w rozgrywce bierze udzia tylko 20 kart, tj. w rkach rozgrywajcego i jego przeciwnika) lub te przywoa pasujcego gracza do gry, mwic "przywouj". Gracz przywoany do gry moe zosta przywoany "na ciemno" (bierze udzia w rozgrywce z kartami zakrytymi) lub "na widno" (po pierwszym wyjciu odsania wwczas swoje karty i kadzie je na stole). Gracz przywoany na ciemno sam decyduje, jak rozgrywa; kartami gracza przywoanego dysponuje ten, kto go do gry przywoa.
                    </p>
                    <p>
                        Gracz przywoujcy partnera do gry zobowizuje si tym samym do wzicia w rozgrywce czterech lew (jeli rozgrywajcy zobowiza si wzi sze lew), dwu lew (jeli rozgrywajcy zobowiza si wzi siedem lew) lub jednej lewy (jeli rozgrywajcy zobowiza si wzi osiem lub dziewi lew).
                    </p>
                    <p>
                        Jeli obydwaj przeciwnicy rozgrywajcego spasuj, rozgrywka nie odbywa si - zapisuje si j jako wygran przez rozgrywajcego.
                    </p>
                    <p>
                        W rozgrywce pierwszy wychodzi gracz rozgrywajcy. Istnieje obowizek dorzucania do koloru i przebijania atutem w wypadku nieposiadania koloru wyjcia. Nie ma obowizku przebijania starsz kart. Do kolejnej lewy wychodzi tan gracz, ktry wzi lew poprzedni.
                    </p>
                    <p>
                        Z wygran rozgrywk rozgrywajcy zapisuje sobie odpowiedni ilo punktw do wasnej puli, i tak:
                    </p>
                    <ul>
                        <li>
                            za zadeklarowanie i wzicie szeciu lew w kadym z kolorw - 10pkt puli;
                        </li>
                        <li>
                            za wzicie siedmiu lew - 20pkt;
                        </li>
                        <li>
                            za wzicie omiu lew - 30pkt;
                        </li>
                        <li>
                            za wzicie dziewiciu lew - 40pkt;
                        </li>
                        <li>
                            za wzicie totusa (dziesiciu lew) - 50pkt.
                        </li>
                    </ul>

                    <p>
                        Jeli rozgrywajcy w rozgrywce wemie wicej lew, ni zadeklarowa ich w licytacji, zapisuje sobie punkty tylko za lewy zadeklarowane.
                    </p>
                    <p>
                        Jeli rozgrywajcy nie wykona zobowizania licytacyjnego (wemie w rozgrywce mniej lew, ni zapowiedzia), zapisuje sobie punkty ujemne (tzw. "kury") wedug nastpujcych zasad:
                    </p>
                    <ul>
                        <li>
                            przy deklaracji szeciu lew za kad lew brakujc - 10 kurw;
                        </li>
                        <li>
                            przy deklaracji siedmiu lew - 20 kurw;
                        </li>
                        <li>
                            omiu lew - 30 kurw;
                        </li>
                        <li>
                            dziewiciu lew - 40 kurw;
                        </li>
                        <li>
                            totusa (dziesiciu lew) - 50 kurw.
                        </li>
                    </ul>

                    <p>
                        Np. bez trzech przy zobowizaniu wzicia omiu lew, gdy rozgrywajcy wzi ich pi - to 90 kurw (3x30); wpadka bez trzech przy zobowizaniu wzicia szeciu lew - to 30 kurw (3x10).
                    </p>
                    <p>
                        Po ukoczonej rozgrywce zapisuje si take punkty (tzw. punkty wistowe) przeciwnikom rozgrywajcego. I tak:
                    </p>
                    <ul>
                        <li>
                            za kad lew wzit przy zapowiedzi rozgrywajcego na wysokoci szeciu lew - 10pkt;
                        </li>
                        <li>
                            siedmiu lew - 20pkt;
                        </li>
                        <li>
                            omiu lew - 30pkt;
                        </li>
                        <li>
                            dziewiciu lew - 40pkt;
                        </li>
                        <li>
                            totusa - 50pkt.
                        </li>
                    </ul>

                    <p>
                        Jeli rozgrywajcy nie wykona w rozgrywce zobowizania licytacyjnego (wpad), wwczas wistujcemu lub wistujcym dolicza si do iloci lew faktycznie zdobytych w rozgrywce take ilo wpadek rozgrywajcego.<br />
                        Np. rozgrywajcy zapowiedzia siedem lew, wzi pi, wpad wic bez dwu. Pierwszy wistujcy wzi trzy lewy, uzyskujc za nie [3+2(wpadki)]x20, czyli 100pkt wistowych; drugi wistujcy wzi dwie lewy, uzyskujc za nie [2+2(wpadki)]x20, czyli 80pkt wistowych.
                    </p>
                    <p>
                        Oczywicie uzyskanie tylko 50 punktw wistowych za lew przy zadeklarowanym totusie jest teoretycznie niemoliwe. Aby je zdoby, trzeba wzi co najmniej jedn lew, a ta jest ju wpadk dla rozgrywajcego. A wic totus bez jednej: [1+1(wpadka)]x50, czyli 100pkt wistowych.
                    </p>
                    <p>
                        Przy grze z przywoaniem gracz, ktry przywoa partnera, zawsze dolicza sobie przy zapisywaniu punktw wistowych dodatkow jedn lew za przywoanie. Np. rozgrywajcy zapowiedzia osiem lew i wzi je, jeden z graczy w rozgrywce spasowa, drugi przywoa go "na widno", w sumie wzili dwie lewy - dla wistujcego [2+1(przywoanie)]x30, czyli 90pkt wistowych; rozgrywajcy zapowiedzia dziewi lew, wzi siedem, jeden z graczy w rozgrywce spasowa drugi przywoa go "na ciemno", w sumie wzili trzy lewy - dla wistujcego [3+2(wpadki)+1(przywoanie)]x40, czyli 240pkt wistowych. Wszystkie punkty zapisuje sobie gracz przywoujcy. Gracz przywoany nie zdobywa punktw.
                    </p>
                    <p>
                        jeli gracz wistujcy nie wykona w rozgrywce swego zobowizania (nie wemie dwu lew przeciwko zapowiedzi szeciu lew lub jednej lewy przeciwko innym zapowiedziom), zapisuje sobie wpadk lub wpadki tak samo jak rozgrywajcy (odpowiedni ilo kurw). Np. wistujcy przeciwko zapowiedzi szeciu lew nie wzi adnej lewy, cho obowizyway go dwie; jest to wpadka bez dwu - a wic 20 kurw (po 10 za wpadk, jak przy grze na poziomie szeciu).
                    </p>
                    <p>
                        Nie ma wpadki dla wistujcego, choby nawet nie wzi lew lub lewy, ktre zobowiza si wzi w danej rozgrywce, jeli wzi je drugi wistujcy. Np. przy zapowiedzi szeciu jeden z wistujcych wzi tylko jedn lew, za to drugi trzy; przy zapowiedzi siedmiu jeden z wistujcych nie wzi adnej lewy, za to drugi - dwie; nie ma wpadek.
                    </p>
                    <p>
                        Zapis preferansowy prowadzi si w specjalnej tabeli, ktra jest przedstawiona na poniszym rysunku:
                    </p>

                    <div className="center-outer-container">
                        <div className="center-inner-container">
                            <img className="pure-img" src={preferansImg}></img>
                        </div>
                    </div>

                    <ul>
                        <li>I - wasna pula gracza A;</li>
                        <li>II - wasna pula gracza B;</li>
                        <li>III - wasna pula gracza C;</li>
                        <li>IV, V, VI - kury graczy (odpowiednio) A, B, C;</li>
                        <li>VII - punkty wistowe gracza A zdobyte w rozgrywkach przeciwko graczowi B; </li>
                        <li>VIII - punkty wistowe gracza A zdobyte w rozgrywkach przeciwko graczowi C; </li>
                        <li>IX, X - punkty wistowe gracza B zdobyte w rozgrywkach przeciwko graczom A i C; </li>
                        <li>XI, XII - punkty wistowe gracza C zdobyte w rozgrywkach przeciwko graczom B i A; </li>
                        <li>XIII - koguty; jeden z nich (dwustronnie zaznaczony) zgrany.</li>
                    </ul>
                    <p>
                        Jeli w licytacji wszyscy trzej gracze spasuj, nie ma rozgrywki, notuje si natomiast tzw. koguta. Przy grze z kogutem wszystkie zapisy za gr (pula, wist, kury) zostaj podwojone. Jeli przy grze z kogutem rozgrywka zakoczy si wpadk rozgrywajcego, kogut nie zostaje zgrany, lecz trwa dalej, a jedna z rozgrywek powiedzie si i kogut zostanie zgrany, co zaznacza si w zapisie. Jeli w zapisie istnieje wicej ni jeden kogut niezgrany, w jednej rozgrywce zgrywa si tylko jednego koguta.
                    </p>
                    <p>
                        Gracz, ktry zdoby ju w zapisie 100pkt wasnej puli (zgra wasn pul), bierze dalej udzia w licytacji i w rozgrywkach; jeli jest rozgrywajcym i wygra, ilo punktw, ktr wygra, wpisuje si do puli tego z pozostaych graczy, ktremu brak mniej punktw do ukoczenia puli (nazywa si to zgrywaniem puli przeciwnika), otrzymujc w zamian odpowiedni ilo punktw wistowych na danym graczu w relacji 1:10 (za zgranie np. 30pkt puli - 300pkt wistowych).
                    </p>
                    <p>
                        Gdy wszyscy gracze zdobyli ju w zapisie wszystkie punkty puli, nastpuje zgrywanie kurw. Kady z graczy zgrywa najpierw wasne kury, nastpnie kury przeciwnikw (w relacji 1pkt kurowy = 1pkt puli = 10pkt wistowych).
                    </p>
                    <p>
                        Gra koczy si, gdy wszyscy gracze maj ju zgrane swoje pule i kury. Oblicza si wwczas rnice w punktach wistowych oddzielnie dla gracza A i B, dla gracza B i C oraz dla gracza C i A.
                    </p>

                    <h2 className="content-subhead">Preferans dla czterech osb</h2>
                    <p>
                        W tym wariancie gry rozdajcy karty nie bierze udziau w danej licytacji i rozgrywce (pauzuje). Graj za kadym razem trzej gracze, a wic kolejno BCD, ACD, ABD i ABC.
                    </p>
                    <p>
                        Jeli w talonie znajduje si as w kolorze atutowym danej rozgrywki (lub jakikolwiek as w rozgrywce bez atutowej), rozdajcy otrzymuje w zapisie za t rozgrywk punkty wistowe na rozgrywajcym jak za jedn lew przy wistowaniu.
                    </p>
                    <p>
                        Wszystkie inne zasady licytacji, rozgrywki i zapisu - bez zmian.
                    </p>

                    <h2 className="content-subhead">Wariant preferansa</h2>
                    <p>
                        Mona wprowadzi wariant licytacji "na ciemno". W tym wariancie gracz bierze udzia w licytacji, jeli sam tego chce, nie ogldajc wasnych kart, licytujc na ciemno. Gracz ma prawo w kadym momencie licytacji obejrze wasne karty - wwczas licytacja staje si zwyk licytacj na widno.
                    </p>
                    <p>
                        Jeli gracz wygra licytacj na ciemno, nie okazuje kart talonu wspgrajcym, zapis za za rozgrywk (pula, wist, kury), zarwno w wypadku wygranej, jak i przegranej, zostaje podwojony dla wszystkich graczy.
                    </p>
                    <p>
                        Rozgrywka po wygranej licytacji na ciemno przy kogucie ma wszystkie zapisy poczwrne (dwukrotnie podwojone).
                    </p>
                </div>
            </div>
        );
    }
}



