import { basePath } from '../utils';
import React from 'react';
import konstrukcjaImg1 from '../images/konstrukcja1.gif';
import konstrukcjaImg2 from '../images/konstrukcja2.gif';


export default class Konstrukcja extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Konstrukcja',
            desc: 'gra dla 2 osb',
            url: basePath() + '/konstrukcja',
            players: [2],
            cards: 'Trzy standardowe talie kart (3 x 52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Gra karciano-planszowa, opracowana przez amerykaskiego znawc i twrc nowych gier, Roberta Abbotta, w 1963 roku.
                    </div>

                    <h2 className="content-subhead">Rekwizyty</h2>
                    <p>
                        Trzy pene talie kart (po 52 karty), dwa dokery, dwa piony (biay i czarny) oraz dwanacie po innych, mniejszych pionkw biaych i czarnych.
                    </p>

                    <h2 className="content-subhead">Cel gry</h2>
                    <p>
                        Zbudowanie za pomoc kart, ktrymi dysponuj gracze, tzw. trasy planszowej i przejcie wasnym pionem tej trasy od karty wyjciowej do koca i z powrotem na kart wyjciow.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Przed rozpoczciem gry wykada si pionowo dwa dokery w odlegoci jednej poziomo uoonej karty pomidzy nimi i na dokerach stawia si piony obydwu graczy, biay i czarny.<br />
                        Dokery s kartami wyjciowymi obydwu graczy.
                    </p>
                    <p>
                        Przez losowanie ustala si, ktry z graczy bdzie gra kartami czarnymi (pikami i treflami), a ktry czerwonymi (kierami i karami).
                    </p>
                    <p>
                        Trzy talie kart tasuje si i odsania wierzchni kart zakrytego talonu. W zalenoci od jej koloru (czarna lub czerwona) bdzie to karta do dyspozycji pierwszego lub drugiego gracza. W kolejnych turach gra ten z graczy, ktry dysponuje odkryt kart talonu, moe wic zdarzy si, i jeden z graczy bdzie grajcym kilka razy pod rzd.
                    </p>
                    <p>
                        Kad z kart do dyspozycji gracz ma obowizek wykorzysta na jeden z czterech sposobw:
                    </p>
                    <ul>
                        <li>uy jej do konstrukcji planszy;</li>
                        <li>uy jej do ruchu po planszy wasnym pionem;</li>
                        <li>uy jej do ruchu po planszy ktrym z wasnych maych pionkw;</li>
                        <li>zrzuci kart na stos odkryty.</li>
                    </ul>

                    <p>
                        Konstrukcja planszy odbywa si przez wykadanie kart na st w nastpujcy sposb:
                    </p>
                    <ul>
                        <li>karta wykadana na st moe lee tylko poziomo lub pionowo;</li>
                        <li>
                            jeli kart kadzie si poziomo, musi ona krtszym bokiem przytyka do duszego boku ktrej z ju lecych pionowo kart;
                        </li>
                        <li>
                            jeli kart kadzie si pionowo, musi ona duszym bokiem przytyka do krtszego boku ktrej z ju lecych poziomo kart lub krtszym bokiem przytyka do krtszego boku ktrej z ju lecych pionowo kart.
                        </li>
                    </ul>

                    <p>
                        Innymi sowy, powstajca w trakcie gry plansza konstrukcji skada si z pionowych kolumn kart uoonych pionowo jedna nad drug i poczonych midzy sob jedn lub wieloma kartami lecymi poziomo. Wszystkie moliwe w konstrukcji ukady kart na planszy przedstawia poniszy rysunek.
                    </p>

                    <div className="center-outer-container">
                        <div className="center-inner-container">
                            <img className="pure-img" src={konstrukcjaImg1}></img>
                        </div>
                    </div>

                    <p>
                        Aby wygra gr, gracz ma obowizek swoim pionem ze startowego dokera odby podr na trasie dziesiciu kart pionowo w gr i powrci na startowego dokera. Trasa podry moe by zbudowana przez gracza, przez jego przeciwnika lub przez obydwu. Moe to by trasa zawierajca nie tylko karty pionowe, lecz take poziome (trasa z objazdami). Do dugoci odbytej podry licz si jednak tylko karty pionowe i nie wlicza si startowego dokera.
                    </p>
                    <p>
                        Kolejn kart do swojej dyspozycji gracz ma prawo uoy w dowolnym miejscu planszy zgodnie z zasadami dokadania (take do trasy budowanej przez przeciwnika).
                    </p>
                    <p>
                        Przesuwanie wasnego piona po tej czci planszy, ktra istnieje aktualnie w grze, odbywa si nastpujco:<br />
                        po odsoniciu karty z talonu, jeli okae si ona w dyspozycji danego gracza, ma on prawo zadeklarowa przesunicie swoim pionem i przesun go na dowoln ssiedni kart z tej, na ktrej pion aktualnie stoi (poziomo lub pionowo, w gr lub w d) - jednak wtedy i tylko wtedy, jeli karta odsonita z talonu jest iloci oczek rwna lub wysza ni karta, na ktrej stanie przesunity pion. Wysoko kart liczy si wedug liczby oczek (dama i walet maj po 10pkt; krle peni role specyficzn).
                    </p>
                    <p>
                        Nie wolno wasnym pionem wchodzi na to pole planszy (na t kart), na ktrej stoi pion przeciwnika lub ktry z maych pionw przeciwnika. Kolor karty, na ktrej stanie przesunity pion, nie ma znaczenia.
                    </p>
                    <p>
                        Kart z talonu, uyt do przesunicia wasnego piona na planszy, zrzuca si na stos otwarty.
                    </p>
                    <p>
                        Jeli kart dyspozycyj gracza ujawnion z talonu jest krl, na planszy naley ustawi may pion w kolorze danego gracza. May pion zostaje ustawiony na dokerze startowym gracza i od razu ma prawo (cho nie ma obowizku) przesun si o trzy pola na planszy (o trzy karty) w dowoln stron.
                    </p>
                    <p>
                        Istnienie maych pionw w grze ma na celu blokowanie drogi przeciwnikowi, nigdy bowiem adne dwa wrogie piony, ani due, ani mae, nie mog sta na tym samym polu (karcie) planszy. Dwa piony tego samego koloru mog sta na tym samym polu planszy i mija si wzajemnie.
                    </p>
                    <p>
                        Mae piony przesuwa si po planszy podobnie jak piony due, z jedn wan rnic:<br />
                        pion duy zawsze przechodzi tylko na pole ssiednie, pion may natomiast moe w jednym ruchu przesun si o jedno, dwa lub trzy pola, zalenie od dysponujcego nim gracza, pod warunkiem jednak, e adna z kart mijanych oraz kocowa karta ruchu nie bd wysze wartoci ni dyspozycyjna karta z talonu, przy ktrej gracz porusza may pion.
                    </p>
                    <p>
                        Jeli kolejna karta zamknitego talonu, dyspozycyjna dla jednego z graczy, nie nadaje si wdug jego uznania ani do konstrukcji planszy, ani do adnego ruchu, gracz moe zrzuci j na otwarty stos. Odsania si wwczas nastpn kart talonu.
                    </p>
                    <p>
                        W grze bierze udzia (moe zosta wystawione na planszy) najwyej dwanacie maych pionw kadego z graczy. Jeeli s one ju wystawione, gracz za odsoni z talonu kolejnego swego dyspozycyjnego krla, traktowany jest on jak dama lub walet, lecz nie moe zosta uyty do budowy konstrukcji planszy, ale tylko do ruchu.
                    </p>
                    <p>
                        Jeli wyczerpay si karty w zakrytym talonie, gra za nie dobiega jeszcze koca, odwraca si karty odkrytego stosu, tasuje i tworzy z nich nowy zakryty talon.
                    </p>
                    <p>
                        Nie wolno soimi maymi pionami blokowa duego piona przeciwnika ze wszystkich stron - w kadym momencie gry musi on mie moliwo wykonania co najmniej jednego posunicia, choby niekorzystnego.
                    </p>
                    <p>
                        Jeli w toku gry (co moe si zdarzy, chocia niezmiernie rzadko) w ogle zabraknie kart w talonie lub pozostan tam same krle, gracze zdejmuj po dziesi dowolnie wybranych swoich kart dyspozycyjnych z planszy i wczaj je do kart talonu.
                    </p>
                    <p>
                        W ostatnim dolnym rzdzie pl planszy (wyjciowym - tym, w ktrym le dokery) na jednym polu planszy moe sta dowolna liczba maych i duych pionw obydwu kolorw i adne z nich nie blokuj si wzajemnie.
                    </p>
                    <p>
                        Wolno (cho zdarza si to rzadko) rozbudowa plansz poza dziesi kart w pionie nie liczc dokerw.
                    </p>
                    <p>
                        Gr wygrywa ten z graczy, ktry pierwszy odbdzie obowizkow podr przez dziesi kart tam i z powrotem i znajdzie si swym pionem startowym na wasnym dokerze.
                    </p>

                    <div className="example">
                        <div className="content">
                            W sytuacji przedstawionej na poniszym rysunku jestemy w penym toku gry:<br />
                            plansza zawiera ju dwie moliwe trasy obowizkowe po dziesi kart kada, liczne poczenia midzy kolumnami kart i objazdy. Na planszy stoi pi maych pionkw biaych i sze maych pionkw czarnych oraz oczywicie duy pion biay i duy pion czarny.<br />
                            Sytuacja jest do wyrwnana, skuteczna blokada grozi obydwu graczom.
                        </div>
                    </div>

                    <div className="center-outer-container">
                        <div className="center-inner-container">
                            <img className="pure-img" src={konstrukcjaImg2}></img>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}



