import { basePath } from '../utils';
import React from 'react';


export default class Makao extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Makao',
            desc: 'gra dla 3 lub wicej graczy',
            url: basePath() + '/makao',
            players: [3, 4, 5, 6, 7, 8],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Talia 52 kart.<br />
                        Starszestwo kart nie ma znaczenia.<br />
                        Przy wikszej iloci graczy mona uywa kilku talonw kart.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Pierwszego rozdajcego wybiera si przez losowanie. Nastpnie w kolejnych rozgrywkach gracze rozdaj karty kolejno. Kady z graczy otrzymuje po pi kart. Po rozdaniu kart graczom wykadana jest na st jeszcze jedna karta odkryta. Reszta kart odkadana jest na st do talonu zakrytego. Wyoona na st karta nie moe by dwjk, trjk, czwrk, waletem, krlem lub asem. Jeli jest ktr z powyszych kart wykada si nastpn lub jeszcze jedn, a do momentu kiedy ktra z nich bdzie od nich rna. Wszystkie wczeniej wyoone karty wkadamy zakryte na spd talonu.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Gr rozpoczyna zawsze gracz po lewej stronie rozdajcego. Musi on rzuci kart o takim samym kolorze lub takiej samej wartoci co karta odkryta na stole.
                    </p>
                    <p>
                        Nastpni gracze dorzucaj karty do koloru lub do wartoci zgodnej z kart wyrzucon przez gracza poprzedniego. Jeli nie mog lub nie chc wyrzuci karty z rki na stos, bior grn kart z talonu zakrytego. Jeli ta bdzie zgodna z kart, do ktrej powinien dorzuci, moe to zrobi.
                    </p>
                    <p>
                        Jeli gracz, ktrego nadesza kolej, ma wicej kart tej samej wartoci, moe wyrzuci ich dowoln ilo (wszystkie lub tylko cz), pod warunkiem, e pierwsza z nich bdzie zgodna z kolorem lub wartoci karty, do ktrej dorzuca. Kolejno reszty kart jest dowolna.
                    </p>
                    <p>
                        Jeeli wyrzucon przez gracza karta (lub kilka kart tej samej wartoci) bdzie 2, 3, 4, W, K lub A nastpuj zdarzenia przypisane tym kartom:
                    </p>
                    <ul>
                        <li>
                            2 - gracz nastpny w kolejnoci pobiera z talonu zakrytego dwie karty;
                        </li>
                        <li>
                            3 - gracz nastpny w kolejnoci pobiera z talonu zakrytego trzy karty;
                        </li>
                        <li>
                            4 - gracz nastpny w kolejnoci traci kolejk (do gry przystpuje nastpny z graczy);
                        </li>
                        <li>
                            W - gracz rzucajcy waleta da karty o dowolnej wartoci (oprcz 2, 3, 4, W, K i A);
                        </li>
                        <li>
                            K - jeli jest to K <span className="heart"></span>- nastpny gracz pobiera z talonu zakrytego 5 kart;<br />
                            jeli jest to K <span className="spade"></span> - poprzedni gracz pobiera 5 kart z talonu zakrytego i do niego wraca kolejka;
                        </li>
                        <li>
                            A - gracz rzucajcy asa da dowolnego koloru;
                        </li>
                    </ul>

                    <p>
                        Jeli gracz wyrzuci kilka dwjek, ich warto sumuje si, i tak na przykad przy wyrzuconych trzech dwjkach nastpny gracz powinien pobra 6 kart. Gracz, do ktrego rzucone byy dwjki moe (jeli posiada na rku) przekaza kolejk dalej i zwikszy jednoczenie liczb kart do pobrania z talonu rzucajc rwnie dwjk (lub kilka dwjek).<br />
                        Moe rwnie przekaza kolejk dalej i zwikszy liczb kart do pobrania rzucajc trjk (lub kilka trjek).<br />
                        Moe rwnie przekaza kolejk dalej i zwikszy liczb kart do pobrania rzucajc K <span className="heart"></span> pod warunkiem, e ostatnio pooona dwjka bya tego samego koloru (tzn. 2 <span className="heart"></span>).<br />
                        Moe rwnie cofn kolejk do gracza poprzedniego i zwikszy liczb kart do pobrania rzucajc K <span className="spade"></span> pod warunkiem, e ostatnio pooona dwjka bya tego samego koloru (tzn. 2 <span className="spade"></span>) .
                    </p>
                    <p>
                        Moe rwnie rzuci np. na 2 <span className="spade"></span> K <span className="spade"></span> i dodatkowo K <span className="spade"></span>. W takiej sytuacji sumuje si warto krli, a kolejka jest przekazywana do gracza nastpnego (okrela to zawsze karta leca na wierzchu).
                    </p>
                    <p>
                        Do trjki (trjek) mona dorzuca kolejne trjki, dwjki i odpowiednie krle, przekazujc kolejk do przodu bd do tyu i zwikszajc liczb kart do pobrania o odpowiedni ilo, na takich samych zasadach jak przy dwjkach
                    </p>
                    <p>
                        Sytuacja jest identyczna w wypadku K <span className="heart"></span> oraz K <span className="spade"></span>, do ktrych mona dorzuca kolejne krle, dwjki lub trjki.
                    </p>
                    <p>
                        W przypadku wyrzucenia przez ktrego z graczy czwrki, nastpny gracz traci tyle kolejek, ile czwrek zostao wyrzuconych. Jeli posiada czwrk na rku, moe (lecz nie musi) przekaza straty kolejek do nastpnego gracza. Jeli nie posiada czwrki, nie pobiera karty z talonu i czeka okrelon liczb kolejek, nie biorc w nich udziau.
                    </p>
                    <p>
                        Jeli zosta wyrzucony walet, gracz ktry go wyrzuci moe (lecz nie musi) zada karty o okrelonej wartoci. Nastpni gracze musz wyrzuci kart o danej wartoci lub kolejnego waleta dajc kart o wartoci przez siebie wybranej. Przy kolejnym daniu wartoci kart, poprzednie dania s anulowane. danie to jest wane do momentu, a kolejka wrci do gracza, ktry danej warto kart da.
                    </p>
                    <p>
                        Identyczna sytuacja jest w przypadku wyrzucenia asa, z tym e zamiast wartoci kart, da si koloru.
                    </p>
                    <p>
                        Jeli podczas gry skoczy si talon z kartami zakrytymi, zbiera si karty ze stosu odkrytego, tasuje si i kadzie si na stosie zakrytym.
                    </p>
                    <p>
                        Gra toczy si do momentu, a tylko jeden gracz pozostanie z kartami na rku, i ten gracz bdzie przegranym.
                    </p>
                </div>
            </div>
        );
    }
}



