import { basePath } from '../utils';
import React from 'react';


export default class SzescdziesiatSzesc extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Szedziesit sze',
            name2: '66',
            desc: 'gra dla 2 [3 i 4] osb',
            url: basePath() + '/szescdziesiatszesc',
            players: [2, 3, 4],
            cards: 'Maa talia kart (24 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Jedna ze starszych gier karcianych, pochodzca z Niemiec. Jak twierd niektrzy, gr t wymylili w westfalskim miecie Paderborn w 1652 roku stali bywalce szynku przy ulicy Eckkamp 66 - std ta liczba w przepisach i nazwie gry.<br />
                        Do Polski gra ta dotara pod koniec XVII wieku, we francuskiej formi i pod nazw mariasza; jej warianty znane byy rwnie jako gaigel i sznaps.
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla dwch lub trzech graczy</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Do gry uywa si maej talii kart (24 karty).<br />
                        Starszestwo kart: as, dziesitka, krl, dama, walet, dziewitka.<br />
                        Warto punktowa: as - 11, dziesitka - 10, krl - 4, dama - 3, walet - 2, dziewitka - 0.<br />
                        Istnieje zmienny kolor atutowy. Meldunek (krl i dama w jednym kolorze) - 20pkt. Meldunek w kolorze atutowym - 40pkt.
                    </p>

                    <h2 className="content-subhead">Zasady gry</h2>
                    <p>
                        Kady z grajcych otrzymuje po sze kart, trzynast kart odkrywa si. Wyznacza ona kolor atutowy w danej rozgrywce. Kart odkryt umieszcza si na wp widoczn pod zakrytym stosem.
                    </p>
                    <p>
                        W grze rozgrywane s kolejne lewy; pierwszy wychodzi przeciwnik rozdajcego. Ten z graczy, ktry wzi lew uzupenia karty w rku do szeciu, cignc wierzchni kart ze stosu zakrytego, po czym to samo robi jego przeciwnik. Gracz, ktry wzi ostatni lew, wychodzi do nastpnej.
                    </p>
                    <p>
                        Posiadacz dziewitki atutowej moe, lecz nie musi, po wziciu lewy i przed nastpnym wyjciem wymieni j na kart lec pod stosem zakrytym.
                    </p>
                    <p>
                        Dopki le karty na stosie zakrytym nie ma obowizku dorzucania do koloru i mona przebija kart wyjciow atutem, nawet majc kart w kolorze wyjciowym. Nie ma natomiast obowizku przebijania przy braku koloru karty wyjcia; mona zrzuci dowoln kart.
                    </p>
                    <p>
                        Posiadacz meldunku zgasza go ("melduje") wychodzc, co jest obowizkowe, w jedn z kart meldunku (w krla lub w dam).
                    </p>
                    <p>
                        Gdy skocz si karty na stosie zakrytym, zmieniaj si zasady rozgrywki: teraz istnieje obowizek dorzucania do koloru i obowizek przebijania atutem, gdy nie ma si karty w kolorze wyjcia.
                    </p>
                    <p>
                        Gracz, na ktrego przypada kolejno wyjcia po wziciu lewy, moe w kadym momencie rozgrywki "zamkn gr" (wyj kart atutow spod stosu zakrytego i pooy j na nim). Wwczas gracze nie dobieraj ju kart ze stosu i gra toczy si do rozegrania ostatniej lewy tak, jakby skoczyy si karty na stosie zakrytym.
                    </p>

                    <h2 className="content-subhead">Punktacja</h2>
                    <p>
                        Gracz, ktry pierwszy za karty wzite w lewach i zgoszone meldunki zdoby cznie 66 punktw, wygrywa rozgrywk, choby nie dobiega ona jeszcze koca. Liczenie zdobytych punktw powinien wic kady z graczy na swj wasny uytek prowadzi w pamici na bieco.<br />
                        NIE WOLNO ZGLDA DO KART WCHODCYCH W SKAD ZDOBYTYCH LEW.
                    </p>
                    <p>
                        Jeli przeciwnik wygrywajcego zdoby w swoich lewach i meldunkach ponad 33 punkty (tzw. woda), wygrywajcemu za wygran rozgrywk liczy si jeden punkt.<br />
                        Jeli przeciwnik wygrywajcego w swoich lewach i meldunkach zdoby mniej ni 33 punkty, wygrywajcemu za wygran rozgrywk liczy si dwa punkty.<br />
                        Jeli przeciwnik wygrywajcego nie zdoby ani jednej lewy, wygrywajcemu za wygran rozgrywk liczy si trzy punkty.<br />
                        Przy grze zamknitej wygrywajcy, niezalenie od iloci punktw zdobytych przez przeciwnika, zawsze zdobywa trzy punkty.<br />
                        Jeli gracz, ktry zamkn gr nie zdobdzie do koca rozgrywki 66 punktw, jego przeciwnik zdobywa trzy punkty.
                    </p>
                    <p>
                        Gra toczy si do chwili zdobycia przez jednego z graczy omiu (lub - w zalenoci od umowy - siedmiu) punktw.
                    </p>

                    <h2 className="content-subhead">66 dla trzech osb</h2>
                    <p>
                        Kady z grajcych otrzymuje po osiem kart; ostatnia karta, naleca do rozdajcego, wyznacza kolor atutowy.<br />
                        Gra toczy si bez stosu zakrytego, a wic z obowizkiem dorzucania do koloru i przebijania.<br />
                        Kady z graczy gra na wasny rachunek.<br />
                        Gdy aden z graczy nie zdobdzie 66 punktw, nastpuje kolejna rozgrywka.<br />
                        Wszystkie inne zasady pozostaj bez zmian.
                    </p>
                </div>

                <div className="content">
                    <a name="szwabacha"></a>
                    <h1 className="content-subhead main-header">Szwabacha</h1>
                    <div className="info">
                        Wariant gry (regionalny niemiecki).
                    </div>

                    <p>
                        Do gry uywa si dwu identycznych, zmieszanych razem maych talii kart (48 kart). Kady z grajcych otrzymuje osiem kart, reszta pozostaje na stosie zakrytym.
                    </p>
                    <p>
                        Jeli w lewie spotkaj si dwie identyczne karty, starsza jest ta, ktra bya zagrana pierwsza.
                    </p>
                    <p>
                        Gra toczy si do chwili zdobycia przez jednego z graczy 101 punktw.<br />
                        Kady z graczy gra na wasny rachunek<br />
                        Pozostae zasady gry nie ulegaj zmianie.
                    </p>
                </div>

                <div className="content">
                    <a name="obierany"></a>
                    <h1 className="content-subhead main-header">Obierany</h1>
                    <h2 className="content-subhead">Gracze i karty</h2>
                    <p>
                        Gra dla 3 osb.<br />
                        Talia 24 kart. Starszestwo kart: A, 10, K, D, W, 9.<br />
                        Wartoci kart: 9 - 0pkt, W - 2pkt, D - 3pkt, K - 4pkt, 10 - 10pkt, A - 11pkt.<br />
                        Meldunki: zyky - 20pkt, atutowy - 40pkt.
                    </p>

                    <h2 className="content-subhead">Licytacja</h2>
                    <p>
                        Wybrany losowo rozdajcy rozdaje kademu graczowi po 4 karty naraz. Po rozdaniu wszyscy zagldaj w karty i zaczyna is obieranie (licytacja). Rozpoczyna pierwszy gracz po lewej od rozdajcego, tzw. Forant. Ma on trzy moliwoci:
                    </p>
                    <ul>
                        <li>moe wybra kolor, ktry bdzie kolorem atutowym - grany jest wtedy <a href="#wlasciwy" className="pure-menu-link underline">OBIERANY</a> (waciwy);</li>
                        <li>moe powiedzie: "KONTRA WARSZAWIE", czym sygnalizuje ch grania w <a href="#warszawa" className="pure-menu-link underline">WARSZAW</a>;</li>
                        <li>moe powiedzie: "DALEJ" (lub po prostu "PASS").</li>
                    </ul>
                    <p>
                        Jeeli wybra pierwsz moliwo, to "obieranie" zostao zakoczone i rozdajcy moe rozda pozostae karty (znowu po 4 naraz).<br />
                        Gdy wybierze drug lub trzeci moliwo, prawo gosu ma nastpny gracz (a potem gracz trzeci), ktry rwnie ma do wyboru powysze trzy moliwoci, z tym e w przypadku, gdy ktry z graczy da KONTR, nastpny moe da RE-KONTR, a nastpny SUP.<br />
                        Jeeli wszyscy trzej gracze powiedz DALEJ, to rozgrywana jest <a href="#warszawa" className="pure-menu-link underline">WARSZAWA</a> bez KONTRY.<br />
                        Jeeli obrana zostanie KONTRA lub RE-KONTRA, a nastpny gracz obierze jaki kolor atutowy, to WARSZAWA zostaje anulowana i grany jest <a href="#wlasciwy" className="pure-menu-link underline">OBIERANY</a>.<br />
                        Po zakoczeniu licytacji rozdajcy rozdaje pozostae karty.
                    </p>

                    <a name="warszawa"></a>
                    <h2 className="content-subhead">Rozgrywka w WARSZAW</h2>
                    <p>
                        Gr rozpoczyna ten, kto najwyej przelicytowa WARSZAW (jeli wszyscy gracze powiedzieli DALEJ, gr rozpoczyna Forant). Kady gra na wasny rachunek tak, aby jak najmniej ugra.<br />
                        Jest obowizek dawania do koloru oraz przebijania starsz kart.<br />
                        Nie ma koloru atutowego.<br />
                        Po rozegraniu wszystkich lew podliczamy punkty. Przegrywa ten, kto ma ich najwicej. W zalenoci o jak stawk bya gra, otrzymuje nastpujc liczb punktw:
                    </p>
                    <ul>
                        <li>bez KONTRY: 1pkt;</li>
                        <li>z KONTR: 2pkt;</li>
                        <li>z RE-KONTR: 4pkt;</li>
                        <li>z SUPem: 8pkt;</li>
                    </ul>
                    <p>
                        Przegrany rozdaje karty do nastpnej gry.
                    </p>

                    <a name="wlasciwy"></a>
                    <h2 className="content-subhead">Rozgrywka w OBIERANEGO</h2>
                    <p>
                        Rozpoczyna si, gdy jeden z graczy obierze kolor atutowy w licytacji. Oznajmia on wszystkim: "OBRANE" i podaje obrany kolor atutowy. Mona si te umwi, e nie musi podawa od razu koloru atutowego - odkada wtedy kart w tym kolorze zakryt na bok, aby mona byo potem ustali co wybra (z tego wynika, i musi mie przynajmniej jedn kart w obranym kolorze).
                    </p>
                    <p>
                        Gdy rozdajcy rozda reszt kart rozpoczyna si kolejna licytacja. Gracze mog teraz wybra rozgrywk w <a href="#mizerka" className="pure-menu-link underline">MIZERK</a> lub w <a href="#druh" className="pure-menu-link underline">DRUHA</a>. Pierwszy ma moliwo wyboru gracz, ktry obra kolor atutowy, jeli jednak nie wybiera adnej z tych rozgrywek pyta: "Czy kto co ma?". Teraz pierwszestwo w wyborze Fortan.<br />
                        Jeeli dwch graczy chce gra w MIZERK, to pierwszestwo ma pierwszy z nich, gdy jednak jeden wybierze MIZERK a drugi DRUHA, to DRUH ma pierwszestwo. Jeli adna z tych dwch rozgrywek nie zostanie wybrana obierajcy moe gra w OBIERANEGO.
                    </p>
                    <p>
                        Jeli gracz obierajcy stwierdzi, i nie jest w stanie wygra, moe "zrzuci" karty i przegrywa wtedy za 2pkt. Jeli chce gra, mwi: "PYTAM SIE" (i wtedy wanie podaje kolor atutowy, jeeli wczeniej tego nie zrobi). W odpowiedzi pozostali gracze mog odpowiedzie "GRAJ" ("PCHAJ SI") lub odpowiedzie KONTR (jeli uwaaj, e gracz nie wygra). W tej sytuacji obierajcy, na zmian z pozostaymi graczami, moe si licytowa, dajc kolejno RE-KONTR, SUP, MORT, LIBER-MORT, itd., podbijajc w ten sposb stawk gry za kadym razem dwukrotnie.<br />
                        Gdy (w kocu :) licytacja si zakoczy, obierajcy rozpoczyna gr.<br />
                        Gra toczy si tak, jak przy grze w 66 bez stosu: naley dawa do koloru, przebija, mona meldowa. Przeciwnicy obierajcego graj wsplnie (przeciwko niemu), oczywicie nie widzc swoich kart, zbierajc karty na jedn "kupk". Gdy kto ugra 66 punktw, zgasza to i wygrywa (musi by wtedy przy sztychu, tzn. musi wzi lew). Jeeli nikt nie ugra 66 punktw, wygrywa ten, ktry wemie ostatni lew.
                    </p>
                    <p>
                        Punktacja jak w 66 dla dwch osb:
                    </p>
                    <ul>
                        <li>bez sztycha (gdy przeciwnicy nie zdobd adnej lewy): 3pkt;</li>
                        <li>bez wody (gdy przeciwnicy zdobd mniej, ni 33 punkty): 2pkt;</li>
                        <li>z wod gdy przeciwnicy zdobd ponad 33 punkty): 1pkt;</li>
                    </ul>
                    <p>
                        Oczywicie, gdy gra toczy si z KONTR, RE-KONTR, itd. punkty s kolejno podwajane.<br />
                        Karty do nastpnej gry rozdaje obierajcy.
                    </p>
                    
                    <a name="mizerka"></a>
                    <h2 className="content-subhead">Rozgrywka w MIZERK</h2>
                    <p>
                        W rozgrywce tej zmienia si starszestwo kart, ktre jest tu nastpujce: A, K, D, W, 10, 9.<br />
                        Polega ona na tym, i gracz ktry si na ni zdecydowa, <strong>NIE MOE</strong> wzi ani jednej lewy (sztycha). Jeli to osignie - wygrywa, w przeciwnym wypadku przegrywa.<br />
                        Zaraz przed rozgrywk, przeciwnicy mog mu da KONTR, on moe odpowiedzie, itd. (jak przy grze w <a href="#wlasciwy" className="pure-menu-link underline">OBIERANEGO</a>). Stawka wtedy kolejno si podwaja.<br />
                        Gr rozpoczyna gracz grajcy w MIZERK. Zasady jak przy grze w 66 dla dwch osb bez stosu.<br />
                        Nie ma koloru atutowego.
                    </p>
                    <p>
                        Za rozgrywk w MIZERK otrzymuje si 4pkt (oczywicie z KONTR, RE, itd. zostaje ona odpowiednio podwojona).<br />
                        Karty do nastpnej gry rozdaje grajcy w MIZERK.
                    </p>

                    <a name="druh"></a>
                    <h2 className="content-subhead">Rozgrywka w DRUHA</h2>
                    <p>
                        Starszestwo figur jak przy normalnej grze.<br />
                        Zasady takie jak przy grze w <a href="#mizerka" className="pure-menu-link underline">MIZERK</a>, z tym e <strong>trzeba</strong> wzi wszystkie lewy (sztychy).<br />
                        Za rozgrywk w DRUHA otrzymuje si 5pkt (oczywicie odpowiednio podojone przy grze z KONTR, RE, itd.).<br />
                        Karty do nastpnej gry rozdaje , ten ktry wybra gr w DRUHA.
                    </p>

                    <h2 className="content-subhead">Zakoczenie</h2>
                    <p>
                        Caa gra w OBIERANEGO toczy si zazwyczaj na czas. Gracze dowolnie okrelaj sobie jej dugo.
                    </p>

                    <h1 className="content-subhead main-header">Obierany dla czterech osb</h1>

                    <h3>Wariant I</h3>
                    <p>
                        Gra toczy si tak, jak dla trzech osb, z tym e rozdajcy nie uczestniczy w rozgrywce, ale rwnie zyskuje lub traci punkty solidaryzujc si zawsze z wikszoci przy grze w OBIERANEGO, DRUHA lub MIZERK. Przy grze w WARSZAW zawsze wygrywa.
                    </p>
                    <p>
                        Karty do nastpnej gry rozdaje nastpna osoba, a nie jak przy grze w trzy osoby, ten ktry prowadzi rozgrywk.

                    </p>
                    <h3>Wariant II (Brydowy)</h3>
                    <p>
                        Gra toczy si w parach (jak w brydu gracze z tej samej pary siedz naprzeciwko siebie).<br />
                        Rozdajcy daje po cztery karty, a po licytacji rozdaje pozostae.<br />
                        Kada para gra wsplnie (wsplnie zdobywajc punkty).<br />
                        Przy tym wariancie nie gra si w MIZERK ani w DRUHA (chocia jeli kto chce to moe - naley to ustali przed gr).<br />
                        Pozostae zasady tak, jak przy grze z trzema osobami.
                    </p>
                </div>
            </div>
        );
    }
}



