import { basePath } from '../utils';
import React from 'react';


export default class Hejolek extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Hejoek',
            url: basePath() + '/hejolek',
            players: [2, 3, 4, 5, 6, 7, 8],
            cards: 'Dwie standardowe talie kart (2 x 52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Informacje wstpne</h2>
                    <p>
                        Hejhoek jest gr w karty opracowan 4 kwietnia 1998 roku przez Pawa Goleniowskiego i Magorzat Dulk.<br />
                        Do gry uywamy dwch penych talii kart z dokerami (108 kart). Liczba graczy - od dwch do czterech. Rozdajemy po 11 kart na gracza. Nastpnie wykadamy pi na st koszulk do blatu - s to tzw. karty odkryte. Pozostae karty odkadamy na bok koszulkami do gry (karty zakryte).
                    </p>

                    <h2 className="content-subhead">Klucze</h2>
                    <p>
                        Podstawowy i jedyny ukad kart w hejhoku to klucz. W hejhoku wyrniamy tylko dwa kolory kart: czerwony (kier i karo) oraz czarny (pik i trefl). Kluczem nazywamy dwie lub wicej kart, tego samego koloru uoonych kolejno z jedn kart odstpu. Dziki temu karty w kluczu s albo tylko parzyste, albo tylko nieparzyste. Oto przykady kluczy:
                    </p>
                    <ul>
                        <li>2 pik i 4 pik</li>
                        <li>7 karo, 9 kier i Walet karo</li>
                        <li>10 trefl, Dama trefl i As pik</li>
                        <li>As karo, 3 kier i 5 kier</li>
                        <li>Walet trefl i Krl pik</li>
                    </ul>
                    <p>
                        As moe suy zarwno jako 1 (karta nisza od 2) jak i normalny As, czyli karta wysza od Krla. Doker moe zastpi dowoln kart w kluczu. W kluczu liczba dokerw nie moe przekroczy poowy liczby kart.
                    </p>

                    <h2 className="content-subhead">Moliwe ruchy</h2>
                    <p>
                        W hejhoku mona wykonywa trzy rodzaje ruchw. W jednej turze gracz moe wykonywa dowoln ilo wszystkich rodzajw ruchw. Oto moliwoci ruchu:
                    </p>
                    <ul>
                        <li>
                            <strong>Wyoy klucz</strong> Jezeli gracz posiada na rce klucz lub par kluczw moe wyoy je na st. Klucze takie nie nale wtedy do nikogo i mona na nich wykonywa dwa pozostae rodzaje ruchw.
                        </li>
                        <li>
                            <strong>Rozbudowa klucz</strong> Gracz moe dooy dowoln ilo kart do wyoonego ju na st klucza, jednak rozbudowany klucz musi by prawidowy, tzn. jednakowego koloru i albo tylko parzysty albo tylko nieparzysty.
                        </li>
                        <li>
                            <strong>Przybi klucz</strong> Klucz ju wyoony moe zosta przybity przez inny klucz bdcy dotychczas na rc gracza. Klucz, ktrym przybijamy musi by przeciwnej barwy do klucza przybijanego oraz wypeni wszystkie luki pomidzy kartami wczeniej wyoonymi (np.: na stole jest ju wyoony klucz 3 kier, 5 karo i 7 kier; przybijamy kluczem 4 trefl i 6 pik; uzyskujemy taki ukad kart: 3 kier, 4 trefl, 5 karo, 6 pik i 7 kier). Liczba kart w kluczu ktrym przybijamy zaley od liczby kart w kluczu ktry jest ju wyoony i ktry przybijamy. Moliw liczb kart przybijajcych przedstawia ponisza tabela:
                            <table className="pure-table pure-table-horizontal">
                                <tbody>
                                    <tr className="pure-table-odd">
                                        <td>ilo kart w kluczu</td>
                                        <td>ilo kart przybijajcych</td>
                                    </tr>
                                    <tr>
                                        <td>2 karty</td>
                                        <td>od 2do 3</td>
                                    </tr>
                                    <tr>
                                        <td>3 karty</td>
                                        <td>od 2do 4</td>
                                    </tr>
                                    <tr>
                                        <td>4 karty</td>
                                        <td>od 3do 5</td>
                                    </tr>
                                    <tr>
                                        <td>5 kart</td>
                                        <td>od 4do 6</td>
                                    </tr>
                                    <tr>
                                        <td>6 kart</td>
                                        <td>od 5do 7</td>
                                    </tr>
                                    <tr>
                                        <td>7 kart</td>
                                        <td>od 6do 7</td>
                                    </tr>
                                </tbody>
                            </table>
                        </li>
                    </ul>

                    <h2 className="content-subhead">Przebieg gry</h2>
                    <p>
                        Gr rozpoczyna gracz po prawej stronie rozdajcego. Kolejka przechodzi na nastpnego gracza nie zgodnie z kierunkiem wskazwek zegara. Wykonuje on dowoln liczb ruchw opisanych w poprzednim akapicie. Jeeli przybije klucz, karty z tego klucza i karty ktrymi przybi klucz przechodz na jego konto punktowe. Po zakoczeniu gry karty z konta punktowego s przeliczane na punkty (patrz: nastpny akapit). Jeeli gracz koczy swoj tur i chce przekaza ruch nastpnemu graczowi to dociga karty z kupki na boku tak, aby znw mia 11 kart na rku, przy czym ma on prawo docign na samym pocztku jedn kart spord piciu odkrytych lecych na stole, a reszt spord kart zakrytych. Jeli zacznie ju dociga spord kart zakrytych to nie ma prawa wykorzysta odkryte pi kart. Po docigniciu kart, w przypadku gdy wykorzystano ktr z pitki odkrytych kart, naley dooy jedn, tak aby na stole znowu leao pi odkrytych kart. Gdy kupka si wyczerpie, na koniec tury dociga si po jednej karcie z kart odkrytych, a gdy i ich zabraknie gracz sygnalizuje koniec tury sowami "ho-hej". Nastpny gracz wykonuje kolejk wedug tych samych zasad.
                    </p>

                    <h2 className="content-subhead">Zakoczenie gry</h2>
                    <p>
                        Gra koczy si w dwch przypadkach: gdy wszyscy gracze pozbd si swych kart lub gdy wszyscy spasuj. Gra koczy si rwnie, gdy cz graczy pozbdzie si kart, a reszta spasuje. Gracz pasuje, wymawiajc sowo "hej-ho-hej". Aby spasowanie byo wane, wszyscy gracze po kolei musz spasowa. Jeeli chocia jeden gracz nie spasuje, tylko wykona ruch, pas jest niewany. Po zakoczeniu gry liczy si punkty za karty odoone na konto punktowe. Wartoci punktowe kadej karty przedstawia ponisza tabela:
                    </p>

                    <table className="pure-table pure-table-horizontal">
                        <tbody>
                            <tr className="pure-table-odd">
                                <td>rodzaj karty</td>
                                <td>ilo punktw</td>
                            </tr>
                            <tr>
                                <td>od 2 do 5</td>
                                <td>1 punkt</td>
                            </tr>
                            <tr>
                                <td>od 6 do 10</td>
                                <td>2 punkty</td>
                            </tr>
                            <tr>
                                <td>Walet</td>
                                <td>3 punkty</td>
                            </tr>
                            <tr>
                                <td>Dama</td>
                                <td>4 punkty</td>
                            </tr>
                            <tr>
                                <td>Krl</td>
                                <td>5 punktw</td>
                            </tr>
                            <tr>
                                <td>As i Doker</td>
                                <td>3 punkty</td>
                            </tr>
                        </tbody>
                    </table>

                    <p>
                        Gr wygrywa gracz, ktry zdobdzie najwicej punktw.
                    </p>
                </div>
            </div>
        );
    }
}



