import { basePath } from '../utils';
import React from 'react';


export default class Kuku extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Kuku',
            desc: 'gra dla 3 lub wicej osb',
            url: basePath() + '/kuku',
            players: [3, 4, 5, 6, 7, 8],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Gracze i karty</h2>
                    <p>
                        Gra dla 3 lub wicej osb.<br />
                        Przy grze od 3 do 7 osb uywa si 24 kart, przy wikszej liczbie graczy mona gra tali 52 kart.<br />
                        Kolory i starszestwo figur nie maj znaczenia.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Kady z graczy otrzymuje po 3 karty, a rozdajcy 4. Reszta kart odkadana jest na stosie zakrytym. Rozdajcy rozpoczyna gr. Oddaje jedn ze swoich kart nastpnemu graczowi po swojej lewej stronie. Tamten j bierze, a nastpnie oddaje jedn ze swoich kart graczowi nastpnemu (moe odda t kart co sam dosta) i tak kolejka posuwa si zgodnie z ruchem wskazwek zegara.
                    </p>
                    <p>
                        W momencie, kiedy gracz oddaje czwart kart z rki nastpnemu graczowi, a na rku pozostaj mu trzy karty tej samej wartoci, moe (chocia nie musi - moe gra dalej) powiedzie: "Kuku!", i pokazujc wszystkim karty skoczy swj udzia w rozdaniu.
                    </p>
                    <p>
                        W sytuacji, gdy jaka karta okry kolejk, gracz ktry otrzyma j ponownie moe j wymieni, odkadajc j obok stosu i pobierajc jego wierzchni kart. Jeli karty na stosie si skocz, jego miejsce zastpuje drugi stos, na ktry odkadane byy wymieniane karty. Stos ten nie jest tasowany, i kolejno kart jest nie naruszana (ostatnia karta na nim pooona bdzie pierwsz kart z niego pobran).
                    </p>
                    <p>
                        Gra jest kontynuowana do momentu, a z kartami "bez kuku" zostanie tylko jeden gracz i to on przegrywa rozdanie. Jeli na koniec wystpi taka sytuacja, e pozostao dwch ostatnich graczy i jeden z nich oddajc kart ma kuku, to drugi, biorc j, moe rwnie powiedzie kuku i odoy czwart kart na st.<br />
                        Gra jest wtedy nierozstrzygnita.
                    </p>
                </div>
            </div>
        );
    }
}



