import { basePath } from '../utils';
import React from 'react';


export default class Pikieta extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Pikieta',
            desc: 'gra dla 2 osb',
            url: basePath() + '/pikieta',
            players: [2],
            cards: 'Pikietowa talia kart (32 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Jedna z najstarszych gier karcianych pochodzca z Francji i wedug niektrych rde znana tam ju pod koniec XIV wieku jako ronfl i cent. Ide gry stworzy rzekomo Etienne de Vignolles (kawaler la Hire), towarzysz walk Joanny d'Arc. Pikieta rozpowszechnia si we Francji w XV wieku; szczyt popularnoci osigna w XVIII wieku, gdy staa si znana w caej Europie. Obowizujce do dzisiaj zasady gry i jej zapisu ustali Charles Picquet z Troyes. Nazwa gry pochodzi od jego nazwiska.<br />
                        W Polsce pikieta w XIX wieku uwaana ju bya za gr dawn. Znano rne warianty gry: rummelpikiet, pikiet z rubikonem, rozbjnick, normask, cesarsk, pisan, "a la poule". W ZSRR pikieta znana bya jako gra w "kogucika".
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>
                        Gra dla 2 osb.
                    </p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Talia tzw. pikietowa (32 karty, od asw do sidemek).<br />
                        Warto punktowa kart: as - 11, figura i dziesitka - 10, blotki - wg iloci oczek.<br />
                        Nie ma koloru atutowego.

                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie. Rozdaje si po dwie karty. Kady z graczy otrzymuje 12 kart. Pozostae osiem kart rozdajcy rozdziela na dwa zakryte talony: piciokartowy (tzw. talon przeciwnika) i trzykartowy (tzw. talon rozdajcego).
                    </p>
                    <p>
                        Jeeli ktry z graczy po rozdaniu kart ma od razu na rku tzw. "carte blanche" (zestaw kart bez figur, tzn. bez krla, bez damy i bez waleta), otrzymuje od razu 10 punktw premii.

                    </p>

                    <h2 className="content-subhead">Wymiana</h2>
                    <p>
                        Wymian kart rozpoczyna przeciwnik rozdajcego. Ma on obowizek wymieni co najmniej jedn kart z rki na jedn kart z talonu. Moe wymieni do piciu kart (cay talon). Jeli wymienia mniej ni pi kart, pozostajce na stole po wymianie karty swego talonu ma prawo podejrze.
                    </p>
                    <p>
                        Rozdajcy nie ma obowizku wymiany. Moe wymieni tyle kart ile pozostao na stole (trzy ze swego talonu oraz te, ktre w swoim talonie pozostawi przeciwnik). Karty nie wymienione ma prawo po wymianie podejrze (poza tymi, ktre zrzuci przeciwnik).
                    </p>

                    <h2 className="content-subhead">Licytacja</h2>
                    <p>
                        Licytacj rozpoczyna przeciwnik rozdajcego.<br />
                        Licytacja polega na kolejnym zgaszaniu posiadanych w rku dugich kolorw, sekwensw i kompletw.<br />
                        Ten z graczy, ktry ma najduszy kolor, otrzymuje za niego punkty - tyle punktw, ile kart w kolorze. Jeli obydwaj gracze maj kolory tej samej dugoci, wygrywa w licytacji i zdobywa punkty za kolor ten z graczy, ktrego kolor ma wysz warto punktow (wiksz sum oczek wszystkich kart w kolorze).
                    </p>
                    <p>
                        Nastpnie licytuje si sekwensy. Maj one swoje specjalne nazwy, wywodzce si z aciny:
                    </p>
                    <ul>
                        <li>oktawa - sekwens omiokartowy (peny kolor)</li>
                        <li>septa - sekwens siedmiokartowy</li>
                        <li>seksta - sekwens szeciokartowy</li>
                        <li>kwinta - sekwens piciokartowy</li>
                        <li>kwarta - sekwens czterokartowy</li>
                        <li>terc - sekwens trzykartowy</li>
                    </ul>

                    <p>
                        Ten z graczy, ktry ma najduszy sekwens, otrzymuje za niego punkty.<br />
                        Za oktaw - 18, sept - 17, sekst - 16, kwint - 15, kwart - 14, a za terc - 13 punktw.<br />
                        Jeli obydwaj gracze maj sekwensy tej samej dugoci, wygrywa w licytacji ten z nich, ktrego sekwens rozpoczyna si od wyszej karty. Najstarszy sekwens, od asa, nosi dodatkow nazw "major" (np. terc major, septa major). Ten z graczy, ktry wygra licytacj sekwensw, zdobywa punkty nie tylko za sekwens wygrywajcy, lecz i za wszystkie inne, sabsze sekwensy w rku.
                    </p>
                    <p>
                        Nastpnie licytuje si komplety: trzy lub cztery jednakowej wysokoci karty w rku.<br />
                        Ten z graczy, ktry ma najstarszy komplet, otrzymuje punkty.<br />
                        Za komplet czterokartowy 14 punktw a za komplet trzykartowy 3 punkty.<br />
                        Kady komplet czterokartowy jest starszy od kadego kompletu trzykartowego.<br />
                        Jeli komplety s tej samej dugoci, w licytacji wygrywa komplet zoony z wyszych kart.<br />
                        Ten z graczy, ktry wygra licytacj kompletw, zdobywa punkty nie tylko za komplet wygrywajcy, lecz i za wszystkie inne, sabsze komplety w rku.
                    </p>
                    <p>
                        W licytacji kolorw, sekwensw i kompletw bior udzia wszystkie karty w rku. Kada karta moe by jednoczenie czci kolory, sekwensu i kompletu.
                    </p>
                    <p>
                        Jeli w licytacji wziy udzia wszystkie karty gracza (kada z nich bya czci koloru, sekwensu lub kompletu), gracz otrzymuje 20 punktw premii za tzw. "carte rouge".<br />
                        Jeli w licytacji ktry z graczy zdobdzie w sumie 30 punktw lub wicej, przeciwnik natomiast nie zdobdzie ani jednego punktu, wygrywajcy licytacj zdobywa dodatkowo 60 punktw premii.
                    </p>

                    <h2 className="content-subhead">Rozgrywka</h2>
                    <p>
                        Pierwszy wychodzi przeciwnik rozdajcego.<br />
                        Nie ma koloru atutowego.<br />
                        O wziciu lewy decyduje starszestwo kart.<br />
                        Jest obowizek dorzucania do koloru.<br />
                        Nie ma obowizku przebijania starsz kart.
                    </p>
                    <p>
                        Za kade wyjcie, ten z graczy, ktry wychodzi, otrzymuje jeden punkt (1pkt za wyjcie, nie za wzicie lewy). Za ostatnie wyjcie zdobywa si dwa punkty.
                    </p>
                    <p>
                        Rozgrywk wygrywa ten z graczy, ktry wemie wicej ni poow lew. Za wygran rozgrywk (za sidm lew) otrzymuje si 10 punktw.
                    </p>
                    <p>
                        Jeli w toku rozgrywki ktry z graczy zdobdzie 30 lub wicej punktw (cznie z poprzednio otrzymanymi punktami za licytacj), przeciwnik natomiast nie ma ani jednego punktu, wygrywajcy otrzymuje dodatkowo 30 punktw premii. Premia ta oczywicie nie obowizuje, gdy gracz otrzyma ju uprzednio premi w licytacji za przekroczenie 30 zdobytych punktw.
                    </p>
                    <p>
                        Obliczenie otrzymywanych punktw prowadzi si zarwno w licytacji, jak i w rozgrywce na bieco.
                    </p>
                    <p>
                        Wygrywa ten, kto w sumie, w licytacji i w rozgrywce, zdoby wicej punktw. Wygrywajcemu dolicza si 100 punktw premii.
                    </p>
                    <p>
                        Pena partia pikiety skada si z czterech rozda; kady z graczy rozdaje karty dwukrotnie.
                    </p>

                    <div className="example">
                        <div className="title">Partia przykadowa</div>
                        <div className="content">
                            <p>Rozdawa gracz A.</p>
                            <p>B ma na rku:</p>
                            <ul>
                                <li>w pikach - A;</li>
                                <li>w kierach - A, K, D, 10, 8, 7;</li>
                                <li>w karach - A, 10, 9;</li>
                                <li>w treflach - 9, 8;</li>
                            </ul>

                            <p>A ma na rku:</p>
                            <ul>
                                <li>w pikach - D, W, 10, 9, 8;</li>
                                <li>w kierach - W, 9;</li>
                                <li>w karach - K, D;</li>
                                <li>w treflach - A, W, 10</li>
                            </ul>

                            <p>
                                B wymienia cztery karty, zrzucajc 9 <span className="club"></span> i 8 <span className="club"></span> oraz 10 <span className="diamond"></span> i 9 <span className="diamond"></span>.<br />
                                Uzyskuje z talonu K <span className="club"></span> i 7 <span className="club"></span>, 7 <span className="spade"></span> i 7 <span className="diamond"></span>. Podglda kart pozosta w talonie - jest to W <span className="diamond"></span>.
                            </p>
                            <p>
                                A wymienia dwie karty, zrzucajc W <span className="heart"></span> i 9 <span className="heart"></span>. Uzyskuje z talonu K <span className="spade"></span> i 8 <span className="diamond"></span>. Podglda kart pozosta w talonie przeciwnika (W <span className="diamond"></span>) i kart pozosta we wasnym talonie - jest to D <span className="club"></span>.
                            </p>
                            <p>
                                Licytacj kolorw wygrywa A - jego szeciokartowy kolor pikowy jest wart 57 oczek; szeciokartowy kolor kierowy u B jest wart 56 oczek. A otrzymuje 6 punktw za kolor.
                            </p>
                            <p>
                                Licytacj sekwensw wygrywa take A - jego seksta pikowa przynosi mu 16 punktw. A zdoby ju 22 punkty.
                            </p>
                            <p>
                                Licytacj kompletw wygrywa B - jego cztery sidemki przynosz mu 14 punktw, dodatkowo za trzy asy otrzymuje 3 punkty.
                            </p>
                            <p>
                                W momencie przystpienia do rozgrywki A ma 22 punkty, B - 17 punktw.
                            </p>
                            <p>
                                Oto kolejne lewy rozgrywki i w nawiasach iloci punktw zdobytych przez graczy:
                            </p>
                            <p>
                                A <span className="heart"></span>, 10 <span className="club"></span> (18 - B; 22 - A);<br />
                                K <span className="heart"></span>, W <span className="club"></span> (19 - B; 22 - A);<br />
                                D <span className="heart"></span>, 8 <span className="diamond"></span> (20 - B; 22 - A);<br />
                                10 <span className="heart"></span>, 8 <span className="spade"></span> (21 - B; 22 - A);<br />
                                7 <span className="heart"></span>, 10 <span className="spade"></span> (23 - B; 22 - A);<br />
                                A <span className="diamond"></span>, D <span className="diamond"></span> (34 - B, bo to ju 7 lewa; 22 - A);<br />
                                7 <span className="diamond"></span>, K <span className="diamond"></span> (35 - B; 22 - A);<br />
                                K <span className="spade"></span>, 7 <span className="spade"></span> (35 - B; 23 - A);<br />
                                D <span className="spade"></span>, A <span className="spade"></span> (35 - B; 24 - A);<br />
                                7 <span className="club"></span>, A <span className="club"></span> (36 - B; 24 - A);<br />
                                W <span className="spade"></span>, K <span className="club"></span> (36 - B; 26 - A).
                            </p>
                            <p>
                                Premia za wygran (100 punktw) dla B.<br />
                                B uzyska cznie 136 punktw, A - 26 punktw.<br />
                                A przegra wic 110 punktw
                            </p>
                        </div>
                    </div>
                </div>
            </div >
        );
    }
}



