import { basePath } from '../utils';
import React from 'react';
import oklahomaImg from '../images/oklahoma.gif';


export default class RemikGin extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Remik Gin',
            url: basePath() + '/remikgin',
            desc: 'gra dla 2 osb',
            players: [2],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Remik jest grup gier karcianych dla dwu lub wicej osb; gra ta w rnych formach znana jest od dziewidziesitych lat XIX wieku, wywodzi si z meksykaskiej gry "con quien", zwanej w krajach anglosaskich "cooncan". Od 1941 roku remik w rnych wariantach jest najbardziej popularn gr karcian w USA, znan tam jako gin rummy, tunk, kaluki, carousel, panguingue, zioncheck, michigan, pif-paf, szanghaj i inne.<br />
                        Najbardziej popularnym wariantem remika jest gra dla dwch osb: remik gin (opisany jako pierwszy). Zasady do niego opracowa w 1909 roku Elwood T. Baker z Brooklynu.<br />
                        Poniej opisanych jest te kilka innych wariantw remika:
                        <ul>
                            <li><a className="pure-menu-link underline" href="#bridge">Remi brid</a></li>
                            <li><a className="pure-menu-link underline" href="#oklahoma">Remik Oklahoma</a></li>
                            <li><a className="pure-menu-link underline" href="#500">Remik 500</a></li>
                        </ul>
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 2 osb.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia kart (52 karty).<br />
                        Starszestwo kart - od krla do asa, ktry jest najnisz kart.<br />
                        Nie ma koloru atutowego.
                    </p>

                    <h2 className="content-subhead">Zasady gry</h2>
                    <p>
                        Pierwszego rozdajcego wybiera si przez losowanie. W nastpnych rozdaniach karty rozdaje zawsze ten z graczy, ktry wygra poprzednie rozdanie.<br />
                        Rozdajcy daje kademu (sobie i przeciwnikowi) po dziesi kart, rozdajc je po jednej. Dwudziest pierwsz kart odsania, kadc na stole. Karta ta tworzy stos odkryty i stanowi pierwsze wyjcie w rozgrywce, do przeciwnika rozdajcego. Jeli ten nie bierze wyoonej karty, ma prawo wzi j rozdajcy, zrzucajc kart z rki. Ten przywilej dotyczy tylko pierwszej odsonitej karty.
                    </p>
                    <p>
                        Celem gry jest dla kadego z graczy takie zestawienie kart na rku w odpowiednie ukady, aby mc zameldowa remika (gin) lub "zastuka" ("zapuka") i osign maksymaln ilo punktw w zapisie - w kolejnych rozgrywkach
                    </p>
                    <p>
                        Istniej dwa rodzaje ukadw: komplety i sekwensy. Kompletem s trzy lub cztery karty tej samej wysokoci; sekwensem s trzy lub wicej kolejnych kart w tym samym kolorze(np. as, dwjka, trjka karo; szstka, sidemka, semka, dziewitka trefl). As, krl i dama nie s sekwensem, nie s nim rwnie dwjka, as i krl.
                    </p>
                    <p>
                        Komplet moe by najwyej czterokartowy, sekwens natomiast moe by (cho jest to rzadkie) nawet dziesiciokartowy.
                    </p>
                    <p>
                        Warto punktowa kart dla obliczenia wartoci rki i wysokoci wygranej w poszczeglnych rozdaniach wynosi:
                    </p>
                    <ul>
                        <li>
                            za karty w kompletach lub sekwensach - 0pkt
                        </li>
                        <li>
                            za lune figury - po 10pkt
                        </li>
                        <li>
                            za lune blotki - ilo punktw w wysokoci danej blotki
                        </li>
                    </ul>
                    <p>
                        Gracz, ktry ma w rku dziesi lub mniej punktw, moe "zastuka", to jest po dobraniu karty ze stosu zakrytego lub odkrytego zrzuci jedn kart i karty z rki wyoy na st. Wwczas jego przeciwnik wykada na st posiadane przez siebie w rku ukady kart, ma take prawo dooy ze swoich lunych kart te, ktre pasuj do ukadw wyoonych przez przeciwnika (np. czwartego waleta do wyoonych trzech waletw; sidemk i szstk pik do wyoonego sekwensu pitki, czwrki i trjki pik). Nie wolno dokada kart do kart lunych, na ktre "stukano". Np. stukajcy wyoy ukady kart i dwjk kierow - stuka z dwch punktw. Przeciwnik stukajcego ma w rku lune asa i trjk kierow - nie wolno mu dooy ich do dwjki, musi je policzy jako swoje punkty ujemne.
                    </p>
                    <p>
                        Jeli stukajcy stuka na mniejsz ilo punktw ni suma punktw kart lunych jego przeciwnika po wyoeniu, zapisuje sobie rnic punktw na swoj korzy. Jeli okae si, e przeciwnik stukajcego ma po wyoeniu mniej punktw ni stukajcy, przeciwnik stukajcego zapisuje sobie na swoj korzy rnic punktw oraz 25 punktw premii.
                    </p>
                    <p>
                        Jeli gracz wykada z rki wszystkie dziesi kart w kompletnych ukadach (ma remika, czyli gin), zapisuje sobie na swoj korzy sum punktw ujemnych (kart lunych) u przeciwnika oraz 25 punktw premii. Przeciwnik nie ma prawa dokadania swoich kart lunych do wyoonych kombinacji, choby do nich pasoway.
                    </p>
                    <p>
                        W kolejnych rozdaniach gra toczy si do chwili, gdy jeden z graczy osignie lub przekroczy sto punktw w zapisie - wwczas nastpuje ostateczne rozliczenie gry.
                    </p>
                    <p>
                        Jeeli na stosie zakrytym pozostay ju tylko dwie karty, a aden z graczy nie zastuka ani nie zdoby remika, rozgrywka jest remisowa; rozdaje si karty powtrnie.
                    </p>
                    <p>
                        Przy ostatecznym rozliczeniu gry gracz, ktry wygra, otrzymuje za wygran 100 punktw premii oraz dodatkowo po 25 punktw premii za kad z rozgrywek wygranych przez przeciwnika.
                    </p>

                    <p>
                        Przykadowy zapis
                    </p>
                    <table className="pure-table">
                        <tbody>
                            <tr className="pure-table-odd">
                                <td>Gracz A</td>
                                <td>Gracz B</td>
                            </tr>
                            <tr>
                                <td>13</td>
                                <td>49</td>
                            </tr>
                            <tr>
                                <td>30</td>
                                <td>50</td>
                            </tr>
                            <tr>
                                <td>55</td>
                                <td></td>
                            </tr>
                            <tr>
                                <td>87</td>
                                <td></td>
                            </tr>
                            <tr>
                                <td>118</td>
                                <td></td>
                            </tr>
                        </tbody>

                    </table>
                    <p>
                        Pierwsz rozgrywk wygra gracz A, stukajc z szeciu. Gracz B mia 19pkt, a wic wynik: 13 dla A.<br />
                        W drugiej rozgrywce gracz B mia remika, gracz A - 24pkt, a wic B wygra 49pkt (24+25 za remika).<br />
                        W trzeciej rozgrywce gracz B stuka z omiu, gracz A mia dziewi punktw - a wic 1 punkt dla gracza B.<br />
                        W czwartej rozgrywce gracz A stuka z 10; gracz B mia 27 - a wic A wygra 17pkt.<br />
                        W pitej rozgrywce gracz B stuka z 4pkt; gracz A mia take 4pkt - a wic gracz A zapisuje sobie premiowe 25pkt. (Nie ma rnicy punktowej).<br />
                        W szstej rozgrywce gracz A zastuka z siedmiu punktw, gracz B mia 39 - wygrywa A zdobywajc 32pkt.<br />
                        W sidmej rozgrywce gracz A wyoy remika, gracz B za mia 6pkt, a wic A zarobi 31pkt (6+25 za remika), przekraczajc sto punktw w zapisie i tym samym wygrywajc ca gr.
                    </p>
                    <p>
                        Teraz nastpuje ostateczne rozliczenie:<br />
                        gracz A (wygrany) ma w zapisie 118pkt; dopisuje sobie premi 100pkt za wygran gr oraz premi punktow za rozgrywki. Gracz B wygra dwie rozgrywki, gracz A - pi rozgrywek, a wic o trzy rozgrywki wicej ni przeciwnik. Za kad z tych trzech rozgrywek otrzymuje teraz po 25 punktw premii, a wic 75pkt. czna wygrana gracza A wynosi wic 118 (zapis) + 100 (partia) + 75 (premia za rozgrywki) - 50 (zapis B), czyli 243 punkty.
                    </p>
                </div>

                <div className="content">
                    <a name="bridge"></a>
                    <h1 className="content-subhead main-header">Remi brid</h1>

                    <p>
                        Wariant gry.<br />
                        Dwie pene talie kart (108 kart).<br />
                        Starszestwo kart od asa do dwjki. Doker zastpuje kad potrzebn kart w ukadach.
                    </p>
                    <p>
                        Kady z grajcych otrzymuje po 10 kart, sam rozdajcy - 11. Rozdajcy wykonuje pierwsz zrzutk. Gracze kolejno dokupuj karty ze stosu zakrytego lub odkrytego i kolejno zrzucaj po jednej karcie.
                    </p>
                    <p>
                        Ukady kart s identyczne jak w remiku ginie, tj. komplety i sekwensy. Dwa dodatkowe ukady kart to kolor (wszystkie karty w jednym kolorze) oraz figury (wszystkie karty wysze od dziesitki). Mona przed rozdaniem gry umwi si, e istnieje jeszcze jeden ukad szczeglny, tzw. kolor kolejny lub peny sekwens (wszystkie karty kolejne i w jednym kolorze).
                    </p>
                    <p>
                        Puka wolno najwyej z piciu punktw. Po wyoeniu kart przez pukajcego jego przeciwnicy nie maj prawa dokadania swoich kart lunych do jego kombinacji, maj natomiast prawo do wymiany jeszcze jednej karty z ktrego ze stosw i wyoenia ewentualnie utworzonych w wyniku wymiany ukadw kart.
                    </p>
                    <p>
                        Po wyoeniu remika, koloru lub figur przeciwnicy nie maj prawa do dodatkowej kolejki w wymianie kart.
                    </p>
                    <p>
                        Gra toczy si w oparciu o umwion przed rozpoczciem gry stawk do chwili, gdy wszyscy gracze oprcz jednego osign sto punktw ujemnych.<br />
                        Przed rozpoczciem gry, kady z grajcych wpaca do puli umwion stawk. W toku rozgrywki wykadajcy remika otrzymuje od wszystkich swoich przeciwnikw stawk, wykadajcy peny sekwens - cztery stawki od kadego. Pukajcy otrzymuje od przeciwnikw po p stawki; drugie p stawki wpacaj oni do puli.
                    </p>
                    <p>
                        Zasady zapisu s nastpujce:<br />
                        wszystkie lune figury liczy si po 10pkt, blotki wedug iloci oczek, lunego dokera - 20pkt.<br />
                        Zapis prowadzi si oddzielnie dla kadego gracza, notujc mu jako punkty minusowe wszystkie kolejne punkty utracone w rozgrywkach oraz punkty, na ktre stuka.<br />
                        za wyoenie remika - 10pkt plusowych (zmniejsza zapis minusowy);<br />
                        za kolor i figury - 25pkt plusowych;<br />
                        za peny sekwens - 40pkt plusowych.
                    </p>
                    <p>
                        Gracz, ktry przekroczy 100pkt w zapisie, wypada z gry. Moe do niej jeszcze wrci, jeli wpaci do puli trzy stawki; bierze wtedy udzia w dalszych rozgrywkach z t iloci punktw, ktr ma w zapisie gracz najbliszy granicy 100 punktw.
                    </p>
                    <p>
                        powraca do gry wolno wielokrotnie, dopki poza wygrywajcym dan rozgrywk choby jeden z graczy nie przekroczy granicy 100 punktw. Z chwil, gdy granic t przekrocz wszyscy gracze prcz jednego, wygrywa on parti, zabierajc pul.
                    </p>
                </div>

                <div className="content">
                    <a name="oklahoma"></a>
                    <h1 className="content-subhead main-header">Remik Oklahoma</h1>

                    <p>
                        Wariant remika.<br />
                        Dodatkowe zasady gry i zapisu s nastpujce:<br />
                        pierwsza odsonita karta wyznacza, do ilu maksymalnie punktw wolno zapuka w danej rozgrywce (rozdaniu); jeli jest to dziesitka lub figura - do 10, dziewitka - do 9, semka - 8, itd.; jeli kart odsonit jest as, w danej rozgrywce puka nie wolno, naley zakoczy j remikiem.<br />
                        Jeli odsonita karta jest koloru pikowego, wwczas zapis za dan rozgrywk ulega podwojeniu.
                    </p>
                    <p>
                        Zapis prowadzi si trzykrotnie (w trzech naraz podwjnych kolumnach) i gra toczy si do momentu osignicia przez ktrego z graczy umwionej z gry iloci punktw (zazwyczaj 150 lub 200) we wszystkich zapisach.
                    </p>
                    <p>
                        Na przykad w pierwszych siedmiu rozdaniach gra przebiega nastpujco:<br />
                        odsonito 6 kier; A zastuka z 4pkt, wygrywajc 18pkt - tyle zdobywa w pierwszym zapisie;<br />
                        odsonito 8 karo; A zastuka z 2pkt, wygrywajc 6pkt - tyle zdobywa w pierwszym i drugim zapisie;<br />
                        odsonito waleta trefl; A zastuka z 8pkt i wygra 11pkt - punkty te zdobywa we wszystkich trzech zapisach;<br />
                        odsonito 4 karo; A zastuka z 2pkt i przegra 26pkt (B mia tylko 1pkt na rku) - punkty wygrane B zdobywa w pierwszym zapisie;<br />
                        odsonito 7 pik; B zastuka z 7pkt i wygra 9pkt - poniewa zapis jest podwojony, B zdobywa po 18pkt w pierwszym i drugim zapisie;<br />
                        odsonito 8 kier; B zrobi remika i wygra 17pkt od A - wraz z premi za remika zdobywa wic 42pkt ju we wszystkich trzech zapisach;<br />
                        odsonito asa trefl; A zrobi remika i wygra od B 23pkt - wraz z premi za remika zdoby wic 48pkt we wszystkich trzech zapisach.
                    </p>
                    <p>
                        Po tych siedmiu rozgrywkach zapis rwnolegy przedstawia si nastpujco:
                    </p>

                    <div className="center-outer-container">
                        <div className="center-inner-container">
                            <img className="pure-img" src={oklahomaImg}></img>
                        </div>
                    </div>

                    <p>
                        W pierwszym zapisie prowadzi gracz B, w pozostaych dwch gracz A.<br />
                        Gdy w ktrym z zapisw jeden z graczy osignie wygrywajc ilo punktw, zapis ten koczy si, gra za toczy si dalej w kolejnych rozgrywkach i jej wyniki zapisuje si w dwch zapisach, potem w jednym, a zakoczone zostan wszystkie trzy zapisy i caa gra.
                    </p>
                    <p>
                        Punkty wygrane i przegrane oblicza si we wszystkich trzech zapisach cznie.
                    </p>
                </div>

                <div className="content">
                    <a name="500"></a>
                    <h1 className="content-subhead main-header">Remik 500</h1>
                    <div className="info">Wariant remika</div>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia kart (52 karty) dla dwch do czterech graczy. Gdy udzia w grze bierze od czterech do omiu graczy - dwie pene talie kart (104 karty).
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie. W nastpnych rozdaniach gracze rozdaj karty kolejno.
                    </p>
                    <p>
                        Rozdajcy rozdaje po 13 kart (przy dwch uczestnikach) lub po 7 (przy trzech lub wicej uczestnikach gry). Reszta kart pozostaje na stole w zakrytym talonie; wierzchni kart tego talonu odsania si - stanowi ona pierwsz kart odkrytego stosu. Jest ona te pierwszym wyjciem w grze do gracza po lewej stronie rozdajcego.
                    </p>
                    <p>
                        Gracz moe kart z otwartego stosu zabra (wwczas ma obowizek natychmiast kart wzit wyoy w jakim ukadzie na st) lub te wzi wierzchni kart zakrytego talonu i zrzuci jedn kart z rki na stos otwarty. Do gry przystpuje nastpny z graczy w kierunku ruchu wskazwek zegara.
                    </p>
                    <p>
                        W kolejce gry mona wzi do rki nie tylko wierzchni kart otwartego stosu, lecz take dowoln inn kart w tym stosie (kolejne zrzutki do otwartego stosu ukada si tak, by wszystkie karty byy widoczne) - wwczas jednak naley wzi do rki rwnie te karty, ktre le powyej ostatniej karty branej w otwartym stosie, i natychmiast wyoy na st w jakim ukadzie ostatni kart wzit ze stosu, stowarzyszon tylko z kartami z rki, lecz nie z innymi kartami ze stosu.
                    </p>
                    <p>
                        W kolejce gry po wziciu karty ze stosu lub z talonu gracz ma prawo wyoy z rki wszelkie inne posiadane ukady kart oraz dooy z rki na st poszczeglne karty pasujce do ju uprzednio wyoonych ukadw wasnych lub wspgraczy.
                    </p>
                    <p>
                        Celem gry jest dla kadego gracza zdobywanie w kolejnych rozdaniach maksymalnej iloci punktw za karty wyoone na st w odpowiednich ukadach lub dooone do ukadw ju istniejcych.<br />
                        Za kad figur - 10pkt. Za wszystkie inne karty - ilo punktw rwn wysokoci danej karty (dziesitka - 10pkt, as - 1pkt).<br />
                        As jest kart najmodsz. AKD nie jest sekwensem, natomiast A23 jest nim.<br />
                        Rachunek punktw zdobytych przez poszczeglnych graczy prowadzi si na bieco.
                    </p>
                    <p>
                        Gdy ktry z graczy zakoczy rozgrywk, pozbywajc si wszystkich kart z rki, pozostaym z graczy sum punktw wartoci kart w rku liczy si jako punkty minusowe (nawet jeli s to nie wyoone ukady).<br />
                        Nie ma premii za zakoczenie rozgrywki.
                    </p>
                    <p>
                        Ca gr wygrywa ten z graczy, ktry w zapisie pierwszy przekroczy 500pkt plusowych. Dane rozdanie rozgrywa si do koca. Jeli wicej ni jeden gracz przekroczy w zapisie 500pkt, ca gr wygrywa ten, ktry ma wicej punktw.
                    </p>
                </div>
            </div>
        );
    }
}



