import { basePath } from '../utils';
import React from 'react';


export default class Wojna extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Wojna',
            desc: 'gra dla 2 [3 lub 4] osb',
            url: basePath() + '/wojna',
            players: [2, 3, 4],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 2 [3 lub 4] graczy.</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Talia 52 kart.<br />
                        Starszestwo kart od asa do dwjki.<br />
                        Kolor nie ma znaczenia.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Rozdawane s wszystkie karty - kademu z graczy po 26.<br />
                        Gracze nie zagldaj do swoich kart, lecz kady trzyma je zakryte na swoim stosie.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Celem gry jest zdobycie wszystkich 52 kart.
                    </p>
                    <p>
                        Obaj gracze wykadaj grn kart ze swojego stosu na st, odkrywajc j (kolejno wyjcia nie ma znaczenia - mona si wczeniej umwi, e pierwszy wychodzi przeciwnik rozdajcego, a pniej ten co wzi karty). Gracz, ktry z obu kart pooy wysz, zabiera je i kadzie zakryte na spd swojego stosu. I znw obaj wykadaj karty, a zabiera je ten, ktry wyoy najwiksz, itd.
                    </p>
                    <p>
                        Jeli obie wyoone przez graczy karty bd tej samej wysokoci - jest WOJNA.<br />
                        Wyoone karty pozostaj na stole, a gracze wykadaj po jednej zakrytej karcie, a nastpnie po jeszcze jednej odkrytej (kadc je na poprzedni swoj kart). Wszystkie sze kart zabiera ten z graczy, ktrego druga wyoona odkryta karta bya wysza.<br />
                        Jeli znowu obie karty byy tej samej wysokoci, sytuacja powtarza si: gracze wykadaj po jednej zakrytej i jednej odkrytej karcie; i wykadaj tak do momentu, a ostatnie odkryte karty bd rnej wartoci. Wszystkie karty wyoone podczas wojny zabiera ten z graczy, ktrego ostatnia wyoona odkryta karta bya wysza.
                    </p>
                    <p>
                        Gra kontynuowana jest do momentu, a jeden z graczy zdobdzie wszystkie karty i, co za tym idzie, wygra gr.
                    </p>

                    <h2 className="content-subhead">Wojna dla trzech i czterech osb</h2>
                    <p>
                        Przy grze w trzy osoby rozdawanych jest 17 kart, a przy czterech - 13.
                    </p>
                    <p>
                        Wszyscy gracze kad na st po jednej odkrytej kacie z gry swojego zakrytego stosu, a zabiera wszystkie ten z graczy, ktry wyoy kart najwikszej wartoci.
                    </p>
                    <p>
                        W przypadku, gdy dwch lub wicej graczy pooy karty o takiej samej wysokoci (bez wzgldu na wysoko reszty kart) - jest wojna. Wszyscy gracze kad drug kart zakryt i trzeci odkryt. Jeli pomidzy dwoma lub wicej ostatnio odkrytymi kartami znw bdzie wojna, sytuacja si powtarza: wszyscy kad jedn zakryt i jedn odkryt kart na st. Wszystkie karty biorce udzia w wojnie zabiera ten z graczy, ktrego ostatnio pooona karta odkryta miaa najwysz warto.
                    </p>
                    <p>
                        Gr wygrywa ten, kto zdobdzie wszystkie karty.
                    </p>

                    <h2 className="content-subhead">Warianty</h2>
                    <p>
                        Przy grze w wicej ni dwie osoby mona gra tak, e:
                    </p>
                    <ul>
                        <li>
                            jeli jest wojna pomidzy dwoma graczami, lecz trzeci ma wysz kart od tych biorcych udzia w wojnie - wojny nie ma, a karty zabiera gracz, ktry wyoy t o najwyszej wartoci;
                        </li>
                        <li>
                            jeli jest wojna pomidzy dwoma graczami, a trzeci ma nisz kart od tych biorcych udzia w wojnie - wojna toczy si tylko pomidzy tymi dwoma graczami, i tylko ktry z nich moe wzi wszystkie karty;
                        </li>
                        <li>
                            jeli wojna jest pomidzy wiksz (ni 2) iloci graczy, wszyscy biorcy w niej udzia wykadaj najpierw jedn zakryt, a potem jedn odkryt kart; jeli w dalszym cigu ktre karty s rwnej wartoci, dalsza wojna toczy si tylko pomidzy tymi graczami, do ktrych one nale;
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}



