import { basePath } from '../utils';
import React from 'react';


export default class A358 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '3-5-8',
            name2: 'Trzy Pi Osiem',
            desc: 'gra dla 3 osb',
            url: basePath() + '/trzypiecosiem',
            players: [3],
            cards: 'Standardowa talia kart (52 karty)'
        }
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>
                        Gra dla 3 osb.
                    </p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia kart (52 karty).<br />
                        Starszestwo kart od Asa do dwjki.<br />
                        Istnieje zmienny kolor atutowy.<br />
                        Jest obowizek dorzucania do koloru.<br />
                        Nie ma obowizku przebijania starsz kart.<br />
                        Nie ma obowizku wyrzucenia atu w przypadku, gdy nie ma koloru wyjcia.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Rozdajcy wybierany jest przez losowanie. Karty rozdawane s po jednej zaczynajc od gracza siedzcego po lewej stronie rozdajcego.<br />
                        Kady z grajcych otrzymuje po 16 kart i dodatkowo cztery karty rozdane s do tzw. "musiku".
                    </p>
                    <p>
                        Gracz siedzcy po lewej stronie rozdajcego, z szeciu pierwszych otrzymanych kart wybiera rodzaj rozgrywki. Po dokonaniu wyboru moe wzi ze stou reszt kart.
                    </p>

                    <h2 className="content-subhead">Rodzaje rozgrywek</h2>
                    <p>
                        Istnieje sze rodzajw rozgrywek.<br />
                        Przy wyborze rodzaju rozgrywki, gracz decyduje, ktry z czterech kolorw bdzie w danym rozdaniu kolorem atutowym, lecz moe wybra take rozgrywk BA ("bez atu") lub BL ("bez lew"). BA jest rozgrywk, w ktrej nie ma koloru atutowego. W BL rwnie nie ma koloru atutowego, lecz naley wzi jak najmniej lew.<br />
                    </p>
                    <p>
                        Kady rodzaj rozgrywki moe zosta wybrany przez kadego z graczy tylko jeden raz, a wic wszystkich rozgrywek cznie jest 18.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Po rozdaniu kart i wybraniu rodzaju rozgrywki, moe nastpi wymiana kart. Po ewentualnej wymianie, gracz po lewej stronie rozdajcego podnosi musik, pokazuje go pozostaym graczom, nastpnie bierze karty z musiku na rk i rwnie cztery odkada na st, nie ujawniajc ich innym. Po odoeniu kart wychodzi z jednej, rozpoczynajc w ten sposb pierwsz lew. Gracze dorzucaj do lewy po jednej kacie zgodnie z ruchem wskazwek zegara. Gracz, ktry wemie lew wychodzi do nastpnej.
                    </p>

                    <h2 className="content-subhead">Punktacja</h2>
                    <p>
                        Rozdajcy musi wzi w rozgrywce minimum 3 lewy, gracz po prawej stronie rozdajcego minimum 5 lew, a trzeci gracz (wybierajcy rodzaj rozgrywki) - 8 lew.<br />
                        Jeli ktry z graczy wzi wicej lew ni powinien, otrzymuje ilo punktw rwn iloci lew nadliczbowych, jeeli wzi mniej ni powinien, otrzymuje punkty minusowe rwne iloci brakujcych lew.<br />
                        Np.; jeli gracz po lewej stronie rozdajcego wziby 10 lew (a powinien 8), otrzymaby +2 punkty, jeli wziby tylko 5 lew, otrzymaby -3 punkty.
                    </p>
                    <p>
                        W przypadku gry "bez lew" sytuacja si troch zmienia. Rozdajcy moe wzi maksymalnie osiem lewy, gracz wybierajcy rodzaj rozgrywki - maksymalnie 3, a trzeci gracz - maksymalnie 5 lew.<br />
                        Jeli ktry z graczy wzi wicej lew ni powinien, otrzymuje ilo punktw minusowych rwn iloci lew nadliczbowych, jeli wzi mniej ni powinien, otrzymuje punkty dodatnie rwne iloci brakujcych mu lew do minimum.<br />
                        Np.; jeli rozdajcy wziby 10 lew (a powinien 8), otrzymaby -2 punkty, jeli wziby tylko 5 lew, otrzymaby +3 punkty.<br />
                        <strong>
                            Suma zdobytych punktw w rozgrywce przez wszystkich graczy musi zawsze da zero.
                        </strong>
                    </p>
                    <p>
                        Gr wygrywa ten z graczy, ktry po ostatnim, osiemnastym rozdaniu uzyska najwicej punktw.
                    </p>

                    <h2 className="content-subhead">Wymiana</h2>
                    <p>
                        Wymiana nastpuje pomidzy graczem, ktry w poprzedniej rozgrywce uzyska dodatni liczb punktw, a tym co uzyska ujemn. Jeli dwch graczy uzyskao dodatni liczb punktw, pierwszy prawo do wymiany ma ten, ktry w aktualnej rozgrywce bdzie mia wicej lew do wzicia. Jeli dwch graczy uzyskao ujemn ilo punktw, trzeci gracz moe wymieni karty u obu.<br />
                        Maksymalna ilo kart, jakie mona wymieni u jednego gracza, jest rwna iloci punktw dodatnich zdobytych przez gracza wymieniajcego w poprzedniej rozgrywce (mona wymieni mniej kart lub wcale).<br />
                        Jeli wymienia si wicej ni jedn kart, daje si do wymiany od razu wszystkie karty.
                    </p>
                    <p>
                        Gracz, u ktrego wymieniane s karty, otrzymujc je od wymieniajcego , musi do kadej z nich odda ze swoich jedn, najwysz, w tym samym kolorze. Jeli nie ma wyszej, musi odda t, ktr otrzyma.
                    </p>
                    <p>
                        Jeli w przypadku gry z kolorem atutowym, gracz u ktrego nastpuje wymiana (ten co w rozgrywce poprzedniej by na minusie) otrzyma kart w tym kolorze do wymiany, nie musi oddawa najwyszej w tym kolorze, lecz moe odda dowoln swoj kart w innym kolorze (ale nie musi - jak chce moe odda najwysz kart atutow).
                    </p>
                    <div className="example">
                        <div className="title">Przykad:</div>
                        <div className="content">
                            W ostatnio rozegranym rozdaniu gracz A rozdawa karty, mia wic do wzicia 3 lewy a wzi 4, B mia wzi 8 a <br />
                            Punktacja z tej rozgrywki wyglda nastpujco: A otrzymuje +1pkt, B +1pkt, C -2pkt.<br />
                            W nastpnej rozgrywce rozdaje B. A musi wzi 5 lew, B - 3, a C - 8 lew.<br />
                            Poniewa gracz C uzyska w poprzedniej rozgrywce -2pkt, u niego pozostali gracze wymieniaj karty.<br />
                            Poniewa gracze A i B uzyskali po jednym punkcie, mog wymieni u C tylko po jednej karcie.<br />
                            Przed wymian C wybra jako kolor atutowy karo.<br />
                            Pierwszy wymienia karty gracz A, poniewa ma wicej w tej rozgrywce lew do zdobycia (5) od B (3).<br />
                            Gracz A wymienia 4 <span className="heart"></span>, gracz C musi mu wic odda kart najwysz z posiadanych w tym samym kolorze (np. As <span className="heart"></span>).<br />
                            Od gracza B otrzymuje 6 <span className="diamond"></span>, ale poniewa karo jest kolorem atutowym, gracz C moe mu odda dowoln swoj kart (np. 2 <span className="spade"></span>).
                        </div>
                    </div>
                    <p><strong>
                        Nie wolno wymienia kart w przypadku gry "bez atu", bd "bez lew".
                    </strong></p>
                </div>
            </div>
        );
    }
}



