import { basePath } from '../utils';
import React from 'react';


export default class Atutowiec extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Atutowiec',
            desc: 'gra dla 2 osb',
            url: basePath() + '/atutowiec',
            players: [2],
            cards: 'Skatowa talia kart (32 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info"> Tradycyjna gra rosyjska (zwana "swoi kozyri" - "swoje atuty"), ktra zdobya sobie spor popularno take w Wielkiej Brytanii (pod nazw "challenge" - "wyzwanie".<br />
                        Atutowiec wywodzi si ze znanej i u nas, dawnej gry po rnymi nazwami: dure, kasztelan, gap, wzek.
                    </div>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Skatowa talia kart (32 karty).<br />
                        Starszestwo kart od asa do sidemki.<br />
                        Istnieje zmienny kolor atutowy.<br />
                    </p>
                    <p>
                        Mona gra take mniejszymi taliami kart (28 lub 24 karty); gra wikszymi taliami (36 i wicej kart) jest moliwa, lecz zbyt komplikuje gr.
                    </p>

                    <h2 className="content-subhead">Atut</h2>
                    <p>
                        Przed rozdaniem kart rozdajcy wybiera sobie dowolne dwa kolory i je ogasza (na przykad: piki i trefle), jeden z nich ogaszajc jako swj kolor atutowy (na przykad piki). Z pozostaych dwu kolorw (w tym przykadzie kierw i kar) przeciwnik rozdajcego ogasza jeden jako swj kolor atutowy (na przykad kiery).
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Gdy wybr kolorw zosta ju dokonany, rozdajcy daje sobie, odsaniajc je, szesnacie kart. Usuwa w nich wszystkie karty w tych kolorach, ktrych nie ogasza, pozostawia karty w kolorach przez siebie nominowanych, ktre bd jego kartami do gry.
                    </p>
                    <p>
                        Przeciwnik rozdajcego dobiera sobie teraz karty w dwu przez siebie ogoszonych kolorach, identyczne co do wartoci, jak karty rozdajcego (naley bra pod uwag kolor atutowy).<br />
                        Po zakoczeniu podziau kart, karty w rku obydwu graczy s niejako lustrzanym odbiciem, maj zawsze identyczn warto i znaczenie.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Rozgrywk pierwszym wyjciem rozpoczyna przeciwnik rozdajcego.<br />
                        Kady z grajcych ma swj kolor atutowy. Jeden z graczy wychodzi, drugi kart wyjcia powinien przebi starsz kart w tym samym kolorze lub atutem i "nadoy", wyj do przeciwnika. Gdy kolejny gracz nie moe lub nie chce przebi karty, ktr wyszed do niego przeciwnik, musi wzi do rki nie tylko kart ostatniego wyjcia, lecz wszystkie karty lece na stole; wychodzi wwczas przeciwnik.<br />
                        Wygrywa ten, ktry pierwszy pozbdzie si wszystkich kart z rki.
                    </p>
                    <div className="example">
                        <div className="title">Przykadowa partia</div>
                        <div className="content">
                            Rozegrana 28 kartami (od asw do semek). Rozdawa gracz B.<br />
                            Gracz B jako swoje kolory nominuje kiery i kara, a jako kolor atutowy kiery.<br />
                            Gracz A nominuje wic piki i trefle, a jako kolor atutowy piki.<br />
                            W szesnastu pierwszych kartach odkrytych dla gracza B znajduj si A <span className="heart"></span>, D <span className="heart"></span>, W <span className="heart"></span> i 9 <span className="heart"></span> oraz A <span className="diamond"></span> i W <span className="diamond"></span><br />
                            Po dokonaniu podziau kart gracze maj na rku:<br />
                            <table className="pure-table pure-table-horizontal">
                                <tbody>
                                    <tr>
                                        <td>Gracz B:</td>
                                        <td>
                                            K <span className="spade"></span>, 10 <span className="spade"></span> i 8 <span className="spade"></span><br />
                                            A <span className="heart"></span>, D <span className="heart"></span>, W <span className="heart"></span> i 9 <span className="heart"></span><br />
                                            A <span className="diamond"></span> i W <span className="diamond"></span><br />
                                            K <span className="club"></span>, D <span className="club"></span>, 10 <span className="club"></span>, 9 <span className="club"></span> i 8 <span className="club"></span><br />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Gracz A:</td>
                                        <td>
                                            A <span className="spade"></span>, D <span className="spade"></span>, W <span className="spade"></span> i 9 <span className="spade"></span><br />
                                            K <span className="heart"></span>, 10 <span className="heart"></span>, 8 <span className="heart"></span><br />
                                            K <span className="diamond"></span>, D <span className="diamond"></span>, 10 <span className="diamond"></span>, 9 <span className="diamond"></span> i 8 <span className="diamond"></span><br />
                                            A <span className="club"></span> i W <span className="club"></span><br />
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                            <p>
                                Gracz A wychodzi w 8 <span className="diamond"></span>, B bije W <span className="diamond"></span>, wychodzi w 8 <span className="club"></span>, A bije W <span className="club"></span>, wychodzi w 10 <span className="heart"></span> - B decyduje si wzi karty ze stou. A wychodzi w 9 <span className="diamond"></span>, B bije W <span className="diamond"></span> i wychodzi w 8 <span className="club"></span>. A bije A <span className="club"></span> i wychodzi w 10 <span className="diamond"></span>. B bije A <span className="diamond"></span> i wychodzi w 8 <span className="spade"></span>. A bije 9 <span className="spade"></span> i wychodzi w D <span className="diamond"></span>. B bije 9 <span className="heart"></span> i wychodzi w 10 <span className="spade"></span> - tym razem A decyduje si zabra wszystkie karty.
                            </p>
                            <p>
                                B wychodzi w 9 <span className="club"></span>, A bije A <span className="club"></span> i wychodzi w 9 <span className="diamond"></span>. B bije 10 <span className="heart"></span> i wychodzi w 10 <span className="club"></span>. A bije 8 <span className="spade"></span> i wychodzi w 10 <span className="diamond"></span>. B bije W <span className="heart"></span> i wychodzi w W <span className="club"></span>. A bije 9 <span className="spade"></span> i wychodzi w 8 <span className="club"></span>. B bije D <span className="club"></span> i wychodzi w K <span className="club"></span>. A bije 10 <span className="spade"></span> i wychodzi w 8 <span className="heart"></span>. B bije D <span className="heart"></span> i wychodzi w 8 <span className="diamond"></span>. A bije W <span className="diamond"></span> i wychodzi w 9 <span className="heart"></span>. B bije A <span className="heart"></span> i wychodzc w K <span className="spade"></span> pozbywa si ostatniej karty z rki, a wic wygrywa gr.
                            </p>
                            <p>
                                A pozostao na rku: A <span className="spade"></span>, D <span className="spade"></span>, W <span className="spade"></span>, A <span className="diamond"></span>, K <span className="diamond"></span>, D <span className="diamond"></span> oraz K <span className="heart"></span>.<br />
                                Gracz A zbyt pno zacz ofensyw w drugiej turze gry, le obliczy zasoby rki B - i musia przegra.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}



