import { basePath } from '../utils';
import React from 'react';
import lampartImg1 from '../images/lampart1.gif';
import lampartImg2 from '../images/lampart2.gif';


export default class Lampart extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Lampart',
            desc: 'gra dla 2 [4] osb',
            url: basePath() + '/lampart',
            players: [2, 4],
            cards: 'Dwie standardowe talie kart (2 x 52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Gra opracowana w 1962 roku przez Roberta Abbotta z Nowego Jorku.
                    </div>

                    <h2 className="content-subhead">Gracze i karty</h2>
                    <p>
                        Gra dla 2 [4] osb.<br />
                        Do gry uywa si dwch penych talii kart.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Po przetasowaniu kady z graczy otrzymuje osiem kart, reszta kart pozostaje w zakrytym talonie.<br />
                        Gr rozpoczyna przeciwnik rozdajcego od uzupenienia iloci kart w rku do dziewiciu jedn z kart z zakrytego talonu, nastpnie za zrzuca lub wykada jedn kart.
                    </p>
                    <p>
                        W swojej kolejce gry gracz moe:
                    </p>
                    <ul>
                        <li>
                            zrzuci zbdn mu kart na otwarty stos zrzutek, ktre nie bior udziau w grze;
                        </li>
                        <li>
                            wyoy jedn kart dla siebie (do swego kwadratu);
                        </li>
                        <li>
                            lub wyoy jedn kart dla przeciwnika (do kwadratu przeciwnika).
                        </li>
                    </ul>

                    <p>
                        Miejsce wykadania kart to dla kadego z graczy wyobraony kwadrat, ktry przedstawia poniszy rysunek
                    </p>

                    <div className="center-outer-container">
                        <div className="center-inner-container">
                            <img className="pure-img" src={lampartImg1}></img>
                        </div>
                    </div>

                    <p>
                        Na odpowiednich miejscach w kwadracie naley wykada odpowiednie karty, tj. karty o rnej wartoci, od Asa do 9. Jedna wyoona na pusty st karta wyznacza swoj wartoci dokadne miejsce innych kart w kwadracie danego gracza.
                    </p>
                    <p>
                        Karty wykadane do kwadratu wasnego lub przeciwnika mona ka tylko na miejsce wolne lub zwolnione (z wyjtkiem kart specjalnych). I tak:
                    </p>
                    <ul>
                        <li>
                            karty od Asa do 9 wolno ka tylko na wolne lub zwolnione i waciwe miejsca u siebie lub u przeciwnika;
                        </li>
                        <li>
                            dziesitk wolno ka na wolne miejsce lub zwolnione, dowolnie wybrane miejsce, tylko u siebie;
                        </li>
                        <li>
                            waleta - kart specjaln - wolno ka zawsze zakrytego na inn wyoon kart, na dowolnie wybrane miejsce, u siebie lub u przeciwnika;
                        </li>
                        <li>
                            dam - kart specjaln - wolno ka zawsze zakryt na inn wyoon kart, na dowolnie wybrane miejsce, tylko u siebie;
                        </li>
                        <li>
                            krla - kart specjaln - wolno ka zawsze odkrytego na wolne lub zwolnione miejsce, a take na inn wyoon kart, na dowolnie wybrane miejsce tylko u siebie.
                        </li>
                    </ul>

                    <p>
                        Miejsca w ukadzie zapenione kartami zakrytymi (waletami i damami) uwaa si za wolne. Na jednym miejscu w ukadzie moe lee wicej ni jedna karta (jedna na drugiej); o cechach i wartociach miejsca decyduje karta leca na wierzchu.
                    </p>
                    <p>
                        Rozgrywk wolno zakoczy w momencie, gdy w kwadracie gracza wyoone jest co najmniej pi punktw i na danego gracza przypada kolej gry. Moe on wwczas zoy deklaracj zakoczenia rozgrywki, lecz nie ma tego obowizku - moe kontynuowa gr dla uzyskania wikszej liczby punktw. Rozgrywka koczy si z chwil zoenia deklaracji o jej zakoczeniu przez jednego z graczy, lub z chwil wyczerpania si kart w talonie.
                    </p>

                    <h2 className="content-subhead">Punktacja</h2>
                    <p>
                        Za uoenie w swoim kwadracie w jednym jego rzdzie, kolumnie lub przektnej trzech kart jednej barwy (wszystkich czarnych lub czerwonych) gracz zdobywa w momencie zakoczenia rozgrywki jeden punkt. Za uoenie w ten sposb trzech kart jednego koloru (np. pikw lub kierw) gracz zdobywa w momencie zakoczenia rozgrywki dwa punkty.
                    </p>
                    <p>
                        Wolno w jednej rozgrywce w swoim kwadracie dy do zdobycia punktw za wyoenie kart zarwno czarnych, jak i czerwonych (w rnych rzdach, kolumnach i przektnych).
                    </p>
                    <p>
                        Z chwil zakoczenia rozgrywki graczom zapisuje si punkty uzyskane w danej rozgrywce wedug nastpujcych zasad:
                    </p>
                    <ul>
                        <li>
                            przeciwnikowi gracza koczcego rozgrywk: sum punktw za wszystkie uzyskane waciwe ukady kart w jego kwadracie;
                        </li>
                        <li>
                            graczowi koczcemu rozgryk: sum punktw za wszystkie waciwe ukady kart w jego kwadracie, minus jeden punkt (za skoczenie gry), plus punkty premiowe za ewentualne przekroczenie piciu punktw w rozgrywce (wszystkie punkty powyej piciu licz si podwjnie).
                        </li>
                    </ul>

                    <p>
                        Gra toczy si w umwionej z gry iloci rozda (zazwyczaj po cztery rozdania) lub do osignicia w zapisie umwionej z gry iloci punktw (zazwyczaj 33).
                    </p>
                    <p>
                        <strong>Przykadowe rozliczenie</strong> jednego rozdania przedstawia poniszy rysunek:
                    </p>

                    <div className="center-outer-container">
                        <div className="center-inner-container">
                            <img className="pure-img" src={lampartImg2}></img>
                        </div>
                    </div>

                    <p>
                        Zakoczenie gry zadeklarowa gracz grny. Uzyska on w rzdach poziomych cztery pounkty (po jednym punkcie za dwa rzdy czerwone i dwa punkty za dolny rzd karowy), w rzdach pionowych trzy punkty oraz na przektnych take trzy punkty (jedna przektna czerwona za jeden punkt oraz jedna karowa za dwa punkty). Zdoby wic cznie 10pkt, a wic przekroczy o pi punktw prg piciopunktowy i za te 5pkt przekroczenia liczy mu si podwjnie - 10pkt. W sumie gracz grny zdoby 15pkt minus jeden punkt za zakoczenie, czyli 14 punktw.
                    </p>
                    <p>
                        Gracz dolny uzyska cztery punkty (za dwa rzdy pionowe - kierowy i pikowy) - i nic wicej. Jedno miejsce w jego ukadzie jest zwolnione, zapewne dam lub waletem.
                    </p>

                    <h2 className="content-subhead">Lampart dla czterech osb</h2>
                    <p>
                        W tym wariancie gry gracze graj parami.<br />
                        Kady z graczy otrzymuje po osiem kart.
                    </p>
                    <p>
                        Istniej tylko dwa wykadane kwadraty dwch walczcych stron. Gracze kolejno wykadaj i zrzucaj karty do swojego kwadratu, kwadratu przeciwnikw lub na stos zrzutek.
                    </p>
                    <p>
                        Gracz, ktry chciaby zakoczy rozgrywk (w kwadracie danej strony jest ju 5pkt lub wicej), moe, lecz nie ma obowizku, zapyta partnera, czy j zakoczy. Gracz pytajcy zobowizany jest postpi zgodnie z odpowiedzi partnera (np. jeli partner nie zgodzi si na zakoczenie rozgrywki, gracz pytajcy nie moe tego zrobi w tej kolejce gry).
                    </p>
                    <p>
                        Gra toczy si w umwionej z gry iloci rozda (zazwyczaj po dwa rozdania) lub do osignicia w zapisie umwionej z gry iloci punktw (zazwyczaj 33).
                    </p>
                    <p>
                        Wszystkie inne zasady gry i zapisu pozostaj bez zmian.
                    </p>
                </div>
            </div>
        );
    }
}



