import { basePath } from '../utils';
import React from 'react';


export default class Wybory extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Wybory',
            name2: 'Variety',
            desc: 'gra dla 4 osb',
            url: basePath() + '/wybory',
            players: [4],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Gra opracowana w 1963 roku przez amerykaskiego znawc i twrc nowych gier, Roberta Abbotta, ktry nazwa j "variety" ("rozmaitoci").
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>
                        Gra dla 4 osb.<br />
                        W kadej rozgrywce dwie osoby graj przeciwko pozostaym dwom, ale partnerzy zmieniaj si w kolejnych rozgrywkach. Mog siedzie naprzeciw lub te obok siebie (nie zmieniaj miejsc).
                    </p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia kart (52 karty). Starszestwo kart od asa do dwjki.<br />
                        Istnieje zmienny kolor atutowy.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie. Do kolejnych rozgrywek gracze rozdaj karty kolejno. Gr rozpoczyna gracz na lewo od rozdajcego.
                    </p>

                    <h2 className="content-subhead">Licytacja</h2>
                    <p>
                        Pierwszym etapem gry jest licytacja.
                    </p>
                    <p>
                        W licytacji ustala si, ktry z moliwych omiu rodzajw rozgrywek bdzie rozgrywany oraz jacy dwaj gracze bd partnerami w danej rozgrywce.
                    </p>
                    <p>
                        Przy grze w Wybory istniej nastpujce moliwoci rozgrywek o rnych kolorach atutowych i rnych kartach premiowych:
                    </p>
                    <ul type="I">
                        <li>
                            - bez atu - za wzicie wikszej iloci lew ni przeciwnicy: 12 punktw plusowych;
                        </li>
                        <li>
                            - atu trefl - za kad wzit lew 2 punkty plusowe;
                        </li>
                        <li>
                            - atu kier - za kad wzit kart pikow 2 punkty plusowe;
                        </li>
                        <li>
                            - atu karo - za kad wzit kart czerwon 1 punkt plusowy, za kad wzit kart czarn 1 punkt minusowy;
                        </li>
                        <li>
                            - atu pik - za kad wzit czarn dziesitk i waleta 5 punktw plusowych, za kad wzit czerwon dziesitk i waleta 5 punktw minusowych;
                        </li>
                        <li>
                            - atu trefl - za kad wzit kart czerwon 1 punkt plusowy (lew bierze najnisza karta w lewie);
                        </li>
                        <li>
                            - atu pik - za kad wzit kart treflow 2 punkty minusowe;
                        </li>
                        <li>
                            - bez atu - za kad wzit lew 2 punkty minusowe.
                        </li>
                    </ul>

                    <p>
                        W licytacji gracz po lewej stronie rozdajcego pierwszy deklaruje, ktr rozgrywk spord wszystkich moliwych wybiera do gry. Nastpnie kolejno deklaracje tak skadaj pozostali gracze.
                    </p>
                    <p>
                        Gdy w licytacji ktra z rozgrywek zostanie przez ktrego gracza powtrzona (zadeklarowana po raz drugi), licytacja koczy si - rozgrywana bdzie ta wanie rozgrywka, partnerami za w niej bd ten gracze, ktrzy zadeklarowali t sam rozgrywk.
                    </p>
                    <p>
                        adnemu z graczy nie wolno w licytacji dwukrotnie zgasza tej samej rozgrywki.
                    </p>
                    <p>
                        Przykadowo: rozpoczynajcy licytacj gracz A deklaruje rozgrywk II, gracz B - rozgrywk VIII, gracz C - rozgrywk VI, gracz D - rozgrywk II. Rozgrywka II zostaa wic wybrana do gry i partnerami w niej bd gracze A i D przeciwko graczom B i C.<br />
                        Inny przykad: gracz A deklaruje rozgrywk V, gracz B - rozgrywk I, gracz C - rozgrywk VIII, gracz D - rozgrywk IV. Poniewa nic nie wybrano, licytacja toczy si dalej - gracz A nie moe ju ponownie zadeklarowa rozgrywki V, musi zmieni odzywk, deklaruje wic rozgrywk VII. Gracz B - rozgrywk III, gracz C - rozgrywk IV. A wic rozgrywka IV pojawia si po raz drugi w licytacji i one bdzie rozgrywana; partnerami bd gracze D (ktry j pierwszy zgosi) i C (ktry j powtrzy), przeciwko graczom A i B.
                    </p>
                    <p>
                        W VI rozgrywce obowizuj zmienione zasady bicia lew: lew bierze nie karta w niej najstarsza, lecz najmodsza. Jeli lewa zostaa przebita atutem, bierze j ten gracz, ktry przebi; jeli dwoma atutami - atut modszy.
                    </p>
                    <p>
                        W toku licytacji wszystkim graczom wolno, zgodnie lub niezgodnie z prawd, wedle ich uznania, informowa o wasnych kartach w rku i reklamowa ich warto, propagujc swoj odzywk licytacyjn, np. tak:<br />
                        "licytuj VIII, nie mam ani jednej figury, na pewno wygram"; "licytuj I, mam cztery asy, sporo figur dodatkowo..."
                    </p>
                    <p>
                        Po zakoczeniu licytacji nie wolno ju nikomu udziela adnych dodatkowych informacji.
                    </p>
                    <p>
                        W kolejnej licytacji mog by deklarowane i grane rodzaje rozgrywek zadeklarowane i zagrane uprzednio; kada rozgrywka jest caoci sama dla siebie i w ich cyklu mog powtrzy si dwie lub wicej identycznych rozgrywek, jeli wyniknie to z licytacji.
                    </p>

                    <h2 className="content-subhead">Rozgrywka</h2>
                    <p>
                        Po zakoczeniu licytacji do pierwszej lewy wychodzi gracz po lewej stronie tego gracza, ktry dan rozgrywk pierwszy zgosi w licytacji. Wygrywajcy licytacj s atakujcymi, ich przeciwnicy - bronicymi si.
                    </p>
                    <p>
                        W rozgrywce obowizuje dorzucanie do koloru wyjcia, nie ma obowizku przebijania starsz kart i nie ma obowizku przebijania atutem w przypadku nieposiadania koloru wyjcia (jeli rozgrywka jest, oczywicie, atutowa). Do nastpnej lewy wychodzi ten z graczy, ktry wzi lew poprzedni.
                    </p>

                    <h2 className="content-subhead">Zapis punktacji</h2>
                    <p>
                        Po zakoczeniu rozgrywki oblicza si punkty w niej uzyskane.
                    </p>
                    <p>
                        Jeli w danej rozgrywce atakujcy uzyskali w swoich lewach wicej punktw plusowych lub mniej punktw minusowych ni bronicy si, wygrali rozgrywk; kadej ze stron zapisuje si ilo punktw plusowych lub minusowych faktycznie uzyskanych. Ilo punktw dla danej strony zapisuje si dwukrotnie - dla kadego z partnerw w danym rozdaniu. Poniewa w kolejnych rozgrywkach zmienia si partnerw, zapis prowadzony jest w czterech kolumnach, dla kadego z graczy oddzielnie.
                    </p>
                    <p>
                        Jeli w danej rozgrywce atakujcy uzyskali w swoich lewach mniej punktw plusowych lub wicej punktw minusowych ni bronicy si, przegrali rozgrywk; kadej ze stron zapisuje si ilo punktw faktycznie uzyskanych, bronicy si za po 10 punktw plusowych premii dodatkowej (dla kadego z graczy).
                    </p>
                    <p>
                        Pena tura gry w Wybory skada si z omiu rozda i rozgrywek (kady z grajcych dwukrotnie rozpoczyna licytacj). Skrcona tura gry: tylko cztery rozdania i rozgrywki.<br />
                        Tur gry wygrywa ten z graczy, ktry w zapisie uzyska najwicej punktw plusowych.
                    </p>
                </div>
            </div>
        );
    }
}



