import { basePath } from '../utils';
import React from 'react';


export default class Callabra extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Callabra',
            name2: 'Cassino',
            url: basePath() + '/callabra',
            players: [],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">Ludowa gra turecka, znana w wielu krajach pod nazw "cassino".</div>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Pena talia (52 karty).<br />
                        Warto punktowa kart: krl - 13, dama - 12, walet - 11, pozostae karty wedug iloci oczek (as - 1pkt).<br />
                        Nie ma koloru atutowego.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Gracze rozdaj karty kolejno w kolejnych rozgrywkach. Kady z grajcych otrzymuje po trzy karty, pi kart wykada si odkryte na stole, reszta kart pozostaje zakryta w talonie.
                    </p>
                    <p>
                        Gdy karty z rki zostan zgrane, wszyscy gracze otrzymuj po raz drugi po trzy karty z talonu, a po ich zgraniu dalsze karty a do wyczerpania si kart w talonie.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Gr rozpoczyna gracz po lewej rce rozdajcego.<br />
                        Moe on kart z rki wygra (zdoby) jedn lub wicej kart ze stou. Kart ze stou wygrywa si, wykadajc z rki kart tej samej wysokoci. Wicej ni jedn kart ze stou zdobywa si, wykadajc z rki kart o wysokoci sumy oczek kart zdobywanych (wykadajc dam - 12pkt - zdobywamy dwie trjki, pitk i asa - take 12pkt).
                    </p>
                    <p>
                        Jeli w swojej kolejce gry gracz nie moe lub nie chce zdoby ani jednej karty ze stou, ma obowizek wyoy na st jedn kart z rki.<br />
                        Gdy ktry z graczy zdobdzie wszystkie lub ostatni kart ze stou, nastpny gracz ma obowizek jedn ze swych kart wyoy na st.
                    </p>
                    <p>
                        Karty pozostae na stole w chwili zgrania ostatniej karty z ostatniej rki staj si wasnoci rozdajcego.
                    </p>
                    <p>
                        Po zakoczeniu rozgrywki oblicza si punkty za zdobyte karty wedug nastpujcych zasad:
                    </p>
                    <ul>
                        <li>za najwiksz ilo kart zdobytych - jeden punkt (jeli dwaj gracze zdobyli tak sam liczb kart, aden z nich nie otrzymuje punktw);</li>
                        <li>za zdobycie dwjki pikowej - dwa punkty;</li>
                        <li>za zdobycie dziesitki karowej - jeden punkt;</li>
                    </ul>
                    <p>
                        Gra toczy si w kolejnych rozdaniach do chwili, gdy jeden z graczy osignie w zapisie umwion z gry liczb punktw (zazwyczaj 11).
                    </p>
                </div>
            </div>
        );
    }
}



