import { basePath } from '../utils';
import React from 'react';


export default class Wist extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Wist',
            desc: 'gra dla 4 osb',
            url: basePath() + '/wist',
            players: [4],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Jest to jedna z najstarszych gier w karty, wywodzca si z dawnej gry pod nazw "triumf", znanej ju na pocztkach XVI wieku (wzmiankowa o niej Rabelais, pniej Szekspir).<br />
                        Z gry oglnoeuropejskiej wist sta si gr przede wszystkim angielsk, po czym znw wszed w mod w wielu krajach Europy w XVIII i XIX wieku jako najbardziej znana karciana gra strategiczno-losowa. W tej roli wista wspczenie zastpi wywodzcy si od niego porednio Bryd.<br />
                        W wista grywano w wielu rnych wariantach (boston, wint, la mort, humbug, Fontainebleau i inne). Wist-solo jest jednym z tych wariantw, ktry zdoby sobie niegdy szczegln popularno i jest do dzisiaj znany i uprawiany w Wielkiej Brytanii.
                    </div>

                    <h2 className="content-subhead">Gracze i karty</h2>
                    <p>
                        Gra dla 4 osb.<br />
                        Pena talia kart.<br />
                        Starszestwo kart od asa do dwjki.<br />
                        Istnieje zmienny kolor atutowy.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie. Gracze otrzymuj po trzynacie kart. Ostatnia karta, naleca do rozdajcego, zostaj odkryta - jej kolor wyznacza kolor atutowy w danej rozgrywce. Kade rozdanie skada si z dwch etapw: licytacji i rozgrywki.
                    </p>
                    <p>
                        W licytacji gracze kolejno, poczynajc od siedzcego po lewej stronie rozdajcego i w kierunku ruchu wskazwek zegara, deklaruj swj udzia w rozgrywce. Gracz, ktremu jako i ukad kart nie pozwalaj bra udziau w licytacji, pasuje; kto raz spasowa, nie bierze udziau w dalszej licytacji. Wygrywa licytacj ten z graczy, ktry zapowie najwysz rozgrywk. Starszestwo w licytacji (kolejno rozgrywek) jest nastpujce: propozycja i przyjcie, solo, misre, abondance, abondance atutowe, misre ouverte, abondance declare.
                    </p>
                    <p>
                        Gracz, ktry licytuje propozycj ("proponuje"), zobowizuje si wzi w rozgrywce z odsonitym kolorem jako atutowym co najmniej osiem z trzynastu lew wraz z partnerem. Partnerem rozgrywajcego zostaje ten z graczy, ktry w dalszej licytacji pierwszy przyjmie propozycj (zalicytuje "akceptuj" lub "przyjmuj"). Gracze, ktrzy stali si partnerami w rozgrywce propozycji i akceptacji, nie zmieniaj miejsc przy stole, mog siedzie obok lub naprzeciw siebie. Jeli pierwszy licytujcy pasowa, ktry za z kolejnych graczy zgosi propozycj, pierwszy licytujcy ma w swojej kolejnoci prawo powtrnie wczy si do licytacji i przyj propozycj. Jest to jedyny wypadek, gdy wolno graczowi raz jeszcze wczy si do licytacji po uprzednim spasowaniu. Jeli zalicytowanej propozycji nikt z pozostaych graczy nie akceptuje, licytujcy propozycj ma prawo przelicytowa sam siebie (zalicytowa wyej); jeli tego nie uczyni, nie ma rozgrywki, karty tasuje si i rozdaje do nastpnej licytacji.
                    </p>
                    <p>
                        Gracz, ktry licytuje solo, zobowizuje si w rozgrywce z odsonitym kolorem jako atutowym wzi samemu co najmniej pi z trzynastu lew.
                    </p>
                    <p>
                        Gracz, ktry licytuje misre, zobowizuje si w rozgrywce bezatutowej nie wzi samemu ani jednej lewy.
                    </p>
                    <p>
                        Gracz, ktry licytuje abondance, zobowizuje si w rozgrywce z kolorem atutowym, ktry sam sobie wybierze, wzi samemu co najmniej dziewi z trzynastu lew. Licytujc abondance gracz nie zapowiada od razu koloru atutowego, lecz czeka, a pozostali trzej gracze spasuj, dopiero wwczas ujawnia swj wybrany kolor atutowy. Gdy abondance zostaje przelicytowana, zamierzony kolor atutowy pozostaje nieujawniony
                    </p>
                    <p>
                        Gracz, ktry licytuje abondance atutow, zobowizuje si w rozgrywce z odsonitym kolorem jako atutowym wzi samemu co najmniej dziewi z trzynastu lew. Licytujc abondance atutow gracz nie zapowiada od razu, i jest ona atutowa, lecz ujawnia to dopiero wwczas, gdy pozostali trzej gracze spasuj - chyba, e abondance atutowa jest przelicytowaniem zwykej abondance, musi wwczas zosta od razu zapowiedziana jako atutowa.
                    </p>
                    <p>
                        Gracz, ktry licytuje misre ouverte, zobowizuje si w rozgrywce bezatutowej nie wzi samemu ani jednej lewy i dodatkowo po pierwszym wyjciu odkry swoje karty.
                    </p>
                    <p>
                        Gracz, ktry licytuje abondance declare (jest to najwysza zapowied w wiscie-solo), zobowizuje si w rozgrywce bezatutowej wzi samemu wszystkie trzynacie lew. Przy rozgrywaniu abondance declare pierwszy wychodzi ten z graczy, ktry t zapowied zalicytowa. Przy rozgrywaniu wszystkich innych zapowiedzi zawsze pierwszy wychodzi ten z graczy, ktry rozpoczyna licytacj (siedzcy po lewej rce rozdajcego).
                    </p>

                    <h2 className="content-subhead">Punktacja</h2>
                    <p>
                        Za poszczeglne zapowiedzi otrzymuje si nastpujce iloci punktw:
                    </p>
                    <ul>
                        <li>
                            za propozycj i akceptacj - 4pkt dla kadego z dwch partnerw i po 1pkt za kad wzit lew powyej obowizujcych omiu dla kadego z dwch partnerw; kontrpartnerom zapisuje si t sam ilo punktw minusowych;
                        </li>
                        <li>
                            za solo - 12pkt dla rozgrywajcego i po 3pkt za kad wzit lew powyej obowizujcych piciu; kontrpartnerom zapisuje si po 4pkt minusowe i po 1pkt minusowym za kad lew powyej obowizujcych piciu zdobyt przez rozgrywajcego;
                        </li>
                        <li>
                            za misre - 24pkt dla rozgrywajcego, a po 8pkt minusowych dla kontrpartnerw; oczywicie nie ma nadrbek;
                        </li>
                        <li>
                            za abondance i abondance atutow - 36pkt dla rozgrywajcego i po 6 punktw za kad lew powyej obowizujcych dziewiciu; kontrpartnerom zapisuje si po 12 punktw minusowych i po 2 punkty minusowe za kad lew powyej obowizujcych dziewiciu zdobyt przez rozgrywajcego;
                        </li>
                        <li>
                            za misre ouverte - 48pkt dla rozgrywajcego; oczywicie nie ma nadrbek;
                        </li>
                        <li>
                            za abondance declare - 72pkt dla rozgrywajcego; oczywicie nie ma nadrbek.
                        </li>
                    </ul>

                    <p>
                        Jeli gracz rozgrywajcy nie wykona swojej zapowiedzi licytacyjnej, otrzymuje zamiast punktw plusowych minusowe, odpowiedni za ilo punktw plusowych (z punktami za wpadk lub wpadki) dziel si kontrpartnerzy. W kadym zapisie ilo zdobytych punktw plusowych rwnoway si z iloci punktw minusowych.<br />
                        Zasady punktowania przedstawia ponisza tabela:
                    </p>

                    <table className="pure-table pure-table-horizontal">
                        <tbody>
                            <tr className="pure-table-odd">
                                <td></td>
                                <td>dla rozgrywajcego</td>
                                <td>dla kontrpartnerw</td>
                                <td>za nadrbk lub wpadk dla rozgrywajcego</td>
                                <td>za nadrbk lub wpadk dla kontrpartnerw</td>
                            </tr>
                            <tr>
                                <th>propozycja i przyjcie</th>
                                <td>4<br /> 4 (partner)</td>
                                <td>4</td>
                                <td>1<br /> 1 (partner)</td>
                                <td>1</td>
                            </tr>
                            <tr>
                                <th>solo</th>
                                <td>12</td>
                                <td>4</td>
                                <td>3</td>
                                <td>1</td>
                            </tr>
                            <tr>
                                <th>misre</th>
                                <td>24</td>
                                <td>8</td>
                                <td>-</td>
                                <td>-</td>
                            </tr>
                            <tr>
                                <th>abondance</th>
                                <td>36</td>
                                <td>12</td>
                                <td>6</td>
                                <td>2</td>
                            </tr>
                            <tr>
                                <th>abondance atutowe</th>
                                <td>36</td>
                                <td>12</td>
                                <td>6</td>
                                <td>2</td>
                            </tr>
                            <tr>
                                <th>misre ouverte</th>
                                <td>48</td>
                                <td>16</td>
                                <td>-</td>
                                <td>-</td>
                            </tr>
                            <tr>
                                <th>abondance declare</th>
                                <td>72</td>
                                <td>24</td>
                                <td>-</td>
                                <td>-</td>
                            </tr>
                        </tbody>
                    </table>

                    <p>
                        Na przykad za propozycj i akceptacj, gdy rozgrywajcy wzili dziesi lew, otrzymuj po 6 punktw kady (4 punktw za rozgrywk i 2 za nadrbki), kontrpartnerom za zapisuje si po 6 punktw minusowych kademu.
                    </p>
                    <p>
                        Za abondance atutow, gdy rozgrywajcy wzi tylko siedem lew, a wic nie zrealizowa swojej zapowiedzi, zapisuje mu si 48 punktw minusowych (36 za przegran rozgrywk i 12 za dwie wpadki), kontrpartnerom za zapisuje si po 16 punktw plusowych kademu (12 za przegran rozgrywk i 4 za dwie wpadki).
                    </p>
                    <p>
                        Kade rozdanie (rozgrywka) w wicie jest samodzieln parti; gr mona przerwa w dowolnym momencie, gdy kolejna rozgrywka dobiegnie koca.
                    </p>

                    <div className="example">
                        <div className="title">Partia przykadowa</div>
                        <div className="content">
                            <p>
                                Rozdawa gracz D; kart odsonit jest 6 trefl. Ukad kart u poszczeglnych graczy jest nastpujcy:
                            </p>

                            <table className="pure-table">
                                <tbody>
                                    <tr className="pure-table-odd">
                                        <td>Gracz A</td>
                                        <td>Gracz B</td>
                                        <td>Gracz C</td>
                                        <td>Gracz D</td>
                                    </tr>
                                    <tr>
                                        <td>Piki: A, K, 10</td>
                                        <td>Piki: 8, 3</td>
                                        <td>Piki: W, 6, 5, 4, 2</td>
                                        <td>Piki: D, 9, 7</td>
                                    </tr>
                                    <tr>
                                        <td>Kiery: K, 9</td>
                                        <td>Kiery: W, 8, 6, 5, 3</td>
                                        <td>Kiery: D, 7, 4, 2</td>
                                        <td>Kiery: A, 10</td>
                                    </tr>
                                    <tr>
                                        <td>Kara: 9, 7, 6, 4</td>
                                        <td>Kara: - </td>
                                        <td>Kara: 3, 2</td>
                                        <td>Kara: A, K, D, W, 10, 8, 5</td>
                                    </tr>
                                    <tr>
                                        <td>Trefle: A, K, 7, 4</td>
                                        <td>Trefle: D, W, 10, 8, 3, 2</td>
                                        <td>Trefle: 9, 5</td>
                                        <td>Trefle: 6</td>
                                    </tr>
                                </tbody>
                            </table>

                            <p>
                                W licytacji zabierali gos kolejno: A - propozycja, B - pas, C - misre, D - abondance, A - pas, B - pasowa ju uprzednio, C - pas; D ujawnia, i jest to abondance w karach.<br />
                                Licytacj wygra wic D - zobowiza si wzi samemu co najmniej dziewi lew z karami jako atuty.
                            </p>
                            <p>
                                ROZGRYWKA:<br />
                                A wychodzi w K <span className="club"></span>, bierze na niego lew i wychodzi w A <span className="club"></span>. D przebija go blotk karow i ciga atuty z gry w czterech kolejnych lewach, grajc A, K, D i W karo, na ktre A zrzuca wszystkie swoje kara, B - D <span className="club"></span> i 8 <span className="club"></span> oraz dwa mae kiery, C - dwa kara i dwa mae piki.
                            </p>
                            <p>
                                Nastpnie D, w nadziei, e spadn jeszcze jakie piki i w ten sposb wyrobi sobie decydujc lew na D <span className="spade"></span>, wychodzi dalej dwukrotnie w kara (10 i 8). A zrzuca dwa trefle, B - dwa piki, C - dwa mae kiery. W tym momencie rozgrywajcy orientuje si, i jedyna jego szansa to wpuci A do rki na kiery i oczekiwa na wyrobienie si damy <span className="spade"></span>. Niestety, tego samego domyli si take A - i po wyjciu D w A <span className="heart"></span> zrzuci na niego... swego K <span className="heart"></span>. Ostatnie cztery lewy bior teraz, niezalenie od tego, w co wyjdzie D, jego przeciwnicy; w skutek dobrej gry obronnej kontrpartnerw D nie wypenia swojej zapowiedzi licytacyjnej, wzi tylko osiem lew zamiast dziewiciu.
                            </p>
                            <p>
                                D zapisuje si wic 42pky minusowe (36 za przegran rozgrywk i 6 za jedn wpadk), przeciwnicy uzyskuj po 14pkt plusowych kady.
                            </p>
                        </div>
                    </div>
                </div>

                <div>
                    <div className="content">
                        <a name="solo"></a>
                        <h1 className="content-subhead main-header">
                            Wist-solo dla trzech osb
                        </h1>

                        <h2 className="content-subhead">
                            Wariant I: Wist-solo trzema kolorami
                        </h2>
                        <p>
                            Z talii usuwa si jeden kolor, gra toczy si 39 kartami, kady z graczy otrzymuje po 13 kart.
                        </p>

                        <h2 className="content-subhead">
                            Wariant II: Wist-solo 40 kartami
                        </h2>
                        <p>
                            Z talii usuwa si 4, 3 i 2 wszystkich kolorw, gra toczy si 40 kartami, kady z graczy otrzymuje po 13 kart; czterdziesta karta, odsonita, wyznacza kolor atutowy i nie bierze udziau w rozgrywce.
                        </p>
                        <p>
                            W obu tych wariantach obowizuje inne ni przy tradycyjnym wicie-solo dla czterech osb starszestwo zapowiedzi, a mianowicie: solo, abondance, abondance atutowa, misre, abondance declare, misre ouverte.
                        </p>
                        <p>
                            Nie ma wicej propozycji i akceptacji, misre za jest starsza od abondance (przeciw dwom tylko, nie trzem kontrpartnerom znacznie trudniej jest je wygra).
                        </p>
                        <p>
                            Inaczej wyglda take zapis - punkty minusowe dzieli si przy wygranej rozgrywce nie na trzech, lecz na dwch kontrpartnerw, punkty plusowe za, wobec zmian w starszestwie zapowiedzi w licytacji, zdobywa si odpowiednio inaczej:
                        </p>
                        <ul>
                            <li>za solo - 12pkt</li>
                            <li>za abondance i abondance atutow - 24pkt</li>
                            <li>za misre - 36pkt</li>
                            <li>za abondance declare - 48pkt</li>
                            <li>za misre ouverte - 72pkt.</li>
                        </ul>
                    </div>
                </div>
            </div>
        );
    }
}



