import { basePath } from '../utils';
import React from 'react';


export default class TrzydziesciJeden extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Trzydzieci Jeden',
            name2: '31',
            desc: 'gra dla 2 osb',
            url: basePath() + '/trzydziescijeden',
            players: [2],
            cards: 'Standardowa talia kart (52 karty)'
        }
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">Ludowa gra amerykaska.</div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla dwch graczy</p>

                    <h2 className="content-subhead">Karty</h2>
                    <p>Do gry uywane s 24 karty (A,2,3,4,5 i 6). Warto punktowa kart liczona jest wedug iloci oczek (As = 1pkt).</p>

                    <h2 className="content-subhead">Zasady gry</h2>
                    <p>Gracze rozpoczynaj gr na zmian.<br />
                        Karty kadzie si odkryte na stole i gracze na zmian bior po jednej z nich. Biorc kolejne karty, kady sumuje ich liczb oczek. Ten, ktry pierwszy uzbiera 31 punktw, wygrywa. Jeli gracz, na ktrego przypada kolej, nie moe wzi adnej z pozostaych na stole kart bez przekroczenia 31 punktw, przegrywa gr.</p>
                </div>
            </div>
        );
    }
}



