import { basePath } from '../utils';
import React from 'react';


export default class Cribbage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Cribbage',
            readAs: 'Kribid',
            desc: 'gra dla 2 osb',
            url: basePath() + '/cribbage',
            players: [2],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Cribbage (wymawiaj: kribid) jest star gr angielsk, do dzi dobrze znan i chtnie uprawian w Wielkiej Brytanii i innych krajach anglosaskich. Znane s liczne warianty tej gry dla rnej liczby grajcych lub wedug nieco innych zasad.<br />
                        Niektrzy sdz, i zasady cribbage'a skodyfikowa i sam gr nazwa angielski poeta sir John Suckling (1609-1642) na podstawie jeszcze dawniejszej gry pod nazw "noddy", o ktrej niewiele wiadomo. Cribbage dotar do USA wraz z pierwszymi osadnikami i jest tam do dzi gr bardzo popularn. Z zachowanej korespondencji wiadomo, i zapalonym graczem w cribbage by Beniamin Franklin. Do dzi w krajach anglosaskich odbywaj si turnieje tej gry.
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla dwch osb.</p>

                    <h2 className="content-subhead">Rekwizyty</h2>
                    <p>
                        Pena talia kart. Warto punktowa kart: as -1, figury i dziesitki - 10, blotki wedug iloci oczek. Starszestwo kart od krla do asa. Nie ma koloru atutowego.<br />
                        Specyficznym rekwizytem do gry jest tabliczka obliczeniowa, zazwyczaj w postaci paskiej deszczuki z otworkami (po kadej stronie dwa rzdy po 30 otworkw oraz szedziesity pierwszy otworek wyjciowy). Gracze w toku gry notuj zdobyte punkty przemieszczajc swj pionek z jednego otworka do drugiego. Posiadanie i uywanie takiej tabliczki obliczeniowej uatwia gr, lecz nie jest konieczne. Zdobyte punkty mona zapisywa na papierze, odkada zapaki lub etony itp.
                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie. Kady z grajcych otrzymuje po 5 kart.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Celem gry jest osignicie umwionej z gry iloci punktw w kolejnych partiach i ich kolejnych etapach: rozgrywce i rozliczeniu (nazywanym take pokazem).<br />
                        W tradycyjnym cribbage'u parti wygrywa ten z graczy, ktry w kolejnym rozdaniu pierwszy zdobdzie 61 punktw.
                    </p>
                    <p>
                        Kady z graczy wybiera dwie dowolnie wybrane ze swoich karty i zrzuca je zakryte na st, pozostajc do rozgrywki z trzema kartami w rku. Cztery lece na stole karty tworz tzw. krib, wany w drugim etapie gry, rozliczeniu (pokazie). Nastpnie przeciwnik rozdajcego przekada pozostae w talonie karty i odsania wierzchni kart; jest to tzw. karta startowa, ktra rwnie bdzie braa udzia w drugim etapie gry, rozliczeniu (pokazie).
                    </p>
                    <p>
                        Rozgrywk rozpoczyna przeciwnik rozdajcego. W rozgrywce gracze wykadaj przed sob na stole kolejno na zmian po jednej wybranej karcie z rki, starajc si zdoby punkty za kombinacje uzyskane za pomoc kart swoich i kart przeciwnika.
                    </p>
                    <p>
                        Rozgrywka toczy si dopki suma oczek kart wyoonych na stole nie osignie 31. Wwczas, niezalenie od tego, ile kart wyoono, ile za pozostao jeszcze na rku u obydwu grajcych, rozgrywka koczy si.
                    </p>

                    <h2 className="content-subhead">Punktacja w rozgrywce</h2>
                    <p>
                        W rozgrywce punkty zdobywa si:
                    </p>
                    <ul>
                        <li>
                            <p>
                                za par - za wyoenie karty tej samej wysokoci jak wyoona poprzednio - 2 punkty (dama na dam, trjka na trjk, sidemka na sidemk itp.);
                            </p>
                        </li>
                        <li>
                            <p>
                                za par krlewsk - za wyoenie trzeciej karty tej samej wysokoci jak para kart wyoonych poprzednio - 6 punktw (dama na par dam, trjka na par trjek, sidemka na par sidemek);
                            </p>
                        </li>
                        <li>
                            <p>
                                za podwjn par krlewsk - za wyoenie czwartej karty tej samej wysokoci jak para krlewska kart wyoonych uprzednio - 12 punktw (trjka na trzy trjki, sidemka na trzy sidemki; wyoenie damy na trzy damy nie jest w rozgrywce moliwe, gdy suma oczek czterech dam przekracza dozwolone 31 punktw);
                            </p>
                        </li>
                        <li>
                            <p>
                                za sekwens - za wyoenie kilku kart, ktre cznie stanowi ukad kolejny - tyle punktw ile jest kart w sekwensie (a wic co najmniej 3, take 4, 5 lub 6 punktw);
                            </p>
                            <p>
                                Uwaga: za sekwens liczy si zestaw kart wyoonych kolejno, lecz same karty nie musz by (w odrnieniu od punktw za pary, pary krlewskie i podwjne pary krlewskie) wykadane kolejno tu po sobie. Sekwensem z trzech kart (za trzy punkty dla tego gracza, ktry wyoy trzeci kart) jest wic zarwno kolejny ukad kart np. 4,5,6 jak te ukad 5,4,6 lub 6,4,5 lub 6,5,4 itd.
                            </p>
                            <p>
                                Jeli nastpny gracz dooy do sekwensu z trzech kart odpowiedni czwart kart, zdobywa cztery punkty za sekwens czterokartowy - mog to by w podanym powyej przykadzie sekwensy 4,5,6,3; 5,4,6,3; 6,4,5,3; 6,5,4,3 i tak dalej, a take wszystkie powysze z sidemk zamiast trjki.
                            </p>
                            <p>
                                Sekwensy czterokartowe i dusze nie musz wynika z sekwensw trzykartowych, mog tworzy si same. Tak na przykad ukad trzech kart 3,2,5 nie jest sekwensem - sekwens czterokartowy powstaje po wyoeniu czwrki (3,2,5,4). Podobnie mog by tworzone sekwensy piciokartowe (3,2,5,A,4) i szeciokartowe (2,A,6,5,3,4).
                            </p>
                            <p>
                                As w sekwensach jest kart najmniejsz; ukad As, Krl, Dama nie jest sekwensem.
                            </p>
                        </li>
                        <li>
                            <p>
                                za pitnastk - za ostatni kart, ktra przy sumowaniu oczek wszystkich kart dotd wyoonych daje pitnacie oczek - otrzymuje si 2 punkty;
                            </p>
                            <p>
                                Oto przykad zestawu kart dajcych graczowi, ktry wykada ostatni z tych kart, 2 punkty za pitnastk: K,5; W,5; 5,10; 9,6; 6,8,A; 3,8,4 (w tym przypadku zapisuje si punkty zdobyte zarwno za sekwens, jak i za pitnastk); 3,6,6 (w tym przypadku zapisuje si punkty zdobyte zarwno za par, jak i za pitnastk); 4,8,2,A; 6,4,A,4 itd.
                            </p>
                        </li>
                        <li>
                            <p>
                                za trzydzieci jeden - za ostatni kart, ktra przy sumowaniu oczek wszystkich kart dotd wyoonych daje 31 oczek - otrzymuje si 2 punkty;
                            </p>
                            <p>
                                Zestawy kart dajcych graczowi, ktry wykada ostatni z tych kart, 2 punkty za trzydzieci jeden, s bardzo liczne - powstaj podobnie jak zestawy za pitnastk.
                            </p>
                        </li>
                        <li>
                            <p>
                                za ostatni kart otrzymuje si jeden punkt.
                            </p>
                            <p>
                                Gdy gracz, na ktrego przychodzi kolej wyoenia karty na st, nie moe tego uczyni, gdy przekroczyby 31 oczek (np. ma w rku 9, na stole za wyoono ju 24 oczka) - deklaruje: "dalej!". Gdy jego kontrpartner moe jeszcze dooy jak kart (w tym przypadku, do 24 oczek, A, 2, 3, 4, 5 lub 6), otrzymuje jeden punkt za ostatni kart. Jeli ostatnia wyoona w ten sposb karta daje w sumie 31 oczek (w tym przypadku 7), gracz otrzymuje 2pkt za trzydzieci jeden, lecz ju bez punktu za ostatni kart.
                            </p>
                            <p>
                                Mona w toku gry kilka razy zdoby po jednym punkcie za ostatni kart, gdy jeden z graczy zadeklaruje "dalej!", drugi ma za do dyspozycji kilka niskich, dajcych si jeszcze kolejno wyoy kart.
                            </p>
                        </li>
                    </ul>
                    <p>
                        Rozgrywka koczy si w momencie osignicia 31 oczek lub w chwili, gdy obaj gracze zadeklaruj "dalej!", co znaczy, e aden z nich nie moe ju wyoy adnej karty.
                    </p>

                    <h2 className="content-subhead">Rozliczenie</h2>
                    <p>
                        Do drugiego etapu gry, rozliczenia-pokazu, pierwszy przystpuje przeciwnik rozdajcego. W jego rozliczeniu-pokazie bior udzia karty wyoone przez niego na st, karty ewentualnie pozostae w rku oraz karta startowa (a wic 4 karty).
                    </p>
                    <p>
                        Gracz zdobywa punkty za nastpujce kombinacje, ktre mona uoy z tych czterech kart:
                    </p>
                    <ul>
                        <li>
                            za pitnastk - jak w rozgrywce, 2 punkty za kady ukad dwu lub wicej kart dajcych w sumie 15 oczek;
                        </li>
                        <li>
                            za pary, pary krlewski, podwjne pary krlewskie i sekwensy - jak w rozgrywce, za kady z moliwych rnych ukadw;
                        </li>
                    </ul>
                    <p>
                        Punkty zdobywa si take, czego nie ma w rozgrywce, za kolor: za trzy karty w tym samym kolorze - 3pkt, za cztery karty - 4pkt. Punkty za kolor licz si tylko za kolor w rku; za kolor w kribie otrzymuje si punkty tylko wwczas, gdy wszystkie pi kart (cztery w kribie i startowa) s w tym samym kolorze - 5 punktw.
                    </p>
                    <p>
                        Kada karta moe bra udzia we wszystkich kombinacjach.
                    </p>
                    <p>
                        Oto przykadowe rozliczenie-pokaz nastpujcych czterech kart: <br />
                        4 <span className="heart"></span> , 5 <span className="heart"></span> , 6 <span className="spade"></span> , D <span className="heart"></span> : 4pkt za dwa razy po pitnacie (4,5,6 oraz D,5); par nie ma; trzy punkty za sekwens z trzech kart (4,5,6); trzy punkty za kolor (<span className="heart"></span>); cznie 10pkt.
                    </p>
                    <p>
                        Po rozliczeniu-pokazie przeciwnika rozdajcego nastpuje rozliczenie-pokaz rozdajcego, skadajce si z dwch czci: pokazu rki i pokazu kriba.
                    </p>
                    <p>
                        Rozliczenie-pokaz rki (kart wyoonych na st, ewentualnie pozostaych w rku oraz karty startowej, a wic czterech kart) - jak uprzednio.
                    </p>
                    <p>
                        Rozliczenie-pokaz kriba (kart dotychczas zakrytych, wyoonych na st oraz karty startowej, a wic piciu kart) moe znacznie zwikszy ilo kombinacji punktowych. Rozdajcy zapisuje sobie punkty zdobyte za obydwa te pokazy-rozliczenia.
                    </p>

                    <h2 className="content-subhead">Punkty premiowe</h2>
                    <p>
                        Istniej dodatkowe punkty premiowe, ktre otrzymuje si w trakcie rozgrywki i pokazu. I tak:
                    </p>
                    <ul>
                        <li>
                            w momencie rozpoczcia rozdawania kart przeciwnik rozdajcego otrzymuje 3pkt (dla wyrwnania przewagi rozdajcego, ktremu bd liczyy si punkty za krib);
                        </li>
                        <li>
                            jeli karta startowa jest waletem, rozdajcy natychmiast otrzymuje 2pkt premii;
                        </li>
                        <li>
                            jeli w rku lub w kribie znajduje si walet koloru karty startowej, waciciel tej rki lub kriba otrzymuje dodatkowo 1pkt w pokazie-rozliczeniu;
                        </li>
                    </ul>
                    <p>
                        Gra koczy si, gdy ktry z graczy osiga 61pkt, choby byo to w toku rozgrywki lub rozliczenia-pokazu. Zapis zdobytych punktw trzeba wic prowadzi na bieco.
                    </p>

                    <h2 className="content-subhead">Partia przykadowa</h2>
                    <p>
                        Rozdawa gracz B.<br />Gracz A otrzyma nastpujce karty: W <span className="heart"></span> , 9 <span className="spade"></span> , 7 <span className="diamond"></span> , 5 <span className="spade"></span>, 5 <span className="club"></span> .<br />
                        Gracz B otrzyma: K <span className="spade"></span> , K <span className="club"></span> , W <span className="diamond"></span> , 8 <span className="heart"></span>, 6 <span className="heart"></span> .<br />
                        Gracz A zrzuca do kriba 9 <span className="spade"></span> i 7 <span className="diamond"></span> .<br />
                        Gracz B zrzuca do kriba 8 <span className="heart"></span> i 6 <span className="heart"></span> .<br />
                        Kart startow okazuje si 5 <span className="heart"></span> .<br />
                        Gracz A notuje sobie 3 pkty przed rozpoczciem gry i wykada W  <span className="heart"></span> - 10 oczek.<br />
                        Gracz B wykada W <span className="diamond"></span> - dwadziecia oczek - i notuje sobie dwa punkty za par waletw.<br />
                        Gracz A wykada 5 <span className="spade"></span> - 25 oczek.<br />
                        Gracz B deklaruje "dalej".<br />
                        Gracz A deklaruje "dalej"<br />
                        Rozgrywka koczy si - gracz A ma 3pkt, gracz B ma 2pkt.
                    </p>

                    <h3>Pokaz-rozliczenie gracza A wyglda nastpujco:</h3>
                    <ul>
                        <li>4 x pitnacie - 8pkt (walet z kad z trzech pitek oraz same trzy pitki);</li>
                        <li>para krlewska - 6pkt (trzy pitki);</li>
                        <li>za waleta koloru karty stoowej - 1pkt;</li>
                    </ul>
                    <p>
                        cznie z punktami za rozgrywk gracz A zdoby 18pkt.
                    </p>

                    <h3>Pokaz-rozliczenie gracza B wyglda nastpujco:</h3>
                    <ul>
                        <li>3 x pitnacie - 6 punktw (kada z figur w rku z pitk startow);</li>
                        <li>para - 2 punkty (za krle);</li>
                    </ul>
                    <p>
                        w kribie znajduj si 9, 8, 7, 6 oraz 5 startowa, a wic za krib:
                    </p>
                    <ul>
                        <li>2 x pitnacie - 4 punkty (9,6 oraz 8,7);</li>
                        <li>sekwens 5-ciokartowy - 5 punktw;</li>
                    </ul>
                    <p>
                        cznie z punktami za rozgrywk gracz B zdoby wic 19 punktw.
                    </p>
                </div>

                <div className="content">
                    <h1 className="content-subhead main-header">
                        Cribbage szecio- i siedmiokartowy
                    </h1>
                    <p>
                        Trudniejszy ni tradycyjny wariant gry.<br />
                        Rnice w zasadach gry s nastpujce:
                    </p>
                    <ul>
                        <li>
                            przy cribbage'u 6-ciokartowym (kady otrzymuje do rki 6 kart) gra toczy si do 121 punktw;
                        </li>
                        <li>
                            przy cribbage'u 7-miokartowym (kady otrzymuje do rki 7 kart) gra toczy si do 181 punktw;
                        </li>
                        <li>przeciwnik rozdajcego nie otrzymuje 3 punktw wyrwnania;</li>
                        <li>
                            rozgrywka toczy si do chwili wyoenia wszystkich kart;<br />
                            gdy obaj gracze zadeklarowali ju "dalej", ten, na ktrego przypada kolejno wyoenia karty, rozpoczyna nastpny cykl - znw do 31 punktw (przy cribbage'u 7-miokartowym moliwe s wic trzy cykle w rozgrywce).
                        </li>
                    </ul>
                </div>

                <div className="content">
                    <h1 className="content-subhead main-header">Cribbage licytowany</h1>
                    <p>
                        Wariant cribbage'a szeciokartowego (do 121 punktw).<br />
                        Zasadnicza rnica w grze polega na tym, e krib nie naley automatycznie do rozdajcego, lecz staje si wasnoci tego gracza, ktry przelicytuje kontrpartnera. Licytacj rozpoczyna przeciwnik rozdajcego od jednego punktu za krib; wygrywa ten kto zaoferuje wicej punktw. Punkty zadeklarowane za krib odpisuje si graczowi, ktry wygra licytacj, z zapisu.
                    </p>
                    <p>
                        Wygrywajcy licytacj pierwszy wykada kart w rozgrywce oraz pierwszy przeprowadza rozliczenie-pokaz rki i kriba.
                    </p>
                </div>

                <div className="content">
                    <h1 className="content-subhead main-header">Cribbage dla czterech osb</h1>
                    <p>
                        Wariant gry picio- lub szeciokartowego, w ktrym dwie osoby graj przeciwko dwom, rozdajc karty kolejno. Krib naley zawsze do rozdajcego.
                    </p>
                    <p>
                        Kady z graczy zrzuca do kriba tylko jedn kart. W rozgrywce gracze wykadaj karty kolejno, wedug ruchu wskazwek zegara, poczynajc od gracza po lewej rce rozdajcego. Podobnie w rozliczeniu-pokazie.
                    </p>
                    <p>
                        Gracze-partnerzy siedz naprzeciw siebie. W toku rozgrywki nie wolno im porozumiewa si ze sob. Punkty zdobyte w rozgrywce i za rozliczenia-pokazy zapisuje si na dwa konta par grajcych przeciwko sobie.
                    </p>
                </div>
            </div>
        );
    }
}



