import { basePath } from '../utils';
import React from 'react';
import kwadratImg1 from '../images/kwadrat1.gif';
import kwadratImg2 from '../images/kwadrat2.gif';
import kwadratImg3 from '../images/kwadrat3.gif';
import kwadratImg4 from '../images/kwadrat4.gif';


export default class Kwadrat extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Kwadrat',
            desc: 'gra dla 4 [2] osb',
            url: basePath() + '/kwadrat',
            players: [2, 4],
            cards: 'Maa talia kart (24 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Wspczesna gra karciana opracowana przez Lecha Pijanowskiego.
                    </div>

                    <h2 className="content-subhead">Gracze i karty</h2>
                    <p>
                        Gra dla 4 [2] osb<br />
                        w grze bior udzia wybrane z talii Asy, 2, 3, 4, 5 i 6 we wszystkich kolorach (24 karty). Warto punktowa wedug iloci oczek<br />
                        Nie ma koloru atutowego.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Pierwszego rozdajcego wyznacza si przez losowanie. W nastnych rozdaniach gracze rozdaj karty kolejno. Kady z grajcych otrzymuje po sze kart.
                    </p>
                    <p>
                        Gr rozpoczyna zawsze narczny rozdajcego. Gracze kolejno, w kierunku ruchu wskazwek zegara, wykadaj na st po jednej karcie z rki.
                    </p>
                    <p>
                        Rozpoczynajcy gr moe wyoy dowoln kart. Kady nastpny gracz ma obowizek wyoy jedn kart z rki w ten sposb:
                    </p>
                    <ul>
                        <li>
                            aby dotykaa ona swoim bokiem duszym lub krtszym (lecz nie rogiem) do odpowiedniego boku jednej z kart ju wyoonych na stole (a wic bya z t kart w jednym rzdzie lub w jednej kolumnie kart);
                        </li>
                        <li>
                            aby w adnym rzdzie ani kolumnie kart ilo kart wyoonych nie przekroczya szeciu;
                        </li>
                        <li>
                            aby w adnym rzdzie ani kolumnie nie znalazy si dwie karty tej samej wartoci (np. dwie pitki, dwie dwjki itp.);
                        </li>
                        <li>
                            aby w adnym rzdzie ani kolumnie nie znalazy si dwie karty tego samego koloru (np. dwa kiery, dwa trefle itp.).
                        </li>
                    </ul>

                    <p>
                        Rozgrywka koczy si w chwili, gdy jeden z graczy pozbdzie si wszystkich kart z rki, lub (czciej) w chwili, gdy gracz, na ktrego przypada kolej gry, nie moe adnej ze swoich kart z rki dooy do ukadu na stole.<br />
                        Wwczas oblicza si punkty za rozdanie.
                    </p>
                    <p>
                        Gracz, ktry pozby si wszystkich kart z rki, zdobywa 12 punktw plusowych.<br />
                        Gracz, ktry doprowadzi do przegranej (ten, ktry dooy ostatni kart do ukadu), zdobywa 6 punktw plusowych i nie licz mu si punkty minusowe za karty pozostae w rku.<br />
                        Pozostali gracze otrzymuj ilo punktw minusowych rwn sumie oczek kart pozostaych w rku.
                    </p>
                    <p>
                        Gra toczy si w kolejnych rozgrywkach do chwili, gdy jeden z graczy przekroczy w zapisie umwion z gry ilo punktw minusowych (zazwyczaj 36).
                    </p>

                    <div className="example">
                        <div className="title">Partia przykadowa</div>
                        <div className="content">
                            <p>
                                Graj a, B, C i D; rozdawa D, a wic zaczyna gr A. Karty w rku:<br />
                                u A - 4 <span className="spade"></span>, 6 <span className="heart"></span>, 4 <span className="heart"></span>, 3 <span className="heart"></span>, 4 <span className="diamond"></span> i 3 <span className="diamond"></span>;<br />
                                u B - 6 <span className="spade"></span>, A <span className="spade"></span>, 2 <span className="heart"></span>, A <span className="heart"></span>, 6 <span className="club"></span> i 4 <span className="club"></span>;<br />
                                u C - 5 <span className="spade"></span>, 5 <span className="heart"></span>, 6 <span className="diamond"></span>, 5 <span className="diamond"></span>, 2 <span className="club"></span> i A <span className="club"></span>;<br />
                                u D - 3 <span className="spade"></span>, 2 <span className="spade"></span>, 2 <span className="diamond"></span>, A <span className="diamond"></span>, 5 <span className="club"></span> i 3 <span className="club"></span>;
                            </p>
                            <p>
                                A rozpoczyna gr od wyoenia 4 <span className="heart"></span>, B dokada w rzdzie po lewej 6 <span className="club"></span>. C poziomo w d od czwrki - 6 <span className="diamond"></span>. Po prawej stronie 6 <span className="diamond"></span> D kadzie 5 <span className="club"></span>. Teraz zowu A - pod 5 <span className="club"></span> umieszcza 6 <span className="heart"></span> - powstaj charakterystyczne w grze "schodki". Wszyscy gracze staraj si pozby najwyszych kart.
                            </p>
                            <p>
                                Po prawej stronie 6 <span className="heart"></span> B gra 4 <span className="club"></span>. Pod ni C gra 5 <span className="heart"></span>. D gra 3 <span className="spade"></span> obok 4 <span className="heart"></span> - wyobraony kwadrat zaczyna si wypenia. A gra 4 <span className="diamond"></span> po prawej stronie 5 <span className="heart"></span> - w tej kolumnie s ju wszystkie cztery kolory, nie moe doj wicej.
                            </p>
                            <p>
                                B gra 6 <span className="spade"></span> obok 5 <span className="heart"></span>, C gra 5 <span className="spade"></span> pod 6 <span className="diamond"></span>, D gra 3 <span className="club"></span> pod 5 <span className="spade"></span> - w drugiej kolumnie ju s cztery kolory.<br />
                                A gra 4 <span className="spade"></span> pod 6 <span className="club"></span>, B gra 2 <span className="heart"></span> nad 6 <span className="club"></span>, C gra 5 <span className="diamond"></span> pod 6 <span className="spade"></span>. Ley ju sze rzdw kart, kwadrat pionowo nie bdzie rs, moe tylko rozszerzea si poziomo.<br />
                                D gra 2 <span className="spade"></span> pod 5 <span className="heart"></span>, A gra 3 <span className="diamond"></span> pod 4 <span className="spade"></span>, B gra A <span className="spade"></span> po lewej stronie 2 <span className="heart"></span>. Ju ley i sze kolumn kart, pooenie wyobraonego kwadratu jest wyznaczone i ustalone ostatecznie, mona go jeszcze tylko w niektrych miejscach  wypenia.
                            </p>
                            <p>
                                C, na ktrego przypada kolej gry, ma jeszcze w rku 2 <span className="club"></span> i A <span className="club"></span>, nie moe jednak adnej z tych kart dooy do ukadu, powtrzy by si bowiem albo kolor, albo warto karty w rzdzie lub w kolumnie. Sytuacj kocow (karty na stole przedstawia poniszy rysunek.
                            </p>

                            <div className="center-outer-container">
                                <div className="center-inner-container">
                                    <img className="pure-img" src={kwadratImg1}></img>
                                </div>
                            </div>

                            <p>
                                Premi 6pkt otrzymuje gracz B (ostatni, ktry dooy kart do ukadu na stole) i nie licz si punkty minusowe za A <span className="heart"></span> pozostaego w rku. C, D i A przegrywaj kady po 3pkt, cho pozostay im rne iloci kart w rku.
                            </p>
                            <p>
                                W toku gry w kwadrat powsta mog najrniejsze, zgodne z reguami gry, ukadfy rnych iloci kart. Niektre z moliwych ukadw przedstawia rysunek poniej:
                            </p>

                            <div className="center-outer-container">
                                <div className="center-inner-container">
                                    <img className="pure-img" src={kwadratImg2}></img>
                                </div>
                            </div>

                        </div>
                    </div>

                    <h2 className="content-subhead">Kwadrat dla dwch osb</h2>
                    <p>
                        Wariant gry mniej losowy, bardziej strategiczny.<br />
                        W grze bierze udzia 16 kart (Asy, 2, 3 i 4); kady z graczy otrzymuje po osiem kart.<br />
                        Wolno dokada karty nie do szeciu, lecz do tylko do czterech rzdw i kolumn.<br />
                        Za pozbycie si kart z rki - 8pkt; za dooenie ostatniej karty - 4pkt.<br />
                        Gra toczy si do chwili zdobycia przez ktrego z graczy 16pkt w zapisie, plusowych lub minusowych.
                    </p>

                    <div className="example">
                        <div className="title">Partia przykadowa</div>
                        <div className="content">
                            <p>
                                Karty gracza A, ktry rozpoczyna gr: 2 <span className="spade"></span>, 4 <span className="spade"></span>, 3 <span className="heart"></span>, A <span className="diamond"></span>, A <span className="club"></span>, 2 <span className="club"></span>, 3 <span className="club"></span> i 4 <span className="club"></span>.<br />
                                Karty gracza B: A <span className="spade"></span>, 3 <span className="spade"></span>, A <span className="heart"></span>, 2 <span className="heart"></span>, 4 <span className="heart"></span>, 2 <span className="diamond"></span>, 3 <span className="diamond"></span> i 4 <span className="diamond"></span>.
                            </p>
                            <p>
                                Kolejno wykadane karty obydwu graczy (na zmian):<br />
                                4 <span className="club"></span>, 2 <span className="heart"></span>, 3 <span className="club"></span>, 4 <span className="diamond"></span>, 2 <span className="club"></span>, 3 <span className="spade"></span>, A <span className="diamond"></span>, 2 <span className="diamond"></span>, 4 <span className="spade"></span>, A <span className="spade"></span>, 3 <span className="heart"></span> - przegrywa gracz B (8pkt minusowych). Gracz A otrzymuje 4 punkty plusowe. Sytuacj kocow przedstawia rysunek:
                            </p>

                            <div className="center-outer-container">
                                <div className="center-inner-container">
                                    <img className="pure-img" src={kwadratImg3}></img>
                                </div>
                            </div>

                            <p>
                                Z tymi samymi kartami u obydwu graczy przy innej taktyce gry rozgrywka mogaby wyglda tak:<br />
                                3 <span className="club"></span>, 4 <span className="heart"></span>, 2 <span className="spade"></span>, 2 <span className="diamond"></span>, 3 <span className="heart"></span>, A <span className="spade"></span>, 4 <span className="club"></span>, 3 <span className="spade"></span>, 2 <span className="club"></span>, A <span className="heart"></span>, A <span className="diamond"></span>, 4 <span className="diamond"></span> - i przegrywa A, 5 punktw minusowych, 4 punkty plusowe dla B.<br />
                                Sytuacj kocow przedstawia rysunek:
                            </p>

                            <div className="center-outer-container">
                                <div className="center-inner-container">
                                    <img className="pure-img" src={kwadratImg4}></img>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        );
    }
}



