import { basePath } from '../utils';
import React from 'react';


export default class Planowanie extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Planowanie',
            url: basePath() + '/planowanie',
            players: [2, 3, 4, 5, 6, 7, 8],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>

                    <h2 className="content-subhead">Karty</h2>
                    <p>
                        Talia 52 kart.<br />
                        Starszestwo kart od asa do dwjki.<br />
                        Istnieje zmienny kolor atutowy.<br />
                        Istnieje obowizek dorzucanie do koloru.<br />
                        Nie ma obowizku przebijania starsz kart ani przebijania atutem w przypadku nieposiadania koloru wyjcia.

                    </p>

                    <h2 className="content-subhead">Rozdanie</h2>
                    <p>
                        Pierwszego rozdajcego wybiera si przez losowanie. Nastpnie w kolejnych rozgrywkach gracze rozdaj karty kolejno. Gr rozpoczyna zawsze gracz po lewej stronie rozdajcego.
                    </p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Gra skada si z dwch etapw: zapowiedzi i rozgrywki.
                    </p>
                    <p>
                        W zapowiedzi gracze deklaruj kolejno ilo lew, ktr zobowizuj si wzi w danym rozdaniu. Zobowizanie nie wzicia adnej lewy (zero) jest rwnie zapowiedzi.
                    </p>
                    <p>
                        Gra toczy si w kolejnych rozgrywkach. W pierwszej rozgrywce kady z graczy otrzymuje tylko jedn kart, kolejna karta zostaje odkryta ("wywicona") i wyznacza kolor atutowy w danej rozgrywce. W pierwszej rozgrywce mog wic istnie tylko dwie zapowiedzi: zero lew lub jedna lewa.
                    </p>
                    <p>
                        W drugiej rozgrywce kady z graczy otrzymuje po dwie karty i kolejna karta z talonu jest odkrywana na st i wyznacza kolor atutowy.
                    </p>
                    <p>
                        W trzeciej rozgrywce po trzy karty, itd. w kadej nastpnej rozgrywce gracze otrzymuj po jednej karcie wicej. Cykl rozgrywek dobiega koca, gdy nie wystarcza ju kart dla podziau pomidzy graczy. Pozostae w talonie karty nigdy nie bior udziau w rozgrywce.
                    </p>
                    <p>
                        Ostatnia rozgrywka cyklu rozgrywana jest zawsze jako bezatutowa (nie odkrywa si karty z talonu).
                    </p>

                    <h2 className="content-subhead">Punktacja</h2>
                    <p>
                        Po zakoczeniu kadej rozgrywki oblicza si punkty zdobyte przez graczy.
                    </p>
                    <p>
                        Gracz, ktry nie wykona swojej zapowiedzi (wzi mniej lub wicej lew, ni deklarowa), nie otrzymuje punktw.<br />
                        Gracz, ktry wykona zapowied, otrzymuje ilo punktw rwn iloci zadeklarowanych lew oraz 10 punktw premii. Np. zadeklarowane i wzite 7 lew - 17 punktw, zadeklarowane i wzite 0 lew - 10 punktw.
                    </p>
                    <p>
                        Gra toczy si w kolejnych rozgrywkach do zakoczenia caego cyklu (ilo rozgrywek oczywicie zaley od iloci uczestnikw gry).
                    </p>
                    <p>
                        Ca gr wygrywa ten z graczy, ktry zdoby najwicej punktw w zapisie.
                    </p>

                    <h2 className="content-subhead">Wariant gry</h2>
                    <div className="note">
                        <p>
                            Autorem poniszego opisu jest Bogusaw Wicek z Mielca, ktry z kumplami ze studiw w Krakowie grywa w Planowanie systemem (przy 4 graczach):
                        </p>
                        <ul>
                            <li>wszystkie moliwe ustawienia osb przy stoliku (6 moliwoci);</li>
                            <li>w kadym ustawieniu kady zaczyna (4 rozpoczcia) - po to, aby w ostatnim rozdaniu bezatutowym, kady mg rozpocz;</li>
                            <li>4 rozdania z jedn kart.</li>
                        </ul>

                        <p>
                            Razem 24 mecze po 16 rozda. Jak sam to okreli, byy to "nieoficjalne mistrzostwa wiata" :)))
                        </p>
                    </div>
                    <ul>
                        <li>
                            Suma zapowiedzianych lew nie moe si rwna liczbie rozdanych kart dla jednego gracza. Pozwala to wyeliminowa sytuacj, gdy wszyscy gracze zrealizuj swoj zapowied, co znacznie podnosi emocje podczas gry (zawsze bdzie przynajmniej jeden "przegrany"). Np., jeeli trzem graczom rozdano po 5 kart i zgoszono ju 2+2 lewy, to ostatni gracz nie moe zgosi 1 lewy (moe zgosi jedynie 0, 2, 3, 4 lub 5 lew);
                        </li>
                        <li>
                            rozdanie z jedn kart mona gra tyle razy ilu jest graczy. W ten sposb, kady bdzie w tej niekorzystnej ostatniej pozycji, gdy nie bdzie mia wyboru w zapowiedzi; dwaj pierwsi gracze nie s niczym ograniczeni (obaj mog zadeklarowa 0 lub 1), ostatni za musi zadeklarowa 0, gdy dwch pierwszych zadeklaruje 0+0; musi zadeklarowa 1, gdy dwaj pozostali zadeklarowali 0+1 (1+0); ma natomiast moliwo wyboru tylko w przypadku deklaracji 1+1;
                        </li>
                        <li>
                            za zrealizowanie zapowiedzi gracz otrzymuje 1pkt duy i tyle punktw maych ile zrealizowanych lew. Gr wygrywa ten z graczy, ktry ma najwicej punktw duych. Przy ich rwnej liczbie decyduje liczba punktw maych. Przy takiej punktacji gracz, ktry dwa razy zadeklarowa i zrealizowa 0 lew otrzymuje 2pkt due i 0pkt maych, i tym samym wygrywa z graczem, ktry raz zadeklarowa i zrealizowa 11 lew (w punktacji tradycyjnej sytuacja jest odwrotna: wygrywa ten drugi gracz zdobywajc 21pkt, poniewa pierwszy otrzyma ich tylko 20);
                        </li>
                        <li>
                            W przypadku gry z trzema osobami ostatnia rozgrywka (bezatutowa) odbywa si przy usunitej z gry 2 <span className="club"></span>.
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}



