import { basePath } from '../utils';
import React from 'react';


export default class Tuzatu extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Tuzatu',
            desc: 'gra dla 4 osb',
            url: basePath() + '/tuzatu',
            players: [4],
            cards: 'Standardowa talia kart (52 karty)'
        };
    }

    render() {
        document.title = this.state.name;
        return (
            <div>
                <div className="content">
                    <h1 className="content-subhead main-header">{this.state.name}</h1>
                    <div className="info">
                        Gra wywodzca si z Francji ("tous atouts")
                    </div>

                    <h2 className="content-subhead">Gracze</h2>
                    <p>Gra dla 4 osb (dwie przeciwko dwom).</p>

                    <h2 className="content-subhead">Gra</h2>
                    <p>
                        Do gry uywa si talii 52 kart.<br />
                        Kady z grajcych otrzymuje po 13 kart. Do pierwszej lewy wychodzi zawsze gracz po lewej stronie rozdajcego. Po pierwszym wyjciu partner rozdajcego wykada swoje karty odkryte na stole (jest dziadkiem - jego kartami w rozgrywce dysponuje rozdajcy)
                    </p>
                    <p>
                        W momencie pierwszego wyjcia kolorem atutowym s kiery do chwili, gdy zostanie zgrany ostatni kier. Wwczas, od nastpnej lewy, kolorem atutowym staj si kara do chwili, gdy zostanie zgrane ostatnie karo. Wwczas od nastpnej lewy kolorem atutowym s trefle.
                    </p>

                    <h2 className="content-subhead">Punktacja</h2>
                    <p>
                        Po zakoczeniu rozgrywki zapisuje si wyniki. Dla partnerw, ktrzy zdobyli wicej ni sze lew - po jednym punkcie za kad lew powyej szeciu (tzn. za siedem lew - jeden punkt, za osiem lew - dwa punkty itd.). Dla partnerw, ktrzy w swoich lewach zdobyli asa karowego, treflowego lub pikowego - po jednym punkcie za kadego z nich.<br />
                        Gra toczy si do chwili, gdy jedna ze stron zdobdzie 10 punktw w zapisie - ta strona wygrywa ca gr.
                    </p>
                </div>
            </div>
        );
    }
}



